export default function (Component) {
  Component.__i18n = Component.__i18n || []
  Component.__i18n.push({
    "locale": "",
    "resource": {
      "ru": {
        "modalSearchTitle": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Произошла ошибка"])}
      },
      "en": {
        "modalSearchTitle": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["An error has occurred"])}
      }
    }
  })
}
