export default function (Component) {
  Component.__i18n = Component.__i18n || []
  Component.__i18n.push({
    "locale": "",
    "resource": {
      "ru": {
        "recommend": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Гости рекомендуют"])},
        "seeReviews": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Читать отзывы гостей"])}
      },
      "en": {
        "recommend": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Guest's choice"])},
        "seeReviews": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Read guest reviews"])}
      }
    }
  })
}
