<i18n>
{
  "ru": {
    "cleaningPrice": "плата за уборку",
    "byOneNight": "за сутки",
    "night": "0|за {n} сутки|за {n} суток|за {n} суток",
    "order": "По запросу",
    "prePay": "Без предоплаты",
    "today": "сегодня",
    "hintText": "Стоимость {data} при заезде {checkIn}",
    "totalNight": "Всего",
    "loading": "загрузка...",
    "sale": "скидка"
  },
  "en": {
    "cleaningPrice": "for cleaning",
    "byOneNight": "per day",
    "night": "0|per {n} day|per {n} days|per {n} days",
    "order": "On request",
    "prePay": "Without prepayment",
    "today": "today",
    "hintText": "Cost {data} at check-in",
    "totalNight": "total",
    "loading": "loading...",
    "sale": "sale"
  }
}
</i18n>
<template>
  <div class="price-order">
    <template v-if="hotSalePrice">
      <div class="discount-wrap">
        <span class="discount-percent">%</span>
        <span class="discount-price">
          {{ $t('sale') }}
        </span>
      </div>
    </template>
    <div>
      <!-- цена за сутки -->
      <div class="price" :class="{ discount: hotSalePrice }">
        <span class="icon-app-lightning" v-if="data.booking_now"></span>
        <span v-if="hotSalePrice">{{ formatedCurrensies(averagePrice) }}</span>
        <span v-else>{{ formatedCurrensies(averagePrice) }}</span>
        <span class="price-text">{{ $t("byOneNight") }}</span>
      </div>
      <!-- цена за сутки -->

      <!--  общая цена за все дни -->
      <div v-if="days > 1 && totalPrice !== averagePrice" class="price-total">
        <span class="price-text">{{ $t("totalNight") }}</span>
        {{ formatedCurrensies(totalPrice) }}
      </div>
      <!--  общая цена за все дни -->
    </div>
  </div>
</template>
<script>
import HTTP from "@/api/http-common";
import getPricesAndAvailabilities from "@/api/objects/getPricesAndAvailabilities.js";
import { format } from "@/utils/format";
import { mapState, mapGetters } from "vuex";
import BaseCalcPrice from "@/components/base/BaseCalcPrice";
import { Objects } from "@/api/";

export default {
  name: "PriceOrder",
  components: {
    BaseCalcPrice,
  },
  props: {
    data: {
      type: Object,
      default: false,
    },
    cleaningPrice: {
      type: Number,
      default: 0,
    },
  },
  data() {
    return {
      prices: null,
      display: false,
    };
  },
  methods: {
    getPrice() {
      Objects.getPricesAndAvailabilities({
        guests: Number(this.maxGuests),
        date_begin: `${format(this.checkIn)} 14:00:00`,
        date_end: `${format(this.checkOut)} 12:00:00`,
        objects: Number(this.data.id),
        currency_id: 1,
      })
        .then((response) => {
          const { data: data } = response;
          this.prices = data.data[this.data.id].data;
        })
        .catch((error) => {
          console.log(
            "Ajax error: Objects.getPricesAndAvailabilities, ",
            error
          );
        });
    },
    formatedCurrensies(price) {
      return new Intl.NumberFormat(this.$i18n.locale, {
        style: "currency",
        currency: "RUB",
        minimumFractionDigits: 0,
      }).format(price);
    },
  },
  computed: {
    ...mapState({
      screenSize: (state) => state.user.viewPort,
    }),
    ...mapGetters("search", ["occupied", "days", "maxGuests"]),
    ...mapState("search", ["checkIn", "checkOut"]),
    totalPrice() {
      return Math.round(this.data.price.full);
    },
    averagePrice() {
      return Math.round(this.data.price.per_night);
    },
    hintCheckIn() {
      return (
        (this.checkIn &&
          this.checkIn.toLocaleString(this.$i18n.locale, {
            day: "numeric",
            month: "long",
          })) ||
        this.$t("today")
      );
    },
    detailPrice() {
      return this.prices ? this.prices.detail : null;
    },
    hotSalePrice() {
      return this.data.hot_sale &&
        Number(this.data.hot_sale.price_per_night) > 0
        ? Number(this.data.hot_sale.price_per_night)
        : null;
    },
  },
};
</script>
<style lang="scss" scoped>
::v-deep .booking__calc {
  .calc__item {
    &:first-child {
      padding-top: 0;
    }
  }
  &_hot {
    padding-top: 0;
  }
}
.price-order {
  display: inline-block;
  vertical-align: middle;
  white-space: nowrap;
  text-overflow: clip;
  color: #212529;
  &-vertical {
    display: flex;
    flex-direction: column;
    text-align: center;
    line-height: 1;

    padding-top: 20%;
    justify-content: center;
    height: 100%;
    .days {
      margin-bottom: auto;
    }
  }
  &.booked {
    color: #909294;
  }
  .icon-fast-booking {
    position: relative;
    display: inline-block;
    margin-right: 8px;
    width: 10px;
    height: 15px;
    background-position: center;
    background-image: url(data:image/svg+xml;base64,);
  }
  .price {
    position: relative;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 20px;
    color: #000;

    @media only screen and (min-width: 1490px) {
      text-align: right;
    }
    .icon-app-lightning {
      display: inline-block;
      width: 11px;
      height: 16px;
      margin-right: 4px;
      position: relative;
      top: 2px;
      &::before {
        content: '';
        display: block;
        height: 100%;
        width: 100%;
        background-image: url(~assets/img/icon/flash.svg);
        fill: #F51449;
      }
    }
    &.discount {
      color: #e10600;
      font-style: normal;
      font-weight: bold;
      font-size: 18px;
      line-height: 23px;
    }
    & > .price-text {
      padding-left: 6px;
      color: #000;
    }
  }
  .price-text {
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 18px;
    color: #717171;
  }
  .price-total {
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 18px;
    color: #717171;
    padding-top: 3px;
    @media only screen and (min-width: 1490px) {
      text-align: right;
    }
  }
  .price-icon {
    font-size: 16px;
  }
  .price-popover {
    display: inline;
    vertical-align: middle;
  }
  .days {
    color: #000;
    font-size: 13px;
    padding: 0 4px;
  }
  .discount-wrap {
    display: flex;
    align-items: center;
    @media only screen and (min-width: 1280px) {
      justify-content: flex-start;
    }
    @media only screen and (min-width: 1490px) {
      justify-content: flex-end;
    }
  }
  .discount {
    margin-left: auto;

    &-percent {
      color: white;
      background: #e10600;
      position: relative;
      // top: -2px;
      padding: 0px 4px;
      font-size: 12px;
      font-weight: bold;
      margin-right: 10px;
      height: 18px;
      display: inline-flex;
      align-items: center;
      border-radius: 2px;
      &::before {
        content: "";
        position: absolute;
        left: 100%;
        top: 0;
        width: 0;
        height: 0;
        margin-left: -1px;
        border-style: solid;
        border-width: 9px 0 9px 9px;
        border-color: transparent transparent transparent #e10600;
        border-radius: 2px;
      }
    }
    &-price {
      color: #717171;
      font-size: 18px;
      line-height: 23px;
      padding-left: 6px;
    }
  }
  .prepay {
    span {
      display: inline-block;
      font-size: 10px;
      text-transform: uppercase;
      color: white;
      background: #417505;
      border-radius: 2px;
      padding: 1px 3px;
    }
  }

  .cleaningPrice {
    font-size: 13px;
    margin-top: 4px;
    span {
      color: #a6a6a6;
    }
  }
}

.popover-text {
  display: flex;
  flex-direction: column;
  color: #444444;

  p {
    margin: 0;
    padding: 0;
  }

  &__price {
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
    &:last-of-type {
      margin-bottom: 0;
    }
  }
}
</style>
