<i18n>
  {
  "en": {
  "promptQuickRegistration": "Use the filter to find accommodation with quick booking",
  "clearFilters": "Clear all filters",
  "noVariants": "No variants",
  "noVariantsDescription": "Try expanding your search radius or changing filters.",
  "open": "Open the map",
  "close": "Show a list of objects",
  "paginationText": "При выбранных фильтрах ваше объявление находится на 42 месте в поиске на 2й странице.",
  "paginationSubtext": "Гость бронирует жилье на 1 странице поиска в 90% случаев. Поднимите позиции своего объявления.",
  "viewObjectsOnMap": "Show map",
  "hideObjectsOnMap": "Hide map",
  "from": "from ",
  "promptFavorites": {
  "title": "Not all featured objects shown",
  "text": "The list contains only those that are available in this city.",
  "text_map": "And only those that fall on the selected section of the map and are free on your dates."
  },
  "yourFilters": "Your filters",
  "clearAll": "Reset filters",
  "getTotalObjects": "from {n}"
  },
  "ru": {
  "promptQuickRegistration": "Нажмите сюда, чтобы увидеть варианты с быстрым бронированием",
  "clearFilters": "Сбросить все фильтры",
  "noVariants": "Нет вариантов",
  "noVariantsDescription": "Попробуйте расширить радиус поиска или изменить фильтры.",
  "open": "Раскрыть карту",
  "close": "Показать список объектов",
  "paginationText": "При выбранных фильтрах ваше объявление находится на 42 месте в поиске на 2й странице.",
  "paginationSubtext": "Гость бронирует жилье на 1 странице поиска в 90% случаев. Поднимите позиции своего объявления.",
  "viewObjectsOnMap": "Раскрыть карту",
  "hideObjectsOnMap": "Скрыть карту",
  "from": "от ",
  "promptFavorites": {
  "title": "Показаны не все избранные объекты",
  "text": "В списке только те, которые доступны в этом городе.",
  "text_map": "А только те, которые попадают на выбранный участок карты и свободны на ваши даты."
  },
  "yourFilters": "Ваши фильтры",
  "clearAll": "Сбросить фильтры",
  "getTotalObjects": "из {n}"
  }
  }
</i18n>

<template>
  <div class="sutochno-widget-result" :class="viewWidget">
    <transition-group name="fade" tag="div">
      <template v-if="viewResult === 'list'">
        <div class="sutochno-widget-result--count">
          <ResultTotalFound
            v-if="!isPosition"
            :is-loading="!['complete', 'loaded'].includes(status[pageNum])"
            :is-moved-map="isMovedMap"
          />
        </div>
        <div class="sutochno-widget-result--objects">
          <template v-if="results">
            <template v-if="viewWidget === 'adaptive'">
              <ObjectListMobile
                ref="objectList"
                :results="results"
                :adblock="adblock"
                :scroll-stop="scrollStop"
                :is-desktop="true"
                @hoverObject="onHoverObject"
                @scrollStop="scrollStop = true"
                @armorClick="armorClick"
              />
            </template>
            <template v-else>
              <ObjectList
                ref="objectList"
                :results="results"
                :adblock="adblock"
                :scroll-stop="scrollStop"
                :hover-placemark="+hoverPlacemark"
                @scrollStop="scrollStop = true"
              />
            </template>
          </template>
        </div>
        <div class="sutochno-widget-result--btn-more">
          <button :class="{'loading': !['complete', 'loaded'].includes(status[pageNum])}" v-if="isNextPage" @click="nextList">Показать еще</button>
        </div>
      </template>
      <div v-else class="sutochno-widget-result--map">
        <template v-if="viewWidget === 'adaptive'">
          <MapMobile
            v-model="isMoved"
            :results="results[pageNum] || []"
            :status="status[pageNum]"
            :zoom="mapZoom"
            :is-section="isSection"
            :is-loading="isLoading"
            @onLoadMap="onLoadMap"
            @onIsSection="(val) => (isSection = val)"
            @mapCenter="mapCenterVal"
            @mapZoom="mapZoomVal"
          />
        </template>
        <template v-else>
          <div
            ref="map"
            class="map"
            :class="{ 'hide-mark-layer': totalFound < 50 }"
            style="position: relative"
          >
            <div
                v-if="!['complete', 'loaded'].includes(status[pageNum]) && switchMap"
                class="loader-wrapper"
            >
              <Loader />
            </div>
            <YandexMap
              :coords="mapCenter"
              :zoom="mapZoom"
              :custom-events="customEventsMap"
              :controls="[]"
              :settings="{
                apiKey,
              }"
              :options="{
                minZoom: 5,
              }"
              @ready="onInitMap"
              @abort="onAbortMap"
              @update:customEvents="updateCustomEvents"
              @update:zoom="updateZoom"
              @boundschange="boundschange"
            >

              <div
                class="map__full"
                :class="{ 'map__full--active': switchMapGet }"
                @click="switchMapGet = !switchMapGet"
              />

              <CardMap
                v-if="activeMapCard"
                :is-map="true"
                :object-data="activeMapCard"
                :style="{
                  opacity: `${coordinate.isVisible ? 1 : 0}`,
                  top: `${coordinate.top}px`,
                  left: `${coordinate.left}px`,
                }"
                :is-hover="hoverCard"
                @onRender="onRender"
              />
              <Placemark
                :placemarks="placemarks"
                @click="onClick"
                @mouseenter="onEnter"
                @mouseleave="onLeave"
              />
              <Placemark
                  v-if="place.lat && place.lng"
                  key="imageFlag"
                  :placemarks="{
              imageFlag: {
                coords: [place.lat, place.lng],
                typeMark: 'image',
                options: {
                  iconImageHref: require('assets/img/flag.png'),
                  iconImageSize: [28, 30],
                  iconImageOffset: [0, -30],
                  zIndex: 700,
                },
              },
            }"
              />
            </YandexMap>
          </div>
        </template>
      </div>
    </transition-group>
  </div>
</template>

<script>
// TODO: на vue 2 была проверка v-show="totalFound > 0" на template и она была забагована (не срабатывала проверить актуальность и нужду в этой проверки)
import _ from "lodash";
import { HTTP, Locations, Objects, Search } from "@/api/index.js";
import { mapState, mapGetters, mapMutations, mapActions } from "vuex";
import { CancelToken } from "axios";
import CardMap from "@/components/Search/Map/CardMap";
import ObjectList from "@/components/Search/ObjectList";
import yandexads from "@sutochno/yandexads";
import { YandexMap, Placemark } from "@sutochno/YandexMap";
import Supercluster from "supercluster";
import format from "@/utils/format";
import Loader from "@/components/base/Loader.vue";
import ResultTotalFound from "@/components/Search/Result/ResultTotalFound";
import ObjectListMobile from "@/components/mobile/NewSearch/ObjectList";
import MapMobile from "@/components/mobile/NewSearch/Map";

export default {
  name: "Resultsearch",
  components: {
    YandexMap,
    Placemark,
    CardMap,
    yandexads,
    Loader,
    ObjectList,
    ResultTotalFound,
    ObjectListMobile,
    MapMobile,
  },
  data() {
    return {
      showBanner: true,
      $markLayer: null,
      observer: null, // Отслеживание body на oveflow hidden
      adblock: 0, // Рекламная позиция 0 = выкл
      results: {},
      mapZoom: 13,
      variableMapZoom: 13,
      mapCenter: [55.745161166568124, 37.62050512070092],
      showLoader: false,
      max_results: process.env.NODE_ENV === "production" ? 999999999 : 500, // МАКСИУМ ОБЪЕКТОВ НА СТРАНИЦЕ ПОИСКА
      isSearchGeocoderMap:
        localStorage &&
        JSON.parse(localStorage.getItem("adminInfo") || "{}")
          .isSearchGeocoderMap,
      apiKey:
        process.env.NODE_ENV === "development"
          ? "ed03bb3a-3f94-48b0-a552-7a2b6d165870"
          : this.$route.name === "Detail"
          ? "0614d94c-41a4-486b-8813-86ee84ce2b0c"
          : "b4456580-40d3-4951-a3fe-e14527fbfa0e",
      pageNum: 1,
      perPage: 15,
      status: {
        1: "loading",
      },
      cancelSource: null,
      cancelSourceGetTotal: null,
      hoverPlacemark: null,
      coordinate: {
        top: 0,
        left: 0,
      },
      recentlyResults: [], // Выборка выдачи для карты
      historyMapActivity: {},
      lockUpdate: false, // Блокирвока между анимациями

      isMoved: false, // Сдвинул карту?
      isSection: false,
      isNoBlockUpdate: true, // Блокировка обновления пользователем
      hoverCard: false,
      unwatch: [],
      shadowDatepicker: false,
      shadowGuests: false,
      $objectManager: null,
      beaches: [],
      screenSize: null,
      scrollStop: false,
      searchObjectsPending: false,
      searchObjectsAnswer: false,
      reloadPage: false,
      searchApiErr: 0,
      timeoutPending: null,
      timeoutRefresh: null,
      viewFilters: false,
      barSticky: true,
      isOpenHintMap: true,
      openMapLs: null,
      hintMapPositionLeft: false,
      loadingObjects: null,
      cacheLastObj: {},
      shadowPosition: false,
      oldValuesWatch: null,
      callWidget: null,
      firstLoadPosition: false, // проставляется true после первой загрузки позиций
      positionModal: false, // модалка о старом сервисе, если планшетный вид
      refreshSearchModal: false,
      startModal: false,
      favoritesPoints: [],
      isBookingNowTooltip: false,
      isWlModal: false,
      isLoadMap: false,
      myGeoObject: null,
      favoriteListId: [],
      totalObjects: null,
      $myMap: null,
      searchType: "search", // defauld: "search"; или "map". откуда происходит поиск объектов(поле вввода или карта)
      isMovedMap: false,
      customEventsMap: ["actionbegin", "actionend", "mousedown"],
    };
  },
  watch: {
    term(val) {
      this.isMovedMap = false;
      this.isLoadMap = false;
    },
    isMoved(val) {
      if (this.isLoadMap && val) {
        this.isMovedMap = true;
      }
    },
    shadowPosition(val) {
      val
        ? (document.body.style.overflow = "hidden")
        : (document.body.style.overflow = null);
    },
    viewFilters(val, oVal) {
      if (val) document.body.style.overflow = "hidden";
      else {
        document.body.style.overflow = null;
        this.isBookingNowTooltip = false;
      }
    },
    variableMapZoom(val, oVal) {
      if (val != oVal) {
        if (val >= 14) {
          this.beachesIconSize([30, 30]);
        } else if (this.objectManager) {
          this.beachesIconSize([0, 0]);
        }
      }
    },
    switchMap(val, oldVal) {
      if (val != oldVal) {
        if (val) {
          this.perPage = 50;
        } else {
          this.perPage = 30;
        }
        this.loadPage();
        this.$nextTick(() => {
          if (this.myMap) this.myMap.container.fitToViewport(); //меняет размер карты
        });
      }
      if (val) {
        this.status[this.pageNum] = "loaded";
        this.addHashMap();
      } else {
        this.removeHashMap();
      }
    },
    results: {
      deep: true,
      handler(val) {
        if (this.pageNum && this.results && this.results[this.pageNum]) {
          this.recentlyResults = this.results[this.pageNum];
        }
      },
    },
    historyMapActivity(value) {
      localStorage.setItem("historyMapActivity", JSON.stringify(value));
    },
    selectedMapPointId: function (value, oldValue) {
      // Помечаем метку как посещеную
      if (oldValue) {
        this.historyMapActivity[oldValue] = "visited";
      }
      if (value) {
        this.historyMapActivity[value] = "active";
      }
    },
    bound: {
      // при изменении переписывает свойсива zoom & mapCenter
      deep: true,
      handler: function (bound) {
        if (!this.myMap) {
          return;
        }
        const { center, zoom } = ymaps.util.bounds.getCenterAndZoom(
          [
            [bound.SW.lat, bound.SW.lng],
            [bound.NE.lat, bound.NE.lng],
          ],
          this.myMap.container.getSize(),
          ymaps.projection.wgs84Mercator,
          {
            inscribe: false,
          }
        );

        if (zoom !== this.mapZoom) {
          this.mapZoom = zoom;
        }
        this.mapCenter = center;

        if (this.isSearchProcessActive) {
          this.searchType = "search";
        } else {
          this.searchType = "map";
        }
      },
    },
    apiLayer(API) {
      if (this.markLayer) {
        this.markLayer.setTileUrlTemplate(API);
        this.markLayer.update();
      }
    },
    "getQuery.query"(val, oVal) {
      if (val !== oVal) {
        localStorage.setItem("query", JSON.stringify(val));
      }
    },
    location() {
      this.getTotalFound();
    },
    // TODO: очень затрано для производительности
    // создано потому что если статус передается в пропс 1 child
    // и 1 child еще ниже в пропс 2 child
    // то у 2 child будет обновление слишком поздно в следствии чего много ошибок
    // оптимизировать
    status: {
      deep: true,
      handler(val) {
        this.setStatusActivePage(val[this.pageNum]);
      },
    },
  },
  computed: {
    ...mapState("search", [
      "viewWidget",
      "filters",
      "map",
      "sortings",
      "cheap_and_fast",
      "totalFound",
      "location",
      "selectedMapPointId",
      "place",
      "bound",
      "cardVisible",
      "switchMap",
      "isPosition",
      "isDefaultLocationRender",
      "filtersView",
      "dataKrym",
      "checkIn",
      "checkOut",
      "objectsBronevik",
      "pricesBronevik",
      "isSupportFilters",
      "selectedFilters",
      "isSearchProcessActive",
      "guests",
      "term",
      "analiticParamForSearchWidList",
      "viewResult",
    ]),
    ...mapGetters("search", [
      "occupied",
      "getQuery",
      "filtersSend",
      "priceAllPeriod",
    ]),
    ...mapGetters("search", { maxGuests: "maxGuests" }),
    idObjects() {
      let resultsObjects = this.results[1];
      return resultsObjects ? resultsObjects.map((item) => item.id) : [];
    },
    searchViewPort() {
      return "desktop";
      // if (768 <= this.screenWidth && this.screenWidth <= 1200) return "tablet";
      // else if (1201 <= this.screenWidth && this.screenWidth <= 1535)
      //   return "laptop";
      // else return "desktop";
    },
    titleRoute() {
      return this.$i18n.t("meta.search.title", { city: this.location.title_w });
    },
    objectFilters() {
      // Применение параметров поиска
      let filters = {};
      if (this.filtersSend) {
        for (const [name, value] of Object.entries(this.filtersSend)) {
          filters[name] = Array.isArray(value) ? value.join(",") : value;
        }
      }

      return {
        ...filters,
        sortings: this.sortings,
        cheap_and_fast: this.cheap_and_fast,
        guests_childrens: this.guests.childrens.join(","),
        max_guests: this.maxGuests,
        occupied: this.occupied,
        booking_now: this.filters.booking_now,
      };
    },
    apiLayer() {
      const getsQuery = Object.entries(this.objectFilters).reduce(
        (acc, [key, value]) => {
          if (value !== null && value !== undefined) {
            acc += `${key}=${value}&`;
          }
          return acc;
        },
        ""
      );
      return `${HTTP.defaults.baseURL}/json/search/searchObjectsByTiles?${getsQuery}token=Hy6U3z61fflbgT2yJ/VdlQ2719&x=%x&y=%y&zoom=%z`;
    },
    switchMapGet: {
      get() {
        return this.switchMap;
      },
      set(val) {
        this.updateSwitchMap(val);
      },
    },
    placemarks() {
      const points = this.recentlyResults.filter(
        ({ id }) => this.mapZoom >= 1 || this.hoverPlacemark == id
      ); //this.results[this.pageNum];

      // SUPECLUSTER
      const geoPoints = points.map((p) => ({
        type: "Feature",
        id: p.id,
        geometry: {
          type: "Point",
          coordinates: [p.lng, p.lat],
        },
      }));
      const index = new Supercluster({
        // Примерно подабрал чтобы вблизи агресивнее брались метки в кластер
        // Есть кейсы где можно сказать метки на разные поъезды и в каждом по много объектов
        radius: this.mapZoom > 15 ? (10 * 1) / (1.5 ^ this.mapZoom) + 10 : 0, // когда зум маленький точки слишком далеко разбрасываются
        minZoom: 8,
        maxZoom: 19,
      });
      index.load(geoPoints);
      // ЭТО ПРАВИЛЬНО, НО БУДЕТ БАГА ТОГДА ТК BOUNDS поменяться быстрее чем с апи придет
      // const cluster = index.getClusters([this.bound.SW.lng, this.bound.SW.lat, this.bound.NE.lng, this.bound.NE.lat], this.mapZoom);
      // БЕРЕМ ВСЮ КАРТУ ИБО НЕХРЕН
      const cluster = index.getClusters([-180, -85, 180, 85], this.mapZoom);

      //SMART CONST
      const offsetZoom = {
        19: 0.00009,
        18: 0.0002,
        17: 0.00025,
        16: 0.0003,
        15: 0.0008,
        14: 0.002,
        13: 0.0035,
        12: 0.005,
        11: 0.006,
        10: 0.008,
        9: 0.00975,
        8: 0.0125,
        7: 0.125,
        // Дальше смысла нет
      };

      const meanPoints = cluster.map((p) => {
        if (p.properties && p.properties.cluster) {
          const [cLng, cLat] = p.geometry.coordinates;
          const cPoints = index.getLeaves(p.properties.cluster_id, Infinity); // Вынимаем из кластера все точки
          const nums = cPoints.length;
          const r = offsetZoom[this.mapZoom] || 0.2; // Радиус измеряеться в градусах

          // Перестановка точек по окружности
          cPoints.forEach((cPoint, index) => {
            const path = ((2 * Math.PI) / nums) * index;
            const newLng = cLng + r * Math.cos(path);
            const newLat =
              cLat + Math.sin((cLat * Math.PI) / 180) * r * Math.sin(path);

            cPoint.geometry.coordinates = [newLng, newLat];
            // cPoint.angle = `${-((path/(2*Math.PI))*360  - 90)}deg`
          });
          return cPoints;
        }
        return p;
      });
      const fixPoints = _.flatten(meanPoints).reduce((acc, p) => {
        acc[p.id] = {
          lat: p.geometry.coordinates[1],
          lng: p.geometry.coordinates[0],
        }; //, angle: p.angle
        return acc;
      }, {});

      return points.reduce((acc, value) => {
        const { lat, lng } = fixPoints[value.id] || value; // FALLBACK так на всякий случай
        // const angle = fixPoints[value.id] && fixPoints[value.id].angle || "0deg"
        // style="transform: translate(-50%, -100%) rotate(${angle}); transform-origin: 50% 30px;"

        const classHover = this.hoverPlacemark == value.id ? "hover" : "";
        const classHistory = this.historyMapActivity[value.id] || "";
        const isFavorite = this.favoriteListId.find((item) => item == value.id)
          ? "favorite"
          : "";
        const classPosition =
          this.isPosition
            ? "buble-position"
            : "";
        const isActive =
          this.historyMapActivity[value.id] === "active" || !!classPosition;

        // цена объекта из бронивика
        let idBronevik = this.objectsBronevik.find((item) => item == value.id);
        let priceBronevik = 0;
        let textFromPrice = "";
        if (idBronevik) {
          priceBronevik =
            this.pricesBronevik &&
            this.pricesBronevik[idBronevik] &&
            this.pricesBronevik[idBronevik]["price"]
              ? this.pricesBronevik[idBronevik]["price"]
              : 0;
          if (priceBronevik) {
            textFromPrice = this.$t("from");
          }
        }
        let countDay = 1;
        if (this.checkIn && this.checkOut) {
          let checkIn = new Date(this.checkIn).getTime();
          let checkOut = new Date(this.checkOut).getTime();
          countDay = (checkOut - checkIn) / 86400000;
        }
        priceBronevik = priceBronevik / countDay;
        let priceObject = priceBronevik ? priceBronevik : value.price.per_night;

        acc[value.id] = {
          coords: [lat, lng],
          typeMark: "html",
          options: {
            // Если ховер то выше
            zIndex:
              this.hoverPlacemark == value.id ? 668 : isActive ? 667 : 666,
            iconLayout: `<div id="${
              value.id
            }" class="map-placemark ${classHover} ${classHistory} ${isFavorite} ${classPosition}">
          ${textFromPrice}${new Intl.NumberFormat(this.$i18n.locale, {
              style: "currency",
              currency: "RUB",
              minimumFractionDigits: 0,
            }).format(priceObject)}
          </div>`,
            iconShape: {
              type: "Rectangle",
              // Прямоугольник описывается в виде двух точек - верхней левой и нижней правой.
              coordinates: [
                [-35, -25],
                [35, 0],
              ],
            },
          },
        };
        return acc;
      }, {});
    },
    activeMapCard() {
      if (
        this.selectedMapPointId &&
        this.results[this.pageNum] &&
        this.cardVisible
      ) {
        return this.results[this.pageNum].find(
          (v) => v.id === Number(this.selectedMapPointId)
        );
      } else {
        return null;
      }
    },
    isLoading() {
      return (
        (this.status[this.pageNum] === "loading" ||
          this.status[this.pageNum] === "fail") &&
        this.isWlModal === false
      );
    },
    totalObjectsFormat() {
      return this.totalObjects?.toLocaleString("ru-RU");
    },
    totalFoundFormat() {
      return this.totalFound.toLocaleString("ru-RU");
    },
    isNextPage() {
      return Math.ceil(this.totalFound / this.perPage) > this.pageNum
    }
  },
  created() {
    this.mapCenter = [
      (this.bound.SW.lat + this.bound.NE.lat) / 2,
      (this.bound.SW.lng + this.bound.NE.lng) / 2,
    ];
  },
  async mounted() {
    this.initSearch(false, this.searchViewPort === "laptop");
    this.viewFilters = window.location.hash === "#filter_on" ? true : false;

    this.getTotalFound();
  },
  methods: {
    ...mapActions("search", [
      "getTermSuggestionsWithBoundingBox",
      "setLocation",
      "getPricesBronevik",
      "resetDatesAndGuests",
    ]),
    ...mapMutations("search", [
      "updateSwitchMap",
      "positionMutate",
      "setQuery",
      "setBound",
      "setTerm",
      "setObjectsBronevik",
      "setTotalFoundAll",
      "setSearchProcessActive",
      "setTermTitle",
      "setStatusActivePage",
    ]),
    onLoadMap (val) {
      this.isLoadMap = val;
    },
    mapCenterVal(val) {
      this.mapCenter = val;
    },
    mapZoomVal(val) {
      this.mapZoom = val;
    },
    bannerEventListener(event) {
      if (event === "close") {
        this.showBanner = false;
      }
    },
    findPoint(coords) {
      // ставит метку на центр карты
      if (coords) {
        if (this.myGeoObject) {
          this.myMap.geoObjects.remove(this.myGeoObject);
        }
        this.myGeoObject = new ymaps.GeoObject({
          geometry: {
            type: "Point",
            coordinates: coords,
          },
        });
        this.myMap.geoObjects.add(this.myGeoObject);
      }
    },
    async initSearch(reset = false, resetObjects = false) {
      this.openMapLs = !!JSON.parse(
        localStorage.getItem("hinterMap") || "false"
      );
      if (this.getQuery?.query)
        localStorage.setItem(
          "query",
          JSON.stringify(this.getQuery.query || {})
        );
      if (this.getQuery?.query)
        localStorage.setItem(
          "query",
          JSON.stringify(this.getQuery.query || {})
        );
      if (!this.isPosition) {
        const query = this.getQuery?.query;

        if (query.id == 1 && query.term != "") {
          let variantsQuery = await this.getTermSuggestionsWithBoundingBox(
            query.term
          );
          variantsQuery = variantsQuery.slice(0, 1);

          if (variantsQuery.length > 0) {
            let location = {};

            if (query.term) {
              location.term = variantsQuery[0].location
                ? variantsQuery[0].title
                : `${variantsQuery[0].title_ext} ${variantsQuery[0].title}`;
              this.setTermTitle;
            }
            if (variantsQuery[0]?.location?.id) {
              location.id = variantsQuery[0].location.id;
            }
            if (variantsQuery[0]?.location?.type) {
              location.type = variantsQuery[0].location.type;
            }
            if (variantsQuery[0]?.bbox) {
              if (query["NE.lat"] && query["SW.lat"] && query.id != 1) {
                location.bbox = { NE: {}, SW: {} };
                location.bbox.NE.lat = query["NE.lat"];
                location.bbox.NE.lng = query["NE.lng"];
                location.bbox.SW.lat = query["SW.lat"];
                location.bbox.SW.lng = query["SW.lng"];
              } else {
                location.bbox = variantsQuery[0].bbox;
              }
            }

            this.$store.dispatch("search/setLocation", {
              ...location,
              customTitle: null,
              title_ext: location.term
            });
          }
        }
        this.simpleSearchStart(resetObjects);
      }
      this.historyMapActivity = Object.keys(
        JSON.parse(localStorage.getItem("historyMapActivity") || "{}")
      ).reduce((acc, key) => ({ ...acc, [key]: "visited" }), {});

      // Отслеживаем body на предмет убирание скрола
      // Select the node that will be observed for mutations
      const targetNode = document.body;
      //document.body.style.overflow
      // Options for the observer (which mutations to observe)
      const config = { attributes: true, childList: false, subtree: false };

      // Callback function to execute when mutations are observed
      const callback = function (mutationsList, observer) {
        // Use traditional 'for loops' for IE 11
        for (let mutation of mutationsList) {
          if (mutation.type === "attributes") {
            if (this.myMap) this.myMap.container.fitToViewport();
          }
        }
      };

      // Create an observer instance linked to the callback function
      this.observer = new MutationObserver(callback.bind(this));

      // Start observing the target node for configured mutations
      this.observer.observe(targetNode, config);
    },
    simpleSearchStart(resetObjects) {
      const funcCreateSearch = () => {
        this.isMoved = false;
        this.results = {};
        this.status = {
          1: "loading",
        };
        this.pageNum = 1;
        this.getObjects();
        // }

        if (this.$refs.NewBar) {
          this.$refs.NewBar.showResults();
        }
      };

      if (resetObjects) {
        funcCreateSearch();
      }

      this.unwatch.push(
        this.$watch(
          (vm) => [
            vm.filters,
            vm.occupied,
            vm.maxGuests,
            vm.sortings,
            vm.cheap_and_fast,
            vm.location,
            vm.currency,
            vm.bound,
          ],
          _.debounce(funcCreateSearch, 50),
          {
            immediate: true,
          }
        )
      );
    },
    loadingSearch() {
      this.status[this.pageNum] = "loading";
    },
    loadedSearch(e) {
      // сделал чтобы объект был реактивным this.$set не работает норм в этом случае с объектом
      // если можешь лучше, то сделай плиз
      let tmpStatus = JSON.parse(
        JSON.stringify({ ...this.status, [this.pageNum]: "loaded" })
      );
      this.status = tmpStatus;
    },
    async refreshSearch() {
      this.loadingSearch();
      this.loadedSearch();
    },
    viewAd() {
      this.loaderPage();
      this.$store.commit("search/setAdults");
      this.$store.commit("search/setChildrens");
      this.$store.commit("search/setFilters");
      this.$nextTick(() => {
        // После изменения BBOX пойдет пойск
        const bound = this.myMap.getBounds();
        this.$store.commit("search/setBound", {
          NE: {
            lat: bound[1][0],
            lng: bound[1][1],
          },
          SW: {
            lat: bound[0][0],
            lng: bound[0][1],
          },
        });
      });
    },
    loaderPage() {
      this.isMoved = false;
      this.results = {};
      this.status[this.pageNum] = "loading";
    },
    clickMap() {
      this.hintMap();
    },
    hintMap() {
      // this.$localStorage.set("query", JSON.stringify(query.query))
      localStorage.setItem("hinterMap", "true");
      this.openMapLs = true;
      // this.isOpenHintMap = !this.isOpenHintMap
    },
    initBeaches() {
      // карта отображение пляжа на карте
      Locations.getSightsByType("beaches").then((res) => {
        this.beaches = res.data;
        if (this.beaches) {
          this.objectManager = new ymaps.ObjectManager({
            clusterize: false,
          });

          this.objectManager.objects.options.set({
            iconLayout: "default#image",
            iconImageHref: require("assets/img/point_map.svg"),
            iconImageSize: [30, 30],
            iconImageOffset: [-15, -30],
            cursor: "arrow",
          });
          this.objectManager.add(this.beaches);
          this.myMap.geoObjects.add(this.objectManager);
        }
      });
    },
    onForceUpdateArea() {
      // Сброс состояния выдачи для плейсхолдеров
      this.results = {};
      this.status = {
        1: "loading",
      };
      this.pageNum = 1;

      this.isMoved = false;
      this.onActionEndDebounced();
    },
    onClearFilters() {
      // Сброс фильтров
      if (this.$refs.filters) {
        this.$refs.filters.clearFilters();
      } else {
        this.$refs.NewBar.clearFilters();
      }

      // Сброс состояния выдачи для плейсхолдеров
      this.results = {};
      this.status = {
        1: "loading",
      };
      this.pageNum = 1;
      // Востанавливаем BBOX локации
      this.$store.commit("search/setBound", this.location.bbox);
      sessionStorage.setItem("isClickBookingNow", true);
    },
    onActionBegin() {
      if (this.selectedMapPointId && !this.lockUpdate) {
        this.$store.commit("search/setSelectedMapPointId", null);
      }
    },
    onMouseDown(event) {
      if (this.selectedMapPointId && !this.lockUpdate) {
        this.$store.commit("search/setSelectedMapPointId", null);
      }
    },
    boundschange() {
      if (this.isLoadMap) {
        this.isMoved = true;
      } else {
        this.isLoadMap = true;
      }
    },

    // TODO: HACK LOCK UPDATE BEFORE CLICK CARD
    onActionEnd() {
      if (this.isNoBlockUpdate && this.isLoadMap) {
        this.isSection = true;
      }
      // карта. обработка событий сдвига карты
      const bound = this.myMap.getBounds();
      // Узнаем центры
      const from = ymaps.util.bounds.getCenter([
        [this.bound.SW.lat, this.bound.SW.lng],
        [this.bound.NE.lat, this.bound.NE.lng],
      ]);
      const to = ymaps.util.bounds.getCenter(bound);
      // deltaDistance
      const deltaDistance = ymaps.coordSystem.geo.getDistance(from, to);

      const distanceZoomFrom = ymaps.coordSystem.geo.getDistance(
        [this.bound.SW.lat, this.bound.SW.lng],
        [this.bound.NE.lat, this.bound.NE.lng]
      );
      const distanceZoomTo = ymaps.coordSystem.geo.getDistance(
        bound[0],
        bound[1]
      );

      // Удаление слоя карты
      if (this.myMap.getZoom() === 19) {
        this.myMap &&
        this.markLayer &&
        this.myMap.layers.remove(this.markLayer);
      } else {
        this.myMap && this.markLayer && this.myMap.layers.add(this.markLayer);
      }

      // Если сдвинули на < 10% экрана и зум не менялся блокируем обновления
      if (
        deltaDistance < distanceZoomFrom * 0.1 &&
        Math.abs(distanceZoomFrom - distanceZoomTo) < 200
      ) {
        return;
      } else if (this.lockUpdate) {
        // Блокирвока при открытие карточки на карте
        return;
      } else if (!this.isNoBlockUpdate) {
        // Пользователь решил не обновлять автоматически
        // TODO: скрыл т.к. появилась проблема VUECLIENT-1070
        //this.isNoBlockUpdate = true; //  ставит галочку на "поиск при пемещении"
        return;
      } else {
        this.onActionEndDebounced();
      }
    },
    onActionEndDebounced: _.debounce(function () {
      const bound = this.myMap.getBounds();
      this.$store.commit("search/setBound", {
        NE: {
          lat: bound[1][0],
          lng: bound[1][1],
        },
        SW: {
          lat: bound[0][0],
          lng: bound[0][1],
        },
      });
    }, 100),
    onRender(card) {
      const placemark = document
        .getElementById(this.selectedMapPointId)
        .getBoundingClientRect();
      const map = document.getElementById("yandexMap").getBoundingClientRect();

      // Возможно надо учитывать ширину маркера, НО КТО ЕЕ ЗАМЕТИТ ;)
      const offsetTop = Math.min(
        placemark.top - (card.height + 76) - map.top,
        0
      );
      const offsetLeft = Math.min(
        placemark.left - (card.width / 2 - 7) - map.left,
        0
      );
      const offsetRight = Math.max(
        placemark.right + (card.width / 2 + 20) - map.right,
        0
      );

      let top = placemark.top - map.top - card.height - 10;
      let left =
        placemark.left - map.left - card.width / 2 + placemark.width / 2;
      // Если смещения нет то просто показываем в нужно
      if (offsetLeft == 0 && offsetRight == 0 && offsetTop == 0) {
        this.coordinate = {
          top,
          left,
          isVisible: true,
        };
        this.lockUpdate = false;
      } else {
        this.coordinate = {
          top,
          left,
          isVisible: false,
        };
        this.lockUpdate = true;

        // Отступ в пикселах от центра
        let projection = this.myMap.options.get("projection");
        let pixels = this.myMap.converter.globalToPage(
          projection.toGlobalPixels(
            // географические координаты
            this.myMap.getCenter(),
            this.myMap.getZoom()
          )
        );
        // Расчет разницы в координатах
        pixels[0] += offsetLeft + offsetRight; //x
        pixels[1] += offsetTop; //y

        let moveToCoords = projection.fromGlobalPixels(
          this.myMap.converter.pageToGlobal(pixels),
          this.myMap.getZoom()
        );

        this.myMap.panTo(moveToCoords, { duration: 400 }).then(async () => {
          this.coordinate = {
            top: top - offsetTop,
            left: left - (offsetLeft + offsetRight),
            isVisible: true,
          };
          await this.$nextTick();
          await this.$nextTick();
          await this.$nextTick();
          this.lockUpdate = false;
        });
      }
    },
    updateZoom(e) {
      this.variableMapZoom = e;
    },
    async beachesIconSize(val) {
      if (this.objectManager && this.objectManager.objects) {
        this.objectManager.objects.options.set("iconImageSize", val);
      } else {
        this.initBeaches();
        this.$nextTick(() => {
          if (this.objectManager && this.objectManager.objects) {
            this.objectManager.objects.options.set("iconImageSize", val);
          }
        });
      }
    },
    onAbortMap() {
      this.$nextTick(() => {
        this.$store.commit("search/setBound", this.bound);
        this.simpleSearchStart();
      });
    },
    onInitMap(map) {
      this.myMap = map;

      const MarkLayer = new ymaps.Layer(this.apiLayer, {
        tileTransparent: true, // слой является прозрачным
      });
      this.myMap.layers.add(MarkLayer);
      this.markLayer = MarkLayer;

      if (this.isSearchGeocoderMap) {
        const searchControl = new ymaps.control.SearchControl({
          options: {
            float: "left",
            floatIndex: 100,
            noPlacemark: false,
            maxWidth: 620,
            fitMaxWidth: true,
            position: {
              top: 70,
              left: 20,
              right: 20,
            },
          },
        });
        this.myMap.controls.add(searchControl);
        setTimeout(() => {
          let versionYmaps = ymaps.meta.version.replace(/[\.\/]/g, "-");
          let controlS = map.container._mapElement.querySelector(
            `.ymaps-${versionYmaps}-searchbox__normal-layout`
          );
          if (controlS) {
            controlS.style.width = "94%";
          }
        }, 1000);
      }
      this.myMap.controls.add("zoomControl", {
        float: "none",
        position: {
          right: 10,
          top: 70,
        },
      });

      const { center, zoom } = ymaps.util.bounds.getCenterAndZoom(
        [
          [this.bound.SW.lat, this.bound.SW.lng],
          [this.bound.NE.lat, this.bound.NE.lng],
        ],
        this.myMap.container.getSize(),
        ymaps.projection.wgs84Mercator,
        {
          inscribe: false,
        }
      );

      this.mapZoom = zoom;
      this.mapCenter = center;

      this.$nextTick(() => {
        // После изменения BBOX пойдет пойск
        const bound = this.myMap.getBounds();
      });
      if (this?.getQuery?.hash === "map") {
        this.switchMapGet = true;
      }
    },
    onClick(mark) {
      this.$store.commit("search/setSelectedMapPointId", mark.id);
      this.$store.commit("search/setCardVisible", true);
    },
    onEnter(mark) {
      this.hoverPlacemark = mark.id;
      this.$store.commit("search/setHoverMapPointId", mark.id);
    },
    onLeave(mark) {
      this.hoverPlacemark = null;
      this.$store.commit("search/setHoverMapPointId", null);
    },
    // ...Ajax,
    async loadPage(page) {
      this.loadingObjects = true;
      return new Promise(async (resolve, reject) => {
        if (!this.results.hasOwnProperty(page)) {
          await this.getObjectsAsync(page);
          this.loadingObjects = false;
          resolve();
        } else {
          this.pageNum = page;
          this.loadingObjects = false;
          resolve();
        }
      });
    },
    async serachObj(request) {
      if (request.price_per == 2) {
        request.price = this.priceAllPeriod;
      }
      const searchApi =
        this.searchType === "search" && this.location.type && !this.isPosition
          ? "json/search/searchObjectsByLocation"
          : "json/search/searchObjects";
      return await HTTP.get(searchApi, {
        adapter: null,
        headers: {
          "api-version": String(null),
        },
        params: request,
        cancelToken: this.cancelSource.token,
        cache: false,
      })
        .then((response) => {
          this.searchApiErr = 0;
          this.reloadPage = false;
          this.setSearchProcessActive(false);
          return {
            succes: true,
            result: response.data.data.objects,
            adblock: Number(
              response.data.data.ad_block ||
              0 ||
              JSON.parse(localStorage.getItem("VUE_APP_ADBLOCK") || "0")
            ),
            count: response.data.data.total_count || response.data.data.count,
          };
        })
        .catch((e) => {
          // objectPosition
          clearTimeout(this.timeoutRefresh);
          this.timeoutRefresh = setTimeout(() => {
            if (this.searchApiErr < 2) {
              this.serachObj(request);
            }
            this.searchApiErr += 1;
            if (this.searchApiErr === 3) {
              this.reloadPage = true;
            }
          }, 1600);
          return {
            succes: false,
          };
        });
    },
    getTotalFound() {
      this.cancelSourceGetTotal = CancelToken.source();
      HTTP.get("rest/search/getTotalFound", {
        params: {
          location_id: this.location.id,
          location_type: this.location.type,
        },
        cancelToken: this.cancelSourceGetTotal.token,
      })
        .then((response) => {
          this.setTotalFoundAll(response.data.data.total_found || 0);
        })
        .catch((error) => {
          console.log("Ajax error: ", error);
        });
    },
    createParams(pageNum = 1) {
      this.status[this.pageNum] = "loading";
      let request = {
        ...this.objectFilters,

        currency_id: 1,
        "NE[lat]": this.bound ? this.bound.NE.lat : "",
        "NE[lng]": this.bound ? this.bound.NE.lng : "",
        "SW[lat]": this.bound ? this.bound.SW.lat : "",
        "SW[lng]": this.bound ? this.bound.SW.lng : "",
        // "size[x]": 865,
        // "size[y]": 421,
        zoom: 13,
        count: this.perPage,
        offset: (this.pageNum - 1) * this.perPage,
      };
      if (this.searchType === "search" && this.location.type) {
        request["location_type"] = this.location.type;
        request["location_id"] = this.location.id;
      }

      if (this.cancelSource && this.pageNum == 1) {
        this.cancelSource.cancel("Operation canceled by the user.");
      }
      this.cancelSource = CancelToken.source();
      // objectPosition
      clearTimeout(this.timeoutPending);
      this.searchObjectsAnswer = false;
      this.timeoutPending = setTimeout(() => {
        if (!this.searchObjectsAnswer) {
          this.searchObjectsPending = true;
        }
      }, 10000);

      let requestFilter = _.omit(request);

      if (requestFilter.ids === "") {
        requestFilter.ids = "[]";
      }
      return requestFilter;
    },
    getObjects: _.debounce(async function (pageNum = 1) {
      this.getObjectsAsync(pageNum);
      this.getTotalFound();
    }, 800),
    async getObjectsAsync(pageNum) {
      await this.serachObj(this.createParams(pageNum))
        .then((res) => {
          const { result, count, adblock, succes } = res;
          if (succes) {
            this.$store.commit("search/setTotalFound", count);
            if (pageNum == 1) {
              if (!this.isPosition) {
                this.results = {};
              }
              this.status = {
                1: "loading",
              };
            }
            result.forEach((elem, index) => {
              elem.price.full = Math.round(elem.price.full) || null;
              elem.price.per_night = Math.round(elem.price.per_night) || null;
            });

            this.adblock = adblock;
            this.results[pageNum] = result;
            if (count == 0 || count == this.results.length) {
              this.status[pageNum] = "complete";
            } else {
              this.status[pageNum] = "loaded";
            }

            this.searchObjectsPending = false;
            this.searchObjectsAnswer = true;

            // в store/search/state/objectsBronevik записываем id объектов
            const objectsBronevik = result.filter(
              (item) => item.owner.id === 4784337
            );
            const uniqId = _.uniq(objectsBronevik.map((o) => o.id));
            this.setObjectsBronevik(uniqId);

            let nextDay = new Date();
            nextDay.setDate(new Date().getDate() + 1);
            let presentDay = new Date();

            if (uniqId.length) {
              this.$store.dispatch("search/getPricesBronevik", {
                guests: Number(this.maxGuests),
                no_time: 1,
                date_begin: format(this.checkIn || presentDay),
                date_end: format(this.checkOut || nextDay),
                objects: uniqId,
                max_rooms: [],
                currency_id: 1,
              });
            }
          }
        })
        .catch((error) => {
          this.status[pageNum] = "fail";
          console.warn("Ошибка при загруки выдачи:", error);
        });
    },
    incrimentMapZoom() {
      this.mapZoom = this.mapZoom - 1;
      this.$nextTick(() => {
        const bound = this.myMap.getBounds();
        this.setBound({
          NE: {
            lat: bound[1][0],
            lng: bound[1][1],
          },
          SW: {
            lat: bound[0][0],
            lng: bound[0][1],
          },
        });
      });
    },
    addHashMap() {
      if (this?.getQuery?.hash !== "map") {
        this.$router.push({
          hash: "#map",
          query: this.getQuery.query,
        });
      }
    },
    removeHashMap() {
      if (this.$router.currentRoute.hash) {
        this.$router.push({
          hash: "",
          query: this.getQuery.query,
        });
      }
    },
    // обработчик для customEventsMap
    updateCustomEvents(val) {
      if (!val?.name) return;
      switch (val.name) {
        case "actionbegin":
          this.onActionBegin(val.event);
          break;
        case "actionend":
          this.onActionEnd(val.event);
          break;
        case "mousedown":
          this.onMouseDown(val.event);
          break;
        default:
          break;
      }
    },
    nextList() {
      if (this.isNextPage) {
        this.pageNum = this.pageNum+1;
        this.loadPage(this.pageNum);
      }
    }
  },
};
</script>

<style lang="scss" scoped>
.sutochno-widget-result {
  padding: 8px 20px;
  &.adaptive {
    padding: 20px 0;
  }
  &--count {
    margin-bottom: 10px;
    font-size: 12px;
    color: #444;
  }
  &--btn-more {
    position: relative;
    button {
      display: block;
      cursor: pointer;
      padding: 0;
      margin: 0;
      outline-style: none;
      border: none;
      border-radius: 4px;
      background: #f1f0ed;
      height: 42px;
      font-size: 14px;
      color: #000;
      text-align: center;
      width: 100%;
    }
  }
  &--map {
    height: 520px;
    background: #ededed;
    overflow: hidden;
    .adaptive & {
      height: 450px;
    }
  }
}


.map {
  grid-area: map;
  user-select: none;
  position: relative;
  z-index: 1;
  height: 100%;
  // z-index: 50;

  .loader-wrapper {
    position: absolute;
    width: 70px;
    height: 38px;
    top: 20px;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 19px;
    box-shadow: 0 2px 13px 0 rgba(0, 0, 0, 0.17);
    background-color: #fff;
    z-index: 2;
  }

  &__full {
    cursor: pointer;
    position: absolute;
    bottom: 60px;
    right: 10px;
    z-index: 1;

    background-image: url("~assets/img/map/full-map.svg");
    background-color: #fff;
    background-size: 43%;
    background-position: center;
    background-repeat: no-repeat;
    height: 36px;
    width: 36px;
    border-radius: 50%;
  }

  &__full--active {
    background-image: url("~assets/img/map/small-map.svg");
  }

  &__dont-touch {
    background: transparent;
    cursor: pointer;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
    transition: background 0.3s;

    &:hover {
      background: rgba(0, 0, 0, 0.05);
    }
  }

  &__switcher {
    cursor: pointer;
    position: absolute;
    z-index: 3;
    left: 0;
    top: 20px;

    width: 25px;
    height: 25px;
    background: #fff;
    box-shadow: 3px 2px 8px rgba(0, 0, 0, 0.25);

    &_block {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 100%;
    }
    &_rotate {
      .icon-app-select {
        transform: rotate(90deg) !important;
      }
    }
    .icon-app-select {
      font-size: 16px;
      color: #000;
      transform: rotate(-90deg);
      transition: transform 0.3s;
    }
  }
  ::v-deep .mark-dot {
    display: inline-block;
    width: 10px;
    height: 10px;
    border: solid 1px #ffffff;
    background-color: #444444;
    border-radius: 50%;
  }
  ::v-deep .map-placemark {
    position: relative;
    display: inline-block;
    white-space: nowrap;
    background-color: #ffffff;
    color: #000;
    height: 24px;
    box-sizing: border-box;
    padding: 0 8px;
    font-size: 12px;
    line-height: 24px;
    border-radius: 8px;
    transform: translate(-50%, -100%);
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.3);
    top: -5px;
    font-weight: 700;

    &:after {
      position: absolute;
      bottom: 5px;
      left: 50%;
      transform: translate(-50%, 100%) rotate(45deg);
      content: "";
      background-color: #ffffff;
      width: 8px;
      height: 8px;
      // box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
      box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.25);
    }
    &.buble-position {
      color: #ffa900;
    }
    .icon-app-lightning {
      display: inline-block;
      font-size: 11px;
      position: relative;
      top: 1px;
      width: 9px;
    }
    &.active {
      background-color: #444444;
      color: white;
      &:after {
        background-color: #444444;
      }
      z-index: 999;
    }
    &.visited {
      background-color: #f6f6f6;
      color: #717171;
      &:after {
        background-color: #f6f6f6;
      }
    }
    &.hover:not(.active) {
      transform-origin: 50% 100%;
      transform: translate(-50%, -100%) scale(1.2, 1.2);
      z-index: 998;
    }
    &.favorite {
      color: #f51449;
    }
    &.favorite.active {
      color: #fff;
    }
  }

  .sc-yandex-map {
    height: 100%;
  }
}
</style>
