<template>
  <div class="block-search" :class="viewWidget">
    <BaseSuggestions
        ref="baseSuggestions"
        class="main-block-field--suggestions"
    />
    <div
        v-click-outside="clickOutside"
        :style="{ zIndex: 10 }"
        class="block-search--occupied"
    >
      <div
          data-cy="checkIn"
          class="occupied"
          :class="{
                'main-block-field--occupied__in-fill': dateViews.in,
                'active':
                  activeIn || activeFieldIn,
                'main-block-field--occupied__in-active-choice':
                  (activeIn || activeFieldIn) && dateViews.in,
              }"
          @click="checkInOpen()"
      >
        <div class="title" :class="{ active: date.in }">
          {{ $t("occupiedIn") }}
        </div>
        <div class="date">
          {{ date.in ? format(date.in) : "" }}
        </div>
      </div>
      <div
          data-cy="checkOut"
          class="occupied"
          :class="{
                'main-block-field--occupied__out-fill': date.out,
                'active':
                  activeOut || activeFieldOut,
                'main-block-field--occupied__in-active-choice':
                  (activeOut || activeFieldOut) && date.out,
              }"
          @click="checkOutOpen()"
      >
        <div class="title" :class="{ active: date.out }">
          {{ $t("occupiedOut") }}
        </div>
        <div class="date">
          {{ date.out ? format(date.out) : "" }}
        </div>
      </div>
      <div class="calendar-in">
        <!-- TODO: какой то баг с v-model -->
        <DatePicker
            ref="calendar"
            v-model="date"
            :fill-mode="fillMode"
            v-model:date-views="dateViews"
            :show-header="!dateViews.in || !dateViews.out"
            :lang="$i18n.locale"
            @visibleCalendar="visibleCalendar = $event"
            @reset="handleReset"
        />
      </div>
    </div>
    <SelectGuests @clickGuests="clickGuests" />
    <div class="block-search--button">
      <button
        :class="{
          loading: isBtnDisabled || listSuggestions.isLoading,
          disabled: isBtnDisabled || listSuggestions.isLoading,
        }"
        :disabled="isBtnDisabled || listSuggestions.isLoading"
        @click="doSearch()"
      >
        Найти
      </button>
    </div>
  </div>
</template>

<script>
import _ from "lodash";
import {mapGetters, mapMutations, mapState} from "vuex";
import SelectGuests from "@/components/base/SelectGuests/Desktop";
import BaseSuggestions from "@/components/base/BaseSuggestions/";
import DatePicker from "@sutochno/datepickerext";

export default {
  name: "SearchMain",
  components: {
    SelectGuests,
    BaseSuggestions,
    DatePicker,
  },
  data() {
    return {
      isBtnDisabled: false,
      city: null,
      viewDate: true,
      presetCity: null,
      visibleCalendar: false,
      activeFieldIn: false,
      activeFieldOut: false,
      date: {
        in: null,
        out: null,
      },
      dateViews: {
        in: null,
        out: null,
      },
      isBlockMainFocus: true,
    }
  },
  watch: {
    "date.in"(val) {
      this.$store.commit("search/setCheckIn", val);
      if (val) {
        this.activeFieldIn = false;
      }
    },
    "date.out"(val) {
      this.$store.commit("search/setCheckOut", val);
      if (val) {
        this.clickOutside();
      }
    },
  },
  computed: {
    ...mapState("search", [
      "viewWidget",
      "location",
      "checkIn",
      "checkOut",
      "guests",
      "term",
      "listSuggestions",
      "selectedVariant",
      "dateFromHistorylist",
      "analiticParamForSearchWidList",
    ]),
    ...mapGetters("search", [
      "maxGuests",
      "termEmpty",
      "getIDsOfSelectedVariant",
    ]),
    activeIn() {
      return !!(
        this.visibleCalendar &&
        !this.dateViews.in &&
        !this.dateViews.out
      );
    },
    activeOut() {
      return !!(
        this.visibleCalendar &&
        this.dateViews.in &&
        !this.dateViews.out
      );
    },
    fillMode() {
      if (this.activeFieldIn) {
        return "in";
      } else if (this.activeFieldOut) {
        return "out";
      } else {
        return null;
      }
    },
  },
  methods: {
    ...mapMutations("search", ["setVisibleResult"]),
    handleReset() {
      this.activeFieldIn = false;
      this.activeFieldOut = false;
    },
    checkInOpen() {
      if (this.dateViews.in) {
        this.activeFieldOut = false;
        this.activeFieldIn = true;
      }
      this.$refs.calendar.show();
    },
    checkOutOpen() {
      if (this.dateViews.out) {
        this.activeFieldIn = false;
        this.activeFieldOut = true;
      }
      this.$refs.calendar.show();
    },
    preselectCity(city) {
      this.$store.dispatch("search/setLocation", city);
      this.$store.commit("search/setBound", {
        NE: {
          lat: city.bbox.NE.lat,
          lng: city.bbox.NE.lng,
        },
        SW: {
          lat: city.bbox.SW.lat,
          lng: city.bbox.SW.lng,
        },
      });
      this.presetCity = city;

      if (
        this.$children &&
        this.$children.find((item) => item.sight) &&
        this.$children.find((item) => item.sight).sight
      ) {
        this.$children.find((item) => item.sight).sight = null;
      }
      this.city = city; //HACK
    },
    clickOutside() {
      this.handleReset();
      this.$refs.calendar.close();
    },
    format(date) {
      console.log(date, 'date');
      if (!(date instanceof Date)) return "";
      const day = date.toLocaleString(this.$i18n.locale, {
        day: "numeric",
      });
      const month = date
        .toLocaleString(this.$i18n.locale, {
          month: "short",
        })
        .slice(0, 3);
      const weekday = date.toLocaleString(this.$i18n.locale, {
        weekday: "short",
      });
      return `${day} ${month}, ${weekday}`.toLocaleLowerCase();
    },
    getFormatOccupied(date) {
      const format = (date) => {
        const month = (date.getMonth() + 1).toString().padStart(2, 0),
          day = date.getDate().toString().padStart(2, 0),
          year = date.getFullYear();
        return year + "-" + month + "-" + day;
      };
      return date.in && date.out
        ? format(date.in) + ";" + format(date.out)
        : null;
    },
    async doSearch() {
      if (this.termEmpty) {
        if (!this.$refs.baseSuggestions.focused) {
          this.$refs.baseSuggestions.$refs.input.focus();
        }
        this.$refs.baseSuggestions.focused = true;
      } else if (!this.date.in || !this.date.out) {
        setTimeout(() => {
          this.checkInOpen();
        }, 100);
      } else {
        this.viewDate = false; // для того чтобы колендарь не открывался при нажатии найти
        this.isBtnDisabled = true;

        const occupied = {
          in: this.date.in,
          out: this.date.out,
        };

        const hotelIDS =
          this.selectedVariant?.data?.type === "hotel" &&
          this.getIDsOfSelectedVariant;

        const query = _.pickBy({
          occupied: this.getFormatOccupied(occupied),
          guests_adults: this.guests.adults,
          guests_childrens: this.guests.childrens.join(","),
          // id: this.location.id,
          // type: this.location.type,
          term: this.term,
          ids: hotelIDS || null,
        });

        if(this.analiticParamForSearchWidList?.type === "history"){
          query.search_history = 1;
        } else if (this.analiticParamForSearchWidList?.type === "hotels"){
          query.search_hotel = this.analiticParamForSearchWidList.value;
        }

        await this.$nextTick();

        this.setVisibleResult(true);

        this.isBtnDisabled = false;

      }
    },
    keypressEnter(event) {
      if (event.which === 13 || event.keyCode === 13) {
        if (this.isBlockMainFocus) {
          if (!this.location.bbox) {
            this.$refs.baseSuggestions.showList();
            return false;
          }
          if (!this.checkIn && !this.checkOut) {
            this.$refs.calendar.show();
            return false;
          }
          if (this.location.bbox && this.checkIn && this.checkOut) {
            this.doSearch();
            return true;
          }
        }
      }
    },
    focusBlockMain() {
      this.isBlockMainFocus = true;
    },
    blurBlockMain() {
      this.isBlockMainFocus = false;
    },
  }
}
</script>

<style lang="scss" scoped>
.block-search {
  display: grid;
  grid-template-columns: 1fr 274px 238px auto;
  grid-template-areas: "search occupied guest button";
  column-gap: 10px;
  &.adaptive {
    column-gap: 0;
    row-gap: 10px;
    grid-template-columns: 1fr;
    grid-template-areas:
      "search"
      "occupied"
      "guest"
      "button"
    ;
  }
  &--occupied {
    grid-area: occupied;
    display: flex;
    justify-content: space-between;
    position: relative;
    .occupied {
      cursor: pointer;
      padding: 0 20px;
      width: 50%;
      height: 60px;
      border-radius: 4px;
      border: solid 1px var(--input-border);
      display: flex;
      flex-direction: column;
      justify-content: center;
      &:nth-child(1) {
        margin-right: 5px;
      }
      &:nth-child(2) {
        margin-left: 5px;
      }
      &.active {
        box-shadow: inset 0 0 0 1px #f51449;
      }
      .title {
        color: var(--brownish-grey);
        font-size: 16px;
        margin-bottom: -11px;
        padding-top: 17px;
        transform: translate(0, -13px) scale(1);
        transform-origin: 20px 0;
        transition: transform 0.15s, opacity 0.1s;
        &.active {
          transform: translate(-3px, -10px) scale(0.85714);
        }
      }
      .body {
        color: var(--black);
        font-size: 16px;
      }
    }
    .calendar-in {
      position: absolute;
      top: 100%;
      margin-top: 10px;
    }
  }
  &--button {
    grid-area: button;
    button {
      display: inline-block;
      cursor: pointer;
      outline-style: none;
      padding: 0 20px;
      margin: 0;
      height: 60px;
      min-width: 124px;
      width: 100%;
      border-radius: 4px;
      background: var(--bg-button);
      color: var(--color-button);
      font-size: 16px;
      border: none;
      position: relative;
    }
  }
}
</style>
