export default function (Component) {
  Component.__i18n = Component.__i18n || []
  Component.__i18n.push({
    "locale": "",
    "resource": {
      "ru": {
        "add": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Добавить в избранное"])},
        "remove": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Убрать из избранного"])},
        "added": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Добавлено"])}
      },
      "en": {
        "add": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Add to favourites"])},
        "remove": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Remove from favorites"])},
        "added": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Added"])}
      }
    }
  })
}
