export default function (Component) {
  Component.__i18n = Component.__i18n || []
  Component.__i18n.push({
    "locale": "",
    "resource": {
      "ru": {
        "title": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["В этой области нет вариантов"])},
        "text": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Сдвиньте карту или сбросьте фильры, чтобы увидеть предложения."])},
        "resetAllFilters": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Показать варианты"])}
      }
    }
  })
}
