<i18n>
{
	"ru": {
    "reviews": "отзывов | {n} отзыв | {n} отзыва | {n} отзывов",
    "meter": " м²"
	},
  "en": {
    "reviews": "reviews | {n} reviews | {n} reviews | {n} reviews",
    "meter": " m²"
  }
}
</i18n>

<template>
  <div class="card-desc tmp-font--small">
    <span v-if="countReviws && rating" class="card-desc__rating">
      <span class="icon-app-star card-desc__rating-icon"></span>
      <span class="card-desc__rating-number tmp-font--small_bold">
        {{ $filters.newRating(rating) }}</span
      >
      <span class="card-desc__count-review"> ({{ countReviws }})</span>
    </span>
    <span v-else-if="countReviws" class="card-desc__reviews">{{
      $tc("reviews", countReviws)
    }}</span>
    <span class="card-desc__desc">{{ square }}{{title}}</span>
  </div>
</template>

<script>
export default {
  name: "ObjectInformation",
  props: {
    item: {
      type: Object,
    },
  },
  computed: {
    countReviws() {
      return (
        this.item.count_reviews +
        this.countReviewsExternal(this.item.external_reviews)
      );
    },
    rating() {
      if (this.item.rating) {
        return Math.abs(this.item.rating);
      }
      return null;
    },
    square() {
      if (this.item.area) {
        return this.item.area + this.$t("meter");
      }
      return "";
    },
    title(){
      if (this.item.title.trim()) {
        return ", " + this.item.title;
      }
      return "";
    }
  },
  methods: {
    countReviewsExternal(external_reviews) {
      const externalReviews = external_reviews;
      if (externalReviews) {
        const countAirbnb = externalReviews[0]["count"]
          ? externalReviews[0]["count"]
          : 0;
        const countBooking = externalReviews[1]["count"]
          ? externalReviews[1]["count"]
          : 0;

        return countAirbnb + countBooking;
      } else {
        return 0;
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.card-desc {
  display: flex;
  align-items: center;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  color: #7f7f7f;
}
.card-desc__rating {
  display: flex;
  align-items: center;
  color: #000;
}
.card-desc__rating-icon {
  color: #f51449;
  font-size: 18px;
}
.card-desc__rating-number {
  margin-right: 2px;
}
.card-desc__count-review,
.card-desc__reviews {
  padding-right: 10px;
  color: #717171;
}
.card-desc__desc {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
</style>
