<i18n>
{
  "ru": {
    "founded_results": "Найдено|Найден|Найдено|Найдено",
    "count": "вариантов жилья|вариант жилья|варианта жилья|вариантов жилья",
    "housing": "жилья из",
    "housing_map": "жилья на карте"
  },
  "en": {
    "founded_results": "Found",
    "count": " options housing from|option housing from|option housing from|options housing from",
    "housing": "housing from",
    "housing_map": "housing on the map"
  }
}
</i18n>
<template>
  <div class="sorting" :class="viewWidget">
    <div class="sorting--amount">
      <span :class="{ load: isLoading }">{{ totalFoundText }}</span>
      <Loader v-if="isLoading" />
    </div>
  </div>
</template>

<script>
import { mapMutations, mapState } from "vuex";
import Loader from "@/components/base/Loader.vue";

export default {
  name: "ResultTotalFound",
  components: {
    Loader,
  },
  props: {
    isLoading: {
      type: Boolean,
      default: false,
    },
    isMovedMap: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    ...mapState("search", [
      "viewWidget",
      "sortings",
      "cheap_and_fast",
      "totalFound",
      "totalFoundAll",
    ]),
    totalFoundAllText() {
      if (this.totalFoundAll) {
        const count = new Intl.NumberFormat(this.$i18n.locale, {
          minimumFractionDigits: 0,
        }).format(this.totalFoundAll);
        return `${this.$t("housing")} ${count}`;
      } else {
        return "";
      }
    },
    totalFoundText() {
      let plus = this.totalFound == 10000 ? "+" : "";
      const count =
        new Intl.NumberFormat(this.$i18n.locale, {
          minimumFractionDigits: 0,
        }).format(this.totalFound) + plus;

      if (this.isMovedMap) {
        return `${
          this.$tc("founded_results", this.totalFound)
        } ${
          count
        } ${
          this.$tc("count", count)
        }`
      }

      return `${this.$tc(
        "founded_results",
        this.totalFound
      )} ${count} ${this.$tc("count", this.totalFound)} ${
        this.totalFoundAllText
      }`;
    },
  },
};
</script>

<style lang="scss" scoped>
.sorting {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 24px;
  &.adaptive {
    margin-bottom: 10px;
  }
  &--amount {
    color: #444444;
    font-size: 12px;
    position: relative;
    span {
      transition: opacity 0.2s ease;
      opacity: 1;
      &.load {
        opacity: 0.4;
      }
    }
    .lodaer-dots {
      position: absolute;
      top: 50%;
      left: 50%;
      margin: -2px 0 0 -14px;
    }
  }
}
</style>
