<i18n>
{
  "ru": {
    "price_any": "любая цена",
    "price_from":"oт",
    "price_to":"до"
  },
  "en": {
    "price_any": "any price",
    "price_from":"from",
    "price_to":"to"
  }
}
</i18n>

<template>
  <RangeNumber
    v-model:value-min="rangePriceMin"
    v-model:value-max="rangePriceMax"
    :min="0"
    :step="1"
    :placeholder-min="0"
    :placeholder-max="triol(price[price.length - 1]) + '+'"
    :input-after="currencyVal"
    @changeValue="changePrice($event)"
  />
</template>
<script>
import _ from "lodash";
import RangeNumber from "./RangeNumber";

export default {
  name: "SliderRange",
  components: {
    RangeNumber,
  },
  props: {
    modelValue: {
      type: Array,
      default: function () {
        return [null, null];
      },
    },
    price: {
      type: Array,
    },
    flowPrice: {
      type: Array,
    },
    viewPort: {
      type: String,
    },
    currency: {
      type: Object,
    },
    clearFilters: {
      type: Object,
    },
    viewFilters: {
      type: Boolean,
      default: false,
    },
  },
  mounted() {
    this.currencySymbol();
  },
  methods: {
    init(priceArray) {
      let price = priceArray.map(Number);

      if (price[0] != 0) {
        let priceMinVal = this.nearPrice(this.price, price[0]);
        let priceMinIndex = this.price.findIndex((item) => item == priceMinVal);
        this.rangeMin = priceMinIndex;
        this.rangePriceMin = price[0];
      } else {
        this.rangeMin = 0;
        this.rangePriceMin = null;
      }

      if (price[1] != 0) {
        let priceMaxVal = this.nearPrice(this.price, price[1]);
        let priceMaxIndex = this.price.findIndex((item) => item == priceMaxVal);
        this.rangeMax = priceMaxIndex;
        this.rangePriceMax = price[1];
      } else {
        this.rangeMax = this.rangeMaxVal - 1;
        this.rangePriceMax = null;
      }
    },

    // ввод цены в input
    changePrice: _.debounce(function (val) {
      if (val === "min") {
        if (
          this.rangePriceMin > this.rangePriceMax &&
          this.rangePriceMax !== null
        ) {
          this.rangeMin = this.rangeMax;
          this.rangePriceMin = this.rangePriceMax;
        }
      }
      if (val == "max") {
        if (
          this.rangePriceMax < this.rangePriceMin &&
          this.rangePriceMin !== null
        ) {
          this.rangeMax = this.rangeMin;
          this.rangePriceMax = this.rangePriceMin;
        }
      }

      if (this.rangePriceMin !== null) {
        let minRange = this.nearPrice(this.price, this.rangePriceMin);
        let minIndex = this.price.findIndex((item) => item == minRange);
        this.rangeMin = minIndex;
      }

      if (this.rangePriceMax !== null && this.rangePriceMax !== undefined) {
        let maxRange = this.nearPrice(this.price, this.rangePriceMax);
        let maxIndex = this.price.findIndex((item) => item == maxRange);
        this.rangeMax = maxIndex;
      }
      this.emitPrice();
    }, 1500),


    // ближайшее число из массива
    nearPrice(arrayRange, elem) {
      return arrayRange.reduce(function (prev, curr) {
        return Math.abs(curr - elem) < Math.abs(prev - elem) ? curr : prev;
      });
    },

    // отправка в filter.price
    emitPrice() {
      this.isFiltersState = false;
      const valueMin = this.modelValue[0] || null;
      const valueMax = this.modelValue[1] || null;
      const min =
        this.rangePriceMin === undefined ||
        this.rangePriceMin === null ||
        this.rangePriceMin === 0
          ? null
          : this.rangePriceMin;
      const max =
        this.rangePriceMax === undefined ||
        this.rangePriceMax === null ||
        this.rangePriceMax === 0
          ? null
          : this.rangePriceMax;
      if (valueMin != min || valueMax != max) {
        this.$emit("update:modelValue", [min, max]);

        setTimeout(() => {
          this.isFiltersState = true;
        }, 300);
      }
    },

    // получаем символ валюты
    currencySymbol() {
      // TODO - this.currency.title test
      const currency = new Intl.NumberFormat(this.$i18n.locale, {
        style: "currency",
        currency: "RUB",
        minimumFractionDigits: 0,
      })
        .format(0)
        .replace("0", "")
        .trim();
      this.currencyVal = currency;
    },

    // ввод только цифр
    isNumber: function (evt) {
      evt = evt ? evt : window.event;
      let charCode = evt.which ? evt.which : evt.keyCode;
      if (
        charCode > 31 &&
        (charCode < 48 || charCode > 57) &&
        charCode !== 46
      ) {
        evt.preventDefault();
      } else {
        return true;
      }
    },
    triol(val) {
      return val
        ? val.toLocaleString(this.$i18n.locale, { useGrouping: true })
        : 0;
    },
  },
  data() {
    return {
      rangeMaxVal: 16,
      rangePriceMin: null,
      rangePriceMax: null,
      rangeMin: 0,
      rangeMax: 15,
      minShiftX: null,
      maxShiftX: null,
      btnClick: "",
      stepSliderWidth: null,
      offsetRightSlider: null,
      currencyVal: "",
      isFiltersState: true,
    };
  },
  computed: {
    procentMin() {
      return (this.rangeMin / this.rangeMaxVal) * 100;
    },
    procentMax() {
      return (this.rangeMax / this.rangeMaxVal) * 100;
    },
  },
  watch: {
    modelValue: {
      deep: true,
      handler(val, oVal) {
        if (this.isFiltersState) {
          // чтобы обновлялось только при обновлении значения в сторе
          this.init(val);
          this.isFiltersState = true;
        }
      },
    },
    currency(val) {
      this.rangePriceMin = this.price[this.rangeMin];
      this.rangePriceMax = this.price[this.rangeMax];
      this.currencySymbol();
      this.emitPrice();
    },
    // сброс фильтра
    clearFilters(val) {
      if (val.price === undefined) {
        this.rangeMin = 0;
        this.rangeMax = 15;
        this.rangePriceMin = null;
        this.rangePriceMax = null;
      }
    },
  },
};
</script>
<style lang="scss" scoped>
.slider-range {
  padding-bottom: 10px;
  &.mobile {
    // padding: 0 10px;
    // margin-bottom: 18px;
    padding: 0 10px 18px 10px;
  }
  &.desktop {
    margin-top: -5px;
  }
  &--svg {
    padding: 0 6px;
    margin: 0 0 -1px 1px;
  }
  &--btn {
    height: 3px;
    background: #f51449;
    position: relative;
    width: 100%;
    .line {
      height: 3px;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      background: #f51449;
    }
    button {
      cursor: pointer;
      width: 25px;
      height: 25px;
      border-radius: 30px;
      position: absolute;
      top: -12px;
      left: 0;
      background: #fff;
      border: 3px solid #f51449;
      outline-style: none;
      padding: 0;
      margin: 0;
      left: 0;
    }
    &_mobile {
      button {
        height: 25px;
        top: -12.5px;
        width: 25px;
        &.max {
          left: calc(100% - 25px);
        }
      }
    }
  }
  &--text {
    display: flex;
    font-size: 14px;
    padding: 4px 0 5px;

    .currency {
      position: absolute;
      right: 15px;
      // margin-right: 6px;
    }
    input[type="text"],
    input[type="number"],
    input[type="tel"] {
      outline-style: none;
      // padding: 4px 8px;
      margin: 0 6px;
      background: none;
      border-radius: 3px;
      border: 1px solid #ccc;
      font-size: 14px;
      // width: 80px;
      padding: 9px 15px;
      // width: 105px;
      width: 100%;
      max-width: 105px;
      min-width: 72px;
      &:focus {
        &::-webkit-input-placeholder {
          color: transparent;
        }
        &:-moz-placeholder {
          color: transparent;
        }
        &::-moz-placeholder {
          color: transparent;
        }
        &:-ms-input-placeholder {
          color: transparent;
        }
      }
    }
    input[type="number"] {
      -moz-appearance: textfield;
      &::-webkit-outer-spin-button,
      &::-webkit-inner-spin-button {
        -webkit-appearance: none;
      }
    }
    span {
      display: flex;
      align-items: center;
      position: relative;
      &:first-child {
        margin-right: 3px;
      }
      &:last-child {
        margin-left: 3px;
      }
    }
    &_mobile {
      align-items: center;
      justify-content: space-between;
      // padding-top: 28px;
      margin-bottom: 14px;
    }
    &_desktop {
      display: flex;
      justify-content: space-between;
      padding-bottom: 20px;
    }

    :deep(.range-number.after-value) {
      .range-number {
        &-input {
          padding: 13px 23px 12px 15px;
        }
        &-separator {
          padding: 0 9px;
        }
      }
      input[type="number"] {
        -moz-appearance: textfield;
        &::-webkit-outer-spin-button,
        &::-webkit-inner-spin-button {
          -webkit-appearance: none;
        }
      }
    }
  }
}
</style>
