<i18n>
{
	"ru": {
		"label": "Курорт, город или адрес"
	},
	"en": {
	}
}
</i18n>
<template>
  <div v-click-outside.mousedown="clickOutside" class="suggest-wrapper">
    <input
      ref="input"
      v-model="query"
      class="suggest-input"
      :class="{ 'suggest-input--fill': query && query.length }"
      autocomplete="off"
      type="text"
      name="suggest"
      @focus="onFocus"
    />
    <label class="suggest-label tmp-font--average" for="suggest">{{
      $t("label")
    }}</label>
    <div class="suggest-close icon-app-cross-min" @click="onClear"></div>
    <List
      v-if="focused || visibleList"
      ref="list"
      class="suggest-list"
      :value="query"
      :is-complete="true"
      @choice="onChoice"
    />
    <ModalReload :show="listSuggestions.isError" />
  </div>
</template>
<script>
import List from "./List";
import { mapState, mapGetters } from "vuex";
import ModalReload from "./ModalReload";

export default {
  name: "Bar",
  components: {
    List,
    ModalReload,
  },
  data() {
    return {
      query: "",
      focused: false,
      visibleList: false,
    };
  },
  computed: {
    ...mapState("search", ["term", "listSuggestions"]),
    ...mapGetters("search", ["getTypeOfSelectedVariant"])
  },
  watch: {
    query() {
      this.focused = true;
    },
    term: {
      deep: true,
      handler(term) {
        if (this.query != term) {
          this.query = this.getTypeOfSelectedVariant === 'hotel' ? this.query : term;
          this.$nextTick(() => {
            this.focused = false;
            this.visibleList = false;
          });
        }
      },
    },
  },
  methods: {
    onChoice(variant) {
      this.query = variant.title;
      this.$nextTick(() => {
        this.$refs.input.blur();
        this.focused = false;
        this.visibleList = false;
      });
    },
    clickOutside() {
      this.focused = false;
      this.visibleList = false;
    },
    onClear() {
      this.query = "";
      // Сброс локации города
      this.$store.commit("search/setLocation");
      this.$refs.input.focus();
      this.$store.commit("search/setTerm");
    },
    // onBlur() {
    //   setTimeout(() => {
    //     this.focused = false;
    //   }, 200)
    // },
    onFocus() {
      this.focused = true;
      this.$nextTick(() => {
        this.$refs.list.$el.scrollIntoView({
          block: "end",
          behavior: "smooth",
        });
      });
    },
    showList() {
      this.visibleList = true;
      this.$refs.input.focus();
    },
  },
};
</script>

<style lang="scss" scoped>
.suggest {
  &-wrapper {
    position: relative;
  }
  &-label {
    color: #717171;
    position: absolute;
    left: 0;
    top: 0;
    pointer-events: none;
    transform: translate(0px, 0px) scale(1);
    transition: transform 0.15s, opacity 0.1s;
    transform-origin: 20px 0;
    display: inline-flex;
    align-items: center;
    padding: 0 20px;
    height: 100%;
    width: 100%;
    white-space: nowrap;
  }
  &-input {
    height: 60px;
    padding: 0 19px;
    width: 100%;
    background: #ffffff;
    border-radius: 4px;
    border: solid 1px var(--input-border);
    padding-top: 18px;
    &:focus {
      border: 1px solid #f51449;
      outline: none;
    }
  }
  &-input:focus + &-label,
  &-input--fill + &-label {
    transform: translate(0px, -6px) scale(0.85714);
  }
  &-close {
    position: absolute;
    right: 0px;
    top: 50%;
    font-size: 16px;
    padding: 10px;
    transform: translateY(-50%);
    cursor: pointer;
    opacity: 0;
  }
  .icon-app-cross-min {
    &::before {
      color: red;
    }
  }
  &-input--fill ~ &-close {
    opacity: 1;
  }
  // &-list {
  //   display: none;
  // }
  &-list {
    margin-top: 4px;
    width: 592px;
  }
}
</style>
