<i18n>
{
	"ru": {
    "recommend": "Гости рекомендуют",
    "seeReviews": "Читать отзывы гостей"
	},
	"en": {
    "recommend": "Guest's choice",
    "seeReviews": "Read guest reviews"
	}
}
</i18n>

<template>
  <div
    :id="choiceObject ? 'choice-object-rockets' : null"
    class="card"
    :class="{
      'card__choice-position': choiceObject,
      card__position: isPosition,
      card__refresh: choiceRefresh,
    }"
    @click="clickObjectWrap"
  >
    <!-- :class="{ card_recommended: objectData.is_recommended }" -->
    <div
      class="card__sticker"
      :class="{ 'card__sticker-choice': choiceObject }"
      @mouseover="mouseOver"
      @mouseout="mouseOut"
    >
      <a
        class="card__link"
        target="_blank"
        :href="href"
        @mousedown="clickObjectBronevik($event, linkBronevik)"
      >
        <span
          v-if="currentPosition"
          class="card__sticker-text"
          :class="{
            'card__sticker-refresh': choiceRefresh,
          }"
          >{{ currentPosition.pos }}</span
        >
      </a>
    </div>

    <div class="card__img">
      <Img
        :linkBronevik="linkBronevik"
        :isBronevik="isBronevik"
        :choiceObject="choiceObject"
        :isDisabledLink="isDisabledLink"
        :currentPosition="currentPosition"
        :data="objectData"
        :hideOwnerPopularity="hideOwnerPopularity"
        :flag="flag"
        :i="i"
      />
    </div>
    <Content
      :linkBronevik="linkBronevik"
      :isBronevik="isBronevik"
      :isDisabledLink="isDisabledLink"
      :user="user"
      :flag="flag"
      :hideOwnerPopularity="hideOwnerPopularity"
      :rating="rating"
      :data="objectData"
      :show-detail="showDetail"
    />
    <div class="delimiter"></div>
    <Prices
      :linkBronevik="linkBronevik"
      :user="user"
      :flag="flag"
      :hideOwnerPopularity="hideOwnerPopularity"
      :rating="rating"
      :data="objectData"
      :show-detail="showDetail"
    />
    <LoadingPrice v-if="bronevikLoadingPrice" />
  </div>
</template>
<script>
import { mapState, mapGetters } from "vuex";
import Img from "./Img";
import Content from "./Content";
import Prices from "./Prices";
import LoadingPrice from "@/components/base/LoadingPrice.vue";
// import ObjectDetail from "./ObjectDetail";
export default {
  name: "ObjectBlock",
  components: {
    Img,
    Content,
    Prices,
    // ObjectDetail,
    LoadingPrice,
  },
  props: {
    objectData: {
      type: Object,
    },
    isBronevik: {
      type: Boolean,
      default: false,
    },
    analyticsEvent: {
      type: Boolean,
      default: false,
    },
    rating: {
      default: null,
    },
    flag: {
      default: null,
    },
    i: {
      type: Number,
    },
    choiceObject: {
      type: Object,
      default: false,
    },
    choiceRefresh: {
      type: Object,
      default: false,
    },
    currentPosition: {
      type: Object,
      default: null,
    },
  },
  data() {
    return {
      showDetail: false,
      hoveredTop: false,
      bronevikLoadingPrice: false,
    };
  },
  computed: {
    ...mapState("search", [
      "guests",
      "isPosition",
      "objectsBronevik",
      "pricesBronevik",
      "loadBronevikPrice",
    ]),
    ...mapState("searchPr", ["objectPosition"]),
    ...mapGetters("searchPr", [
      "getPositionObjects",
      "getPositionCurrentObjects",
    ]),
    ...mapGetters("search", [
      "occupied",
      "getQuery",
      "getUsersSearchParams",
      "getAvgRatings",
    ]),
    conveniencesList() {
      if (!this.objectData.conveniences) return [];
      return Object.entries(this.objectData.conveniences)
        .filter(([key, value]) => value && key)
        .map(([key]) => key);
    },
    stickerText() {
      return this.hoveredTop ? this.$t("seeReviews") : this.$t("recommend");
    },
    user() {
      const user = this.getUsersSearchParams.find(
        (item) => Number(item.user_id) === Number(this.objectData.owner.id)
      );
      return user ? user : null;
    },
    // rating() {
    //   const rating = this.getAvgRatings.find(item => Number(item.object_id) === Number(this.objectData.id))
    //   return rating
    //     ? rating
    //     : null
    // },
    hideOwnerPopularity() {
      return this.objectData?.owner?.permissions?.includes(
        "hideObjectsPopularityStatus"
      )
        ? true
        : false;
    },
    dataBronevik() {
      let idBronevik = this.objectsBronevik.find(
        (item) => item == this.objectData.id
      );
      if (idBronevik) {
        return this.pricesBronevik &&
          this.pricesBronevik[idBronevik] &&
          this.pricesBronevik[idBronevik]["prices"]
          ? this.pricesBronevik[idBronevik]["prices"]
          : [];
      } else {
        return [];
      }
    },
    linkBronevik() {
      if (this.dataBronevik.length) {
        return this.dataBronevik[0].link;
      } else if (this.objectData.url && this.objectData.owner.id === 4784337) {
        return this.objectData.url;
      } else {
        return "";
      }
    },
    isDisabledLink() {
      let idBronevik = this.objectsBronevik.find(
        (item) => item == this.objectData.id
      );
      if (idBronevik) {
        if (this.dataBronevik.length) {
          return false;
        } else {
          return true;
        }
      } else {
        return false;
      }
    },
  },
  watch: {
    loadBronevikPrice(val) {
      if (!val) {
        if (this.bronevikLoadingPrice) {
          this.bronevikLoadingPrice = false;
        }
      }
    },
  },
  methods: {
    showPage() {
      this.showDetail = !this.showDetail;
      document.body.style.overflow = "hidden";
      return this.showDetail;
    },
    mouseOver() {
      this.hoveredTop = true;
    },
    mouseOut() {
      this.hoveredTop = false;
    },
    clickObjectWrap(event) {
      if (this.isDisabledLink) {
        if (!this.occupied) {
          setTimeout(() => {
            document.querySelector("#datepickerSearch").click();
          }, 150);
        }
        event.preventDefault();
        if (this.occupied) {
        }
      }
      if (this.loadBronevikPrice) {
        event.preventDefault();
        this.bronevikLoadingPrice = true;
      }
    },
    clickObjectBronevik(event, link) {
      if (link || (link && event.which == 1) || (link && event.which == 2)) {
        event.preventDefault();
        window.open(link);
      }
    },
  },
};
</script>
<style lang="scss" scoped>
.card-list__wrapper {
  span {
    &:first-child {
      .card.card__choice-position {
        margin-top: 20px;
      }
    }
  }
}
.card {
  display: grid;
  width: 100%;
  grid-template-columns: 300px 1fr 1px 199px;
  grid-template-rows: auto 200px;
  grid-template-areas: "carousel content prices";
  grid-template-rows: 220px;
  border: none;
  margin: 0px 0 20px 0;
  position: relative;
  border-radius: 4px;

  &__position {
    margin: 40px 0 20px 0;

    &:first-child {
      margin-top: 23px;
    }
  }

  &__choice-position {
    outline: solid 2px #ffa900;

    &.card__refresh {
      outline: solid 1px #f51449;
    }
  }

  &--selected,
  &:hover {
    box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.12);
  }

  &__sticker {
    grid-area: top;
    position: absolute;
    top: -24px;
    left: 0;

    &-choice {
      top: -25px;
    }
  }

  &__link {
    color: inherit;
  }
  &__sticker-text {
    background: #ffffff;
    border-radius: 4px;
    font-size: 13px;
    line-height: 17px;
    padding: 2px 3px;
    color: inherit;
    min-width: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #444444;
    .card__sticker-choice & {
      color: #ffffff;
      background: #ffa900;

      &.card__sticker-refresh {
        background: #fdd2d2;
      }
    }
  }

  .card__img {
    grid-area: carousel;
    z-index: 1;
  }
  .object-content {
    grid-area: content;
  }
  ::v-deep .object-prices {
    grid-area: prices;
    // display: flex;
    // flex-direction: column;
    // align-items: center;
  }
  .delimiter {
    width: 1px;
    height: auto;
    background-color: #fff;
    &::before {
      content: "";
      display: block;
      height: calc(100% - 40px);
      margin: 20px 0;
      border-left: 1px solid #d8d8d8;
    }
  }
  &.disabled-link {
    ::v-deep {
      .price-bronevik,
      .price-order {
        opacity: 0.7;
      }
    }
  }
}
a {
  text-decoration: none;
  &:hover {
    text-decoration: none;
  }
}
</style>
