<template>
  <div class="sc-placemark"></div>
</template>
<script>
// Module scope
// хак который сработает и для defineProps и для defineEmits
export const eventsObjectManager = ["click", "mouseenter", "mouseleave"];
export default {};
</script>
<script setup>
// Setup scope
import { inject, computed, watch, onMounted, onUnmounted } from "vue";
import intersection from "lodash/intersection";
import difference from "lodash/difference";

const props = defineProps({
  placemarks: {
    type: Object,
    default: () => ({}),
  },
  options: {
    type: Object,
    default: () => ({}),
  },
});
const emit = defineEmits(eventsObjectManager);

const myMap = inject("myMap");
const unwatchArr = []; // для вотчеров которые сами не размонтируются
let objectManager = null;

function prepareOptions({ options, typeMark }) {
  switch (typeMark) {
    case "html":
      return {
        ...options,
        iconLayout: ymaps.templateLayoutFactory.createClass(options.iconLayout), // Рендер html метки
        /* Фигура на каторую можно тыкать
        {
          type: 'Rectangle',
          // Прямоугольник описывается в виде двух точек - верхней левой и нижней правой.
          coordinates: [
            [-35, -25], [35, 0]
          ]
        }
        */
        iconShape: options.iconShape,
        zIndex: options.zIndex || 666, // Норм диапазон от 650-775 (https://yandex.ru/blog/mapsapi/24516#56a973e9b15b79e31e0d3361)
      };
      break;
    case "image":
      return {
        ...options,
        // "default#image"
        // "default#imageWithContent"
        iconLayout: "default#image",
        iconImageHref: options.iconImageHref, //require("assets/img/flag.png"),
        iconImageSize: options.iconImageSize, //[28, 30],
        iconImageOffset: options.iconImageOffset, //[0, -30]
        zIndex: options.zIndex || 666,
      };
      break;
    default:
      return {
        ...options,
      };
      break;
  }
}
function renderPlacemark(id, option) {
  return {
    type: "Feature",
    id: id,
    geometry: {
      type: "Point",
      coordinates: option.coords,
    },
    properties: option.properties,
    options: prepareOptions(option),
  };
}
function updatePlacemark(id, option) {
  objectManager.objects.setObjectOptions(id, {
    ...prepareOptions(option),
  });
}

function createInternalPlacemarks() {
  // Копируем чтобы отслеживать изменения до и после
  const internalPlacemarks = computed(() =>
    JSON.parse(JSON.stringify(props.placemarks))
  );
  unwatchArr.push(
    watch(internalPlacemarks, (newVal, oldVal) => {
      // Индентификаторы меток
      const newIDs = Object.keys(newVal);
      const oldIDs = Object.keys(oldVal);

      // Новые метки
      const newPlacemarks = difference(newIDs, oldIDs).map((key) => {
        return renderPlacemark(key, newVal[key]);
      });
      if (newPlacemarks.length) objectManager.add(newPlacemarks);

      // Удаляем старые
      const removeIDs = difference(oldIDs, newIDs);
      if (removeIDs.length) objectManager.objects.remove(removeIDs);

      // Обновленные метки
      const updateIDs = intersection(newIDs, oldIDs).filter(
        (ID) => JSON.stringify(oldVal[ID]) != JSON.stringify(newVal[ID])
      );
      updateIDs.forEach((ID) => updatePlacemark(ID, newVal[ID]));
    })
  );
}

// hooks
onMounted(() => {
  // Создаем менеджер объектов
  objectManager = new window.ymaps.ObjectManager(props.options);
  createInternalPlacemarks();

  // Добавляем менеджер объектов на карту
  myMap.value.geoObjects.add(objectManager);

  // Добавляем метки на карту при создании компонента
  const newMarks = Object.entries(props.placemarks).map(([key, option]) => {
    return renderPlacemark(key, option);
  });
  if (newMarks.length) objectManager.add(newMarks);

  objectManager.objects.events.add(eventsObjectManager, (e) => {
    const mark = objectManager.objects.getById(e.get("objectId"));
    // console.log(e.get("type"));
    // Вызов событий
    emit(e.get("type"), mark);
  });
});

onUnmounted(() => {
  unwatchArr.forEach((unwatch) => unwatch());
  objectManager.removeAll();
  // Мб надо сам менеджер удалять с карты, но пока так)
  // myMap.value.geoObjects.remove(objectManager);
});
</script>
