<i18n>
{
	"ru": {
		"showGoldPartnerLabelAbility": "Золотой партнер",
		"showSilverPartnerLabelAbility": "Серебряный партнер",
    "superhost": "Суперхозяин",
    "favoriteAdd": "Добавить в избранное",
    "favoriteRemove": "Убрать из избранного",
    "meter": "м<sup><small>2</small></sup>",
    "recommended": "Гости рекомендуют",
    "youRockets": "У вашего объекта нет Ракет: | У вашего объекта {n} Ракета: | У вашего объекта {n} Ракеты: | У вашего объекта {n} Ракет:",
    "giveRockets": "подарочные: {rockets}",
    "payRockets": "подключенные вами: {rockets}",
    "moreRockets": "Подробнее о Ракетах",
    "bonuses": "{n}&nbsp;бонусов | {n}&nbsp;бонус | {n}&nbsp;бонуса | {n}&nbsp;бонусов",
    "cashbackInfo": "При бронировании до&nbsp;1&nbsp;марта&nbsp;2023. Вернём до 20&nbsp;000 бонусных рублей после оценки проживания!",
    "cashbackInfoPrice": "Забронируйте до&nbsp;1&nbsp;марта&nbsp;2023 и получите {n} после оценки проживания!",
    "moreDetails": "Подробнее",
    "cashback": "Кэшбэк",
    "hint": {
      "rockets": {
        "on": "Продвижение подключено",
        "off": "Продвижение не подключено",
        "link": "Включить"
      }
    }
	},
	"en": {
		"showGoldPartnerLabelAbility": "Gold partner",
		"showSilverPartnerLabelAbility": "Silver partner",
    "superhost": "Superhost",
    "favoriteAdd": "Add to favorites",
    "favoriteRemove": "Remove from favorites",
    "meter": "m<sup><small>2</small></sup>",
    "recommended": "Guest's choice",
    "bonuses": "0|{n}&nbsp;bonus|{n}&nbsp;bonuses|{n}&nbsp;bonuses",
    "cashbackInfo": "When booking before&nbsp;March&nbsp;1. We will refund up to 20,000 bonus rubles after evaluating the accommodation!",
    "cashbackInfoPrice": "Book before&nbsp;1&nbsp;March&nbsp;2023 and get {n} after your stay!",
    "moreDetails": "More details",
    "cashback": "Cashback"
	}
}
</i18n>

<template>
  <div class="carousel">
    <a
      target="_blank"
      href="#"
      @mousedown="clickObjectBronevik($event, linkBronevik)"
    ></a>
    <div class="object-info">
      <div
        v-if="
          data.owner.permissions.includes('showSuperhostLabelAbility') &&
          !hideOwnerPopularity
        "
        class="object-info__superhost object-info__item"
      >
        {{ $t("superhost") }}
      </div>
    </div>

    <SliderLight
      :media="data.media || []"
      :arrows="true"
      :dots="true"
      class="slider-light"
    />
    <div class="carousel__owner-options">
      <Rating
        class="carousel__rating"
        :data-rating="data.rating"
        :count-reviews="data.count_reviews"
        :view-count="true"
      />
    </div>

    <div
      v-if="isPosition && choiceObject"
      class="carousel__rockets"
      @click.prevent.stop=""
    >
      <div
        v-on:mouseover="showHint = 2"
        v-on:mouseleave="hintPopperLeave"
        v-show="showHint"
        class="rocket-hint"
        :class="{
          'hint-bottom': choiceObject.pos === 1,
        }"
      >
        <template v-if="choiceObject.pr">
          {{ $t("hint.rockets.on") }}
        </template>
        <template v-else>
          {{ $t("hint.rockets.off") }}
          <a class="link" @click.prevent.stop="setIsModalConnection(true)">{{
            $t("hint.rockets.link")
          }}</a>
        </template>
      </div>
      <div
        class="rocket"
        v-on:mouseover="showHint = 1"
        v-on:mouseleave="hintLeave"
      >
        <span
          class="icon-app-rocket-right"
          :class="{ active: choiceObject.pr }"
        />
      </div>
    </div>
    <div class="carousel__size">
      <span>{{ data.area }}</span
      >&nbsp;<span v-html="$t('meter')" />
    </div>
  </div>
</template>

<script>
import SliderLight from "@sutochno/sliderlight";
import BaseFavorite from "@/components/base/BaseFavorite.vue";
import Rating from "@/components/base/BaseRating";
import { mapState, mapGetters, mapMutations } from "vuex";

export default {
  components: {
    SliderLight,
    BaseFavorite,
    Rating,
  },
  props: {
    data: {
      type: Object,
    },
    hideOwnerPopularity: {
      type: Boolean,
      default: false,
    },
    flag: {
      default: null,
    },
    i: {
      type: Number,
      required: false,
    },
    choiceObject: {
      type: Object,
      default: false,
    },
    currentPosition: {
      type: Object,
      default: null,
    },
    linkBronevik: {
      type: String,
      default: "",
    },
    isDisabledLink: {
      type: Boolean,
      default: false,
    },
    isBronevik: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      page: 1,
      isFavorite: this.data.favorite,
      isVisible: false,
      btnCashbackHover: false,
      isCashback: false,
      showHint: 0,
    };
  },
  mounted() {
    // if(this.data?.is_recommended) {
    //   this.objectFlagMutate({
    //     id: Number(this.data.id),
    //     index: 0,
    //     symbol: 'g'
    //   })
    // }
    // if(this.data?.owner?.permissions?.includes('showSuperhostLabelAbility')) {
    //   this.objectFlagMutate({
    //     id: Number(this.data.id),
    //     index: 0,
    //     symbol: 's'
    //   })
    // }
  },
  methods: {
    ...mapMutations("search", ["objectFlagMutate"]),
    ...mapMutations("searchPr", ["setIsModalConnection"]),
    onClickPopoverGA() {
      window.open("https://promo.sutochno.ru/bonus-za-prozhivanie/", "_blank");
    },
    formatedCurrensies(price) {
      return (
        new Intl.NumberFormat(this.$i18n.locale, {
          style: "decimal",
          currency: "RUB",
          minimumFractionDigits: 0,
          useGrouping: true,
        })
          .format(price)
          .replace(" ", "&#8239;") + "&#8239;₽"
      );
    },
    cashbackGA(e) {
      //const el = document.querySelector();
      this.btnCashbackHover = true;
    },
    cashbackHide() {
      this.btnCashbackHover = false;
    },
    setFavorite(payload) {
      this.isFavorite = payload;
    },
    goToPr() {
      window.open("https://sutochno.ru/help/arendodateli/pr", "_blank");
    },
    clickObjectBronevik(event, link) {
      if (link || (link && event.which == 1) || (link && event.which == 2)) {
        event.preventDefault();
        window.open(link);
      }
    },
    hintLeave() {
      setTimeout(() => {
        if (this.showHint === 1) {
          this.showHint = 0;
        }
      }, 300);
    },
    hintPopperLeave() {
      setTimeout(() => {
        if (this.showHint === 2) {
          this.showHint = 0;
        }
      }, 300);
    }
  },
  computed: {
    ...mapState("search", ["guests", "isPosition"]),
    ...mapGetters("search", ["occupied", "getQuery"]),
    isDate() {
      return this.occupied ? true : false;
    },
    cashback() {
      const price = this.data.price.full;
      const cashback = Math.ceil(price * 0.3);
      this.isCashback = cashback >= 1 && cashback <= 20000 ? true : false;
      return cashback;
    },
    message() {
      return this.isFavorite === true
        ? this.$t("favoriteRemove")
        : this.$t("favoriteAdd");
    },
  },
};
</script>

<style lang="scss" scoped>
.slider-light::before {
  background: unset !important;
}
.popover-text {
  .popover-list {
    list-style: unset;
    padding-left: 20px;
    padding-top: 5px;
  }
}
.preview-image {
  display: block;
  width: 100%;
  height: 100%;
  &[data-pages]:before {
    display: block;
    content: attr(data-pages);
    position: absolute;
    left: 50%;
    bottom: 10px;
    z-index: 1;
    display: block;

    background: #00000080;
    border-radius: 10px;
    height: 20px;
    color: white;
    font-size: 13px;
    line-height: 20px;
    text-align: center;
    width: 40px;
    padding: 0 4px;
    transform: translateX(-50%);
  }

  &[data-pages]:before {
    // display: none;

    // @media only screen and (min-width: 1490px) {
    display: block;
    // }
  }
  .shadow {
    display: block;
    content: "";

    z-index: 1;
    pointer-events: none;
    position: absolute;
    left: 0;
    right: 0;

    opacity: 0.35;
    z-index: 0;
    top: 0;
    bottom: 0;

    background: linear-gradient(
      0deg,
      #000000 0%,
      rgba(0, 0, 0, 0.0001) 25%,
      rgba(0, 0, 0, 0.0001) 75%,
      #000000 100%
    );
  }
}
.carousel {
  position: relative;
  height: 100%;
  z-index: 0;

  > a {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 2;
  }
  &:hover {
    ::v-deep .sc-sliderlight__arrows.arrows {
      opacity: 1;
      button {
        z-index: 100;
      }
    }
  }

  .object-info {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin: 0 11px;
    z-index: 99;
    position: absolute;
    top: -8px;
    &__item {
      width: auto;
      height: 18px;
      font-weight: 600;
      padding: 0 9px 0 10px;
      margin-right: 10px;
      border-radius: 9px;
      line-height: 18px;
      text-align: center;
      color: #fff;
      font-size: 9px;
      position: relative;
      cursor: default;
      &:last-child {
        margin-right: 0;
      }
    }
    &__cashback {
      background-color: #f51449;
      &-shadow {
        font-size: 9px;
        color: #fff;
        span {
          line-height: 18px;
        }
      }
    }
    &__superhost {
      background-color: #ffa900;
    }
  }

  img {
    height: 100%;
    width: 100%;
    max-width: 100%;
    object-fit: cover;
  }
  :deep(.VueCarousel) {
    // height: 100%;
    .VueCarousel-slide {
      height: inherit;
    }
    .VueCarousel-inner {
      max-height: 100%;
      width: 100%;
      max-width: 100%;
      object-fit: cover;
    }
  }

  &:hover
    :deep(.VueCarousel-navigation-button.VueCarousel-navigation-next::before) {
    right: 0;
    margin: 0 0 0 auto;
    background-position: -44px 0;
  }
  &:hover :deep(.VueCarousel-navigation-button::before) {
    display: block;
    content: "";
    background-image: url("~assets/img/spr_ss_arrows.png");
    width: 44px;
    height: 44px;
  }
  & :deep(.VueCarousel-navigation-button) {
    outline: none !important;
    top: 0;
    bottom: 0;
    margin: 0;
    width: 50%;
    transform: translateY(0) translateX(0%);
  }

  .partner-label {
    position: absolute;
    right: 10px;
    bottom: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 7px;
    line-height: 13px;
    text-transform: uppercase;
    z-index: 10;

    &.showSilverPartnerLabelAbility {
      color: #fff;
    }
    &.showGoldPartnerLabelAbility {
      color: gold;
    }

    &__text {
      // display: none;
      // @media only screen and (min-width: 1280px) {
      display: block;
      // }
    }

    // @media only screen and (min-width: 1280px) {
    //   display: none;
    // }
    // @media only screen and (min-width: 1490px) {
    //   display: flex;
    // }
  }

  .pr {
    position: absolute;
    bottom: 10px;
    right: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 2;

    font-weight: 400;
    color: #000;
    text-align: center;
    padding: 8px 8px 7px;
    font-size: 14px;
    line-height: 22px;
    border-radius: 16px;
    background-color: rgba(255, 255, 255, 0.7);

    .pr-icon {
      width: 16px;
      height: 16px;
      margin-left: 10px;
      &::before {
        content: "";
        display: block;
        width: 100%;
        height: 100%;
        background-image: url(~assets/img/icon/shuttle-1.svg);
        background-position: center;
        background-size: contain;
        background-repeat: no-repeat;
      }
    }
  }

  &__owner-options {
    display: flex;

    position: absolute;
    top: 10px;
    left: 10px;
    z-index: 2;
  }

  &__size {
    display: none;
  }

  &__rating {
    ::v-deep span {
      color: #fff;
    }

    display: none;
  }

  &__favorite {
    margin-left: 0;
    margin-top: 0;
    position: absolute;
    right: 10px;
    top: 15px;
  }
  &__rockets {
    position: absolute;
    right: 15px;
    top: 20px;
    z-index: 101;
    // pointer-events: none;
    font-weight: bold;

    .rocket {
      border-radius: 16px;
      height: 32px;
      padding: 0 10px;
      background: #fff;
      display: flex;
      align-items: center;
      justify-content: center;
      color: #000000;
      font-size: 14px;

      &__formular {
        display: flex;
        align-items: center;

        .icon-app-rocket-right {
          color: #999;
          &.active {
            color: #f51449;
          }
        }

        .icon-rocket {
          &-wrap {
            margin-right: 5px;
            border-radius: 100%;
            background-color: #999999;

            .icon-app-rocket-right {
              margin: auto;
              padding: 4px;
              display: block;
              font-size: 8px;
              color: #ffffff;
            }
          }
        }
      }

      &__plus {
        display: block;
        height: 8px;
        width: 8px;
        margin: 0 5px;
      }

      &__points {
        height: 16px;
        display: inline-block;
        margin-right: 5px;
      }

      .icon-app-rocket-right {
        font-size: 16px;
        color: #999;
        &.active {
          color: #f51449;
        }
      }

      &-current {
        display: flex;
        .rocket {
          margin-left: 10px;
          &:first-child {
            margin-left: 0;
          }
        }
      }

      &-hint {
        position: absolute;
        z-index: 11;
        top: -12px;
        left: 50%;
        font-size: 14px;
        font-weight: 400;
        line-height: 18px;
        padding: 20px;
        background-color: #444;
        color: #fff;
        transform: translate(-50%, -100%);
        // white-space: nowrap;
        max-width: 270px;
        width: 270px;
        width: max-content;
        // width: revert;
        text-align: left;

        &:after {
          display: block;
          content: "";
          backface-visibility: hidden;
          transform: translateZ(0) scale(1, 1) translate(-50%, -100%);
          position: absolute;
          bottom: -21px;
          left: 50%;
          width: 0;
          height: 0;
          z-index: 1;
          border-style: solid;
          border-width: 11px 11px 0 11px;
          border-color: #444 transparent transparent transparent;
        }

        &.hint-bottom {
          transform: translate(-50%, 100%);
          bottom: -13px;
          top: initial;

          &:after {
            top: -21px;
            transform: translate(-50%, 100%) rotate(180deg);
          }
        }

        .link {
          cursor: pointer;
        }
      }
    }
  }

  :deep(.hooper-pagination) {
    display: block;
  }
}
a {
  text-decoration: none;
  &:hover {
    text-decoration: none;
  }
}
</style>
