<template>
  <SliderRange
      v-model="filters.price"
      :price="priceRange"
      :flow-price="flowPrice"
      :view-port="'desktop'"
      :currency="{
        id: 1,
        title: 'RUB'
      }"
  />
</template>

<script>
import SliderRange from "@/components/Search/Filter/SliderRange";
import { mapState } from "vuex";

export default {
  name: "FilterPrice",
  components: {
    SliderRange
  },
  props: {
    modelValue: {
      type: Object,
      default: {}
    },
    checkboxName: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      filters: {
        price: [null, null],
        price_per: null
      },
      priceRange: [
        0, 500, 1000, 1500, 2000, 2500, 3000, 4000, 5000, 6000, 7000, 8000,
        10000, 15000, 20000,
      ],
      price_per: false,
    };
  },
  watch: {
    filters: {
      handler() {
        this.$emit("switching", this.filters);
      },
      deep: true
    },
    modelValue: {
      handler() {
        if(!this.modelValue.price) {
          this.filters.price = [null, null];
        } else if(JSON.stringify(this.filters.price) != JSON.stringify(this.modelValue.price)) {
          this.filters.price = this.modelValue.price;
        }

        if(!this.modelValue.price_per) {
          this.filters.price_per = null;
        } else if(this.modelValue.price_per && this.price_per != this.modelValue.price_per) {
          this.price_per = this.modelValue.price_per;
          if(this.price_per == 2) {
            this.filters.price_per = true;
            return;
          } else if (this.price_per == 1 || this.price_per === undefined) {
            this.filters.price_per = false;
            return;
          }
          this.filters.price_per = null;
        }
      },
      deep: true
    },
  },
  methods: {
    clearFilters() {
      this.filters.price_per = null;
      this.filters.price = [null, null];
    }
  },
  computed: {
    ...mapState("search", ["flowPrice"]),
    ...mapState("search", ["checkIn", "checkOut"]),
    moreThenOneDay() {
      return this.sumOfDays > 1;
    },
    sumOfDays() {
      return Math.round(Math.abs(this.checkIn - this.checkOut) / 86400000);
    },
    moreThenOneDay() {
      return this.sumOfDays > 1;
    },
  },
}
</script>

