<template>
  <div class="guests-counter">
    <div
      class="guests-counter__decrement"
      :class="{ 'guests-counter__decrement--disabled': modelValue === min }"
      @click="decrement"
    >
      <span class="minus" :class="{ disabled: modelValue === min }"></span>
    </div>
    <div class="guests-counter__text">
      {{ text }}
    </div>
    <div
      class="guests-counter__increment"
      :class="{ 'guests-counter__increment--disabled': modelValue === max }"
      @click="increment"
    >
      <span class="plus" :class="{ disabled: modelValue === max }"></span>
    </div>
    <Alert :modelValue="isAlert" :text="alertText" />
  </div>
</template>
<script>
import Alert from "./Alert";
export default {
  name: "GuestsCounter",
  components: {
    Alert,
  },
  props: {
    modelValue: {
      type: Number,
      default: 0,
    },
    text: {
      type: String,
      default: "",
    },
    min: {
      type: Number,
      default: 1,
    },
    max: {
      type: Number,
      default: 100,
    },
    isAlert: {
      type: Boolean,
      default: false,
    },
    alertText: {
      type: String,
      default: "",
    },
  },
  methods: {
    increment() {
      if (this.modelValue < this.max) {
        let val = JSON.parse(JSON.stringify(this.modelValue));
        this.$emit("update:modelValue", val + 1);
      }
    },
    decrement() {
      if (this.modelValue > this.min) {
        let val = JSON.parse(JSON.stringify(this.modelValue));
        this.$emit("update:modelValue", val - 1);
      }
    },
  },
};
</script>
<style lang="scss" scoped>
$border: solid 1px #d8d8d8;
$signsSize: 12px;

.guests-counter {
  position: relative;
  display: grid;
  grid-template-columns: 36px 1fr 36px;
  height: 42px;
  align-items: center;
  border-radius: 3px;
  text-align: center;
  cursor: pointer;
  &__decrement,
  &__increment {
    display: grid;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 42px;
    line-height: 1;
    font-size: 28px;
    background-color: #f1f0ed;
    border-radius: 50%;
    transition: background-color 0.2s;

    &:hover {
      background-color: #ececec;
    }
  }

  &__decrement--disabled,
  &__increment--disabled {
    opacity: 0.5;

    &:hover {
      background-color: #f1f0ed;
    }
  }

  .plus,
  .minus {
    position: relative;
    height: $signsSize;
    width: $signsSize;
    transition: 0.3s;
  }

  .plus {
    &::before {
      content: "+";
      position: absolute;
      top: -2px;
      left: -2px;
      font-size: 26px;
      line-height: $signsSize;
    }
  }
  .minus {
    &::before {
      content: "-";
      position: absolute;
      top: -1px;
      left: -2px;
      font-size: 37px;
      line-height: $signsSize;
    }
  }
}
</style>
