<i18n>
{
	"ru": {
    "title":"Добавить ребёнка до 18 лет",
    "titleOpen":"Выберите возраст ребёнка",
    "titleSelected":"Ребёнок, ",
    "age": "до года | {n} год | {n} года | {n} лет"
	},
	"en": {
    "title":"Add a child under 18",
    "titleOpen":"Choose your child's age",
    "titleSelected":"Child, ",
    "age": "up to a year | {n} year | {n} years | {n} years"
	}
}
</i18n>
<template>
  <div
    v-click-outside="clickOutside"
    class="children-select"
    :class="{ active: isOpen, selected: selectedItem }"
  >
    <div class="children-select__wrap">
      <p
        class="children-select__title"
        :class="{ 'tmp-font--medium_gray': isOpen }"
        @click="isOpen = !isOpen"
      >
        {{ title }}
      </p>
      <span
        v-if="selectedItem"
        class="icon-app-cross"
        @click.stop.prevent="deleteItem"
      ></span>
      <Alert :modelValue="isAlert" :text="alertText" />
    </div>
    <transition name="slide">
      <div
        v-show="isOpen"
        class="children-select__list"
        :class="{ static: isStatic }"
      >
        <div
          v-for="item in children"
          :key="item.value"
          class="children-select__list-item"
          :class="{
            selected: selectedItem && item.value === selectedItem.value,
          }"
          @click="selectItem(item)"
        >
          {{ item.text }}
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import _ from "lodash";
import Alert from "./Alert";
export default {
  name: "ChildrenSelect",
  components: {
    Alert,
  },
  props: {
    modelValue: {
      type: Number,
      default: null,
    },
    isAlert: {
      type: Boolean,
      default: false,
    },
    alertText: {
      type: String,
      default: "",
    },
    addChildText: {
      type: String,
      default: null,
    },
    isStatic: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      isOpen: false,
      children: _.range(0, 18).map((c, i) => {
        return {
          text: this.$tc("age", c),
          value: i,
        };
      }),
    };
  },
  computed: {
    title() {
      return this.selectedItem
        ? this.$t("titleSelected") + this.selectedItem.text
        : this.isOpen
        ? this.$t("titleOpen")
        : this.addChildText
        ? this.addChildText
        : this.$t("title");
    },
    selectedItem() {
      return this.modelValue >= 0 ? this.children[this.modelValue] : null;
    },
  },
  watch: {
    isOpen(val) {
      this.$emit("isOpen", val);
    },
  },
  methods: {
    selectItem(item) {
      this.isOpen = false;
      this.$emit("update:modelValue", item.value);
    },
    deleteItem() {
      this.$emit("delete");
    },
    clickOutside() {
      this.isOpen = false;
    },
  },
};
</script>

<style lang="scss" scoped>
$borderColor: #d8d8d8;
$bgColor: #ffffff;
$bgColorSelected: #f6f6f6;

$border: solid 1px $borderColor;
$height: 42px;
$closeIconSize: 12px;

$widthAlert: 177px;

.desktop {
  $height: 34px;

  .children-select {
    .icon-app-cross {
      font-size: 10px;
    }

    &__wrap {
      height: $height;
    }

    &__list {
      margin-top: -1px;
      height: $height * 5;

      &-item {
        height: $height;
      }
    }
  }

  .slide-enter-active,
  .slide-leave-active {
    height: $height * 5;
    transition: height 0.1s ease-in;
  }
  .slide-enter,
  .slide-leave-to {
    height: 0;
  }
}

.children-select {
  position: relative;
  display: grid;
  grid-template-columns: 1fr;
  text-align: center;
  cursor: pointer;
  z-index: 1;

  &__wrap {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    border: $border;
    border-radius: 3px;
    height: $height;
    background-color: $bgColor;
    transition: background-color 0.2s;
    min-height: 42px;

    &:hover {
      background-color: #f6f6f6;
    }
  }

  &.active {
    z-index: 2;
  }

  &.selected {
    .children-select {
      &__wrap {
        background-color: $bgColorSelected;
        border: solid 1px #d8d8d8;
        transition: 0.2s;
        min-height: 42px;

        &:hover {
          background-color: #fff;
          border: solid 1px #000;
        }
      }
    }
  }

  .icon-app-cross {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    padding-right: 13px;
    padding-left: 10px;
    width: 35px;
    font-weight: 700;
    color: #717171;
    font-size: $closeIconSize;
    line-height: $closeIconSize;
  }

  &__title {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0;
    height: 100%;
    width: 100%;
  }

  &__list {
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    margin-top: 5px;
    border: $border;
    height: $height * 5;
    background-color: $bgColor;
    overflow: auto;

    &-item {
      display: grid;
      align-items: center;
      justify-content: center;
      height: $height - 2px;

      &.selected {
        background-color: $bgColorSelected;
      }
    }

    &.static {
      position: static;
    }
  }
}
.slide-enter-active,
.slide-leave-active {
  height: $height * 5;
  transition: height 0.2s ease-in;
}
.slide-enter,
.slide-leave-to {
  height: 0;
}
::-webkit-scrollbar {
  width: 10px;
  background: transparent;
}
::-webkit-scrollbar-track {
  // -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
  // border-radius: 10px;
}
::-webkit-scrollbar-thumb {
  width: 4px;
  background: transparent;
  // border-radius: 10px;

  border-radius: 3px;
  box-shadow: inset 0 0 0 2px #d8d8d8;
  border: 3px solid transparent;

  &:hover {
    box-shadow: inset 0 0 0 2px #999999;
  }
}
</style>
