<template>
  <a
    ref="link"
    class="card-content"
    :target="targetOpt"
    :href="{
        name: 'Detail',
        params: { id: data.id },
        query: { ...getQuery.query },
      }"
    @click="setSelectedSlide(selectedSlide)"
    @mousedown="mousedownComponent"
  >
    <div v-if="data.hotel" class="card-content__object-hotel">
      <span v-if="data.hotel.type" class="object-hotel__type">{{
        data.hotel.type
      }}</span>
      <span>«{{ data.hotel.title }}»</span>
    </div>
    <div v-else class="card-content__object-hotel">
      <span v-if="data.title && data.name" class="object-hotel__type">{{
        data.title
      }}</span>
    </div>
    <h2 class="card-content__object-type tmp-font--average_bold">
      {{ data.name ? data.name : data.title }}
    </h2>

    <div
      v-if="
        data.occupied !== 'busy' &&
        data.location &&
        ((data.location.ropeway && data.location.ropeway.distance != null) ||
          (data.location.sea && data.location.sea.distance != null) ||
          (data.metro && data.metro[0]) ||
          data.location.place)
      "
      class="text-fog"
      style="overflow: hidden; position: relative"
    >
      <!-- подъемники -->
      <div
        v-if="data.location.ropeway && data.location.ropeway.distance != null"
      >
        <a
          :target="targetOpt"
          class="card-content__property-list-elevator"
          :href="{
            name: 'Detail',
            params: { id: data.id },
            query: {
              ...getQuery.query,
              showMap: true,
            },
          }"
        >
          <span class="img-elevator"></span>
          <span>
            {{
              $t("distance_elevator", {
                distance: disatanceDozen(data.location.ropeway.distance),
                objectName: data.location.ropeway.title,
              })
            }}
          </span>
        </a>
      </div>
      <!-- подъемники -->

      <!-- море -->
      <div
        v-else-if="data.location.sea && data.location.sea.distance != null"
        v-tooltip="{
          content: $t('hint_sea'),
          popperClass: 'v-popper__popper--max-width-300',
        }"
        class="object-location"
      >
        <a
          :target="targetOpt"
          :href="{
          name: 'Detail',
          params: { id: data.id },
          query: {
            ...getQuery.query,
            showMap: true,
          },
        }"
          @click="setSelectedSlide(selectedSlide)"
          @mousedown="clickObjectBronevik($event, linkBronevik)"
        >
          <span class="icon-app-sea"></span>
          <span class="info-sea">{{
            $t("distance_to_sea", { n: disatanceDozen(data.location.sea.distance) })
          }}</span>
        </a>
      </div>
      <!-- море -->

      <!-- метро -->
      <div
        v-else-if="data.location && data.location.metro"
        v-tooltip="{ content: hintMetro, popperClass: 'v-popper__popper--max-width-300' }"
        class="object-location"
      >
        <a
          :target="targetOpt"
          :href="{
            name: 'Detail',
            params: { id: data.id },
            query: {
              ...getQuery.query,
              showMap: true,
              isShowMetro: 1,
            },
          }"
          @click="setSelectedSlide(selectedSlide)"
          @mousedown="clickObjectBronevik($event, linkBronevik)"
        >
          <span
            class="icon-app-metro-circle"
            :style="{ color: data.location.metro.line_color || '#ADACAC' }"
          />
          <span>{{
            $t("metro_description", {
              metroName: data.location.metro.title,
              distance: meterToMinWalking(data.location.metro.distance),
            })
          }}</span>
        </a>
      </div>
      <!-- метро -->

      <!-- район -->
      <div
        v-else-if="data.location.place"
        v-tooltip="{ content: hintDistrict, popperClass: 'v-popper__popper--max-width-300' }"
        class="object-location"
      >
        <a
          class="object-place"
          :target="targetOpt"
          :href="{
            name: 'Detail',
            params: { id: data.id },
            query: {
              ...getQuery.query,
              showMap: true,
            },
          }"
          @click="setSelectedSlide(selectedSlide)"
          @mousedown="clickObjectBronevik($event, linkBronevik)"
          >{{ data.location.place.title }} {{ $t("place") }}</a
        >
      </div>
      <!-- район -->
    </div>

    <!-- адресс -->
    <div v-if="data.address" class="card-content__address">
      <a
        class="address__link"
        :target="targetOpt"
        :href="{
          name: 'Detail',
          params: { id: data.id },
          query: {
            ...getQuery.query,
            showMap: true,
          },
        }"
        @click="setSelectedSlide(selectedSlide)"
        @mousedown="clickObjectBronevik($event, linkBronevik)"
      >
        <p
          v-tooltip="{
            content: $t('showMapAddress', { address: data.address }),
            popperClass: 'v-popper__popper--max-width-300',
            html: true,
          }"
          class="address__text"
        >
          {{ data.address }}
        </p>
      </a>
    </div>
    <template v-if="!data.occupied !== 'busy'">
      <transition name="fade" mode="out-in">
        <div
          v-if="meals && (meals.included || meals.unincluded)"
          class="card-content__meals"
        >
          <div v-if="meals.unincluded" class="meals__item">
            <span class="meals__item-icon"></span>
            <span class="meals__item-text">
              {{ meals.unincluded }}
            </span>
          </div>
          <div
            v-if="meals.included"
            class="meals__item meals__item-text meals__included"
          >
            {{ meals.included }}
          </div>
        </div>
      </transition>
      <transition name="fade" mode="out-in">
        <!-- гости -->
        <div
          v-if="hideOwnerPopularity"
          key="facilities"
          class="card-content__facilities"
        >
          <div class="facilities__main">
            <p v-if="data.guests" class="facilities__facility tmp-font--small">
              {{ $tc("guests", data.guests) }}
            </p>
            <p
              v-if="data.count_beds"
              class="facilities__facility tmp-font--small"
            >
              {{ $tc("beds", data.count_beds) }}
            </p>
            <p
              v-if="data.bedrooms"
              class="facilities__facility tmp-font--small"
            >
              {{ $tc("bedrooms", data.bedrooms) }}
            </p>
          </div>
          <div v-if="data.area" class="facilities__size">
            <span> {{ data.area }} {{ $t("meter") }} </span>
          </div>
        </div>
        <!-- статусы -->
        <div v-else key="popularity" class="card-content__popularity">
          <div class="popularity__item">
            <div v-if="profitable" class="item-wrapp profitable">
              <span class="popularity__icon"></span>
              <span class="profitable">{{ $t("profitable") }}</span>
            </div>
          </div>
        </div>
      </transition>
    </template>
    <template v-if="data.occupied === 'busy'">
      <div class="card-content__property">
        <div class="info-block">
          <div class="info-block-ico">
            <div class="icon-app-clock"></div>
          </div>
          <div>
            {{ $t("booked_for_your_dates") }}
            <br />
            {{ $t("housing_is_in_demand") }}
          </div>
        </div>
      </div>
    </template>
  </a>
</template>

<script>
import { mapState, mapGetters, mapActions } from "vuex";
import BaseWarningBronevik from "@/components/base/BaseWarningBronevik";

export default {
  name: "ObjectBlockContent",
  components: {
    BaseWarningBronevik,
  },
  props: {
    data: {
      type: Object,
    },
    showDetail: {
      required: true,
    },
    selectedSlide: {
      type: Number,
      default: 0,
    },
    linkBronevik: {
      type: String,
      default: "",
    },
    isDisabledLink: {
      type: Boolean,
      default: false,
    },
    rating: {
      type: Object,
    },
  },
  emits: ["mousedown"],
  data() {
    return {
      hideOwnerPopularity: true,
      timeInterval: 3400,
      interval: null,
    };
  },
  computed: {
    ...mapState("search", ["guests", "checkIn"]),
    ...mapGetters("search", ["occupied", "getQuery"]),
    meals() {
      const ucFirst = (str) => {
        return str[0].toUpperCase() + str.slice(1);
      };

      if(this.data.meal?.is_all_inclusive){
        return {
          included: ucFirst(this.$t("allInclusive")),
          unincluded: null
        }
      }
      
      // питание
      if (!this.data?.meal?.meal_types) return null;

      let included = [];
      // let unincluded = [];
      this.data.meal.meal_types.forEach((v) => {
        if (v.kind === "nofood" || v.kind === "") return false;
        if (v.price === 0) {
          included.push(this.$t(`meals.${v.name}`));
        }/*  else if (v.price === 1) {
          unincluded.push(this.$t(`meals.${v.name}`));
        } */
      });

      // return null;

      return {
        included: included.length
          ? ucFirst(this.$tc("meals.included", included.length - 1, included))
          : null,
        unincluded: null,
        /* unincluded.length
          ? ucFirst(
              this.$tc("meals.unincluded", unincluded.length - 1, unincluded)
            )
          : null
        */
      };
    },
    profitable() {
      return this.rating?.ratings?.price_quality > 4;
    },
    freeCancellation() {
      if (
        this.data.hot_sale &&
        Number(this.data.hot_sale.price_per_night) > 0
      ) {
        return false;
      }
      if (this.data.conveniences.fc) {
        return true;
      } else {
        return false;
      }
    },
    conveniencesList() {
      if (!this.data.conveniences) return [];
      return Object.entries(this.data.conveniences)
        .filter(([key, value]) => value && key)
        .map(([key]) => key);
    },
    place() {
      if (this.data.sea_distance && this.data.sea_distance.distance != null) {
        const dist =
          this.data.sea_distance.distance < 1000
            ? `${this.data.sea_distance.distance} м`
            : `${(this.data.sea_distance.distance / 1000).toFixed(1)} км`;
        return `${dist} ${this.data.sea_distance.sea}`;
      }
      return this.data.location;
    },
    textPlace() {
      if (this.data.sea_distance) {
        return this.$t("hint_sea");
      }
      return "none";
    },
    hintMetro() {
      return this.$t("hint_metro");
    },
    hintDistrict() {
      return this.$t("hint_district");
    },
    hintRoomInfo() {
      const bedrooms = this.data.bedrooms
        ? `${this.$tc("bedroomsBeutifull", this.data.bedrooms)}, `
        : "";
      const beds = this.data.beds
        ? `${this.$tc("beds", this.data.beds)}, `
        : "";
      const person = this.data.guests
        ? `${this.$t("posible_living")} ${this.$tc("person", this.data.guests)}`
        : "";
      return (
        `${this.$t("in_room")} ` +
        bedrooms +
        beds +
        person +
        `, ${this.$t("includeChild")}`
      );
    },
    tooltipClasses() {
      return "max";
    },
    targetOpt() {
      return "_blank";
    },
  },
  mounted() {
    if (this.profitable || this.breakfast) {
      this.switchingTime();
      document.addEventListener("scroll", () => {
        clearInterval(this.interval);
        this.hideOwnerPopularity = true;
        this.switchingTime();
      });
    }
  },
  methods: {
    ...mapActions("detail", ["setSelectedSlide"]),
    showCalendarBronevik() {
      if (!this.occupied) {
        setTimeout(() => {
          document.querySelector(".open-calendar").click();
        }, 150);
      }
    },
    switchingTime() {
      this.interval = setInterval(() => {
        this.hideOwnerPopularity = !this.hideOwnerPopularity;
      }, this.timeInterval);
    },
    linkTo() {
      this.$parent.showDetail = !this.showDetail;
      document.body.style.overflow = "hidden";
    },
    getFormatOccupied(date) {
      let format = (date) => {
        let month = (date.getMonth() + 1).toString().padStart(2, 0),
          day = date.getDate().toString().padStart(2, 0),
          year = date.getFullYear();
        return year + "-" + month + "-" + day;
      };
      return date.in && date.out
        ? format(date.in) + ";" + format(date.out)
        : null;
    },
    meterToMinWalking(distance) {
      const SPEED_WALKING = 0.012; // величина времени пройденый за 1 метр
      const time = Math.round(distance * SPEED_WALKING);
      return time === 0 ? 1 : time;
    },
    disatanceDozen(distance) {
      if (distance < 50) {
        return this.$t("distance_meters", {
          distance: "< 50",
        });
      } else if (distance < 991) {
        return this.$t("distance_meters", {
          distance: (Math.ceil(distance / 10) * 10).toLocaleString(),
        });
      } else {
        return this.$t("distance_kilometers", {
          distance: (
            (Math.round(distance / 100) * 100) /
            1000
          ).toLocaleString(),
        });
      }
    },
    clickObjectBronevik(event, link) {
      if (link) {
        event.preventDefault();
        this.$gtag.event("click", {
          event_category: "search_sa",
          event_label: "object_b_link",
          value: this.data.location.city.title
        });
        window.open(link);
      }
    },
    mousedownComponent($event) {
      this.$emit("mousedown", $event);
      this.clickObjectBronevik($event, this.linkBronevik);
    },
  },
};
</script>

<style lang="scss" scoped>
.card-content {
  position: relative;
  display: flex;
  flex-direction: column;

  padding: 15px 15px 22px 15px;

  background: #fff;
  text-decoration: none !important;

  &__facilities {
    display: flex;
    // align-items: center;
    margin-top: 10px;

    .facilities {
      &__main {
        display: flex;
      }
      &__facility {
        margin-right: 10px;
        margin-top: -2px;
        font-size: 14px;
        color: #717171;
      }
    }
  }

  a {
    text-decoration: none !important;
    color: inherit;
  }
  .btn-order {
    float: right;
  }

  .text-fog {
    // margin-top: 10px;
  }

  .object-location {
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 18px;
    color: #000;
    display: flex;
    align-items: center;
    width: max-content;
    margin-bottom: 5px;

    .icon-app-metro-circle {
      font-size: 13px;
      margin-right: 10px;
    }
  }

  .icon-app-sea {
    font-size: 13px;
    color: #000;
    margin-right: 10px;
  }
  .info-sea {
    color: #000;
  }

  &__object-options {
    display: flex;
    flex-wrap: wrap;
  }

  .superhost {
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    line-height: 18px;
    font-size: 10px;
    text-transform: uppercase;
    border-radius: 2px;
    padding: 0 6px;
    height: 18px;
    margin-bottom: 10px;
    margin-right: 10px;
  }

  .free-cancellation {
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    line-height: 18px;
    font-size: 10px;
    text-transform: uppercase;
    border-radius: 2px;
    padding: 0 6px;
    height: 18px;
    margin-bottom: 10px;
    margin-right: 10px;
    background: #437407;
    color: #ffffff;
  }

  .facilities__size {
    border-radius: 0px;
    font-size: 14px;
    white-space: nowrap;
    line-height: 18px;
    border-radius: 2px;
    padding: 0 6px;
    height: 18px;
    color: #717171;
  }

  .superhost {
    border: none;
    background-color: #f1c260;
    color: #000;
  }

  &__object-hotel {
    color: #7f7f7f;
    font-size: 14px;
    line-height: 16px;
    margin-bottom: 5px;

    .object-hotel__type {
      margin-right: 3px;
    }
  }
  &__object-type {
    overflow: hidden;
    color: #000;
    padding: 0;
    margin: 0;
    margin-bottom: 15px;
    position: relative;
    max-height: 46px;
    text-overflow: ellipsis;
    // display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
  }
  .object-place {
    font-size: 14px;
    // line-height: 1;
    // color: #999;
    // margin-bottom: 2px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    display: inline-block;
  }

  .object-price {
    display: inline-block;
    vertical-align: middle;
    font-size: 25px;
  }
  .object-unit {
    display: inline-block;
    vertical-align: middle;
    font-size: 13px;
    color: #717171;
  }

  &__address {
    display: block;

    p {
      padding-bottom: 0;
    }

    .address {
      &__text {
        color: #7f7f7f;
        line-height: 14px;
      }
      &__link {
        display: flex;
        height: 15px;
        flex-wrap: nowrap;
        position: relative;
        white-space: nowrap;
        overflow: hidden;
        font-size: 14px;
        color: #444;
        &:hover {
          color: #2d6cb4;
        }

        &:before {
          background: linear-gradient(
            to right,
            rgba(255, 255, 255, 0) 0%,
            #fff 85%,
            #fff 100%
          );
          content: "";
          display: block;
          height: 100%;
          position: absolute;
          right: 0;
          top: 0;
          width: 20px;
          z-index: 0;
        }
      }
      &__icon {
        min-width: 16px;
        margin-right: 9px;
        .icon-app-location {
          position: relative;
          top: 2px;
          font-size: 16px;
        }
      }
    }
  }
  &__popularity {
    height: 18px;
    margin-top: 10px;

    .popularity {
      &__icon {
        display: block;
        margin-right: 5px;
      }
      &__item {
        display: flex;
        align-items: flex-start;
        font-size: 10px;
        .item-wrapp {
          display: flex;
          align-items: center;
          margin-right: 15px;
          &:last-child {
            margin-right: 0;
          }
          .popularity__icon {
            display: block;
            background-size: contain;
            background-position: center;
            background-color: unset;
          }
          &.profitable {
            .popularity__icon {
              width: 13px;
              height: 16px;
              background-image: url(~assets/img/icon/hot-sale.svg);
            }
            .profitable {
              color: #14c405;
            }
          }
        }
      }
    }
  }

  &__address {
    margin-bottom: 5px;
  }

  &__property {
    display: flex;
    width: 100%;
    flex-direction: column;
    justify-content: left;
    align-items: left;
    font-size: 13px;
    color: #444;

    &-size {
      border: 1px solid #d8d8d8;
      box-sizing: border-box;
      border-radius: 2px;
      height: 18px;
      display: inline-block;
      font-size: 12px;
      line-height: 18px;
      padding: 0 6px;
      margin-right: 10px;
    }
    &-list {
      display: none;
      line-height: 1;
      padding: 0;
      margin-top: auto;

      & > span {
        display: inline-block;
        width: 28px;
        height: 28px;
        font-size: 28px;
        color: #444;
        margin-right: 6px;
        vertical-align: top;
        div {
          font-size: 28px;
        }
      }
      .more {
        width: 28px;
        height: 28px;
        line-height: 28px;
        font-size: 14px;
        border: 1px solid #999;
        color: #717171;
        border-radius: 50%;
        box-sizing: border-box;
        text-align: center;
        letter-spacing: -1px;
      }
    }

    &-list-elevator {
      margin-bottom: 5px;
      display: flex;
      align-items: center;
      font-size: 14px;
      line-height: 18px;
      color: #000 !important;

      .img-elevator {
        margin-right: 10px;
        width: 14px;
        height: 14px;
        background-image: url(~assets/img/icon/elevator.svg);
        background-position: center;
      }

      p {
        width: calc(100% - 30px);
        padding: 0;
        margin-top: 0;
        white-space: nowrap;
      }
    }
    .info-block {
      // width: 89%;
      // position: absolute;
      // bottom: 20px;
      // left: 20px;
      display: flex;
      background: #fdd2d2;
      color: #e10600;
      font-size: 13px;
      padding: 10px;
      margin-top: 10px;
      &-ico {
        margin-right: 10px;
        padding-top: 2px;
        .icon-app-clock {
          font-size: 18px;
        }
      }
    }
  }
  .warning-broneevik {
    margin-top: 10px;
  }

  &__meals {
    margin-top: 6px;
    display: flex;

    .meals__item {
      + .meals__item {
        margin-left: 10px;
      }
      display: flex;
      align-items: center;
    }

    .meals__item-icon {
      margin-right: 6px;
      width: 12px;
      height: 16px;
      display: block;
      border-radius: unset;
      background-image: url(~assets/img/icon/cutlery.svg);
      background-position: center;
      background-size: contain;
      background-repeat: no-repeat;
      background-color: unset;
    }
    .meals__item-text {
      font-size: 9px;
      color: #0659ff;
    }
    .meals__included {
      padding: 3px 5px 4px;
      border-radius: 20px;
      border: solid 1px #0659ff;
    }
  }
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.4s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active до версии 2.1.8 */ {
  opacity: 0;
}
a {
  text-decoration: none;
  &:hover {
    text-decoration: none;
  }
}
