<template>
  <a
    ref="link"
    :href="data.id"
    class="card-content"
    :target="targetOpt"
    @mousedown="clickObjectBronevik($event, linkBronevik)"
  >
    <div v-if="data.hotel" class="card-content__object-hotel">
      <span v-if="data.hotel.type">{{ data.hotel.type }}</span>
      «{{ data.hotel.title }}»
    </div>

    <h2 class="card-content__object-type">{{ data.title }}</h2>
    <span
      v-if="data.location"
      class="text-fog"
      style="overflow: hidden; position: relative"
    >
      <!-- подъемники -->
      <div
        v-if="data.location.ropeway && data.location.ropeway.distance != null"
        class="card-content__property-list-elevator"
      >
        <div class="img-elevator"></div>
        <p>
          {{
            $t("distance_elevator", {
              distance: disatanceDozen(data.location.ropeway.distance),
              objectName: data.location.ropeway.title,
            })
          }}
        </p>
      </div>
      <!-- подъемники -->

      <!-- море -->
      <div
        v-else-if="data.location.sea && data.location.sea.distance != null"
        class="object-location"
      >
        <span class="icon-app-sea"></span>
        <span class="info-sea">{{
          $t("distance_to_sea", { n: disatanceDozen(data.location.sea.distance) })
        }}</span>
      </div>
      <!-- море -->

      <!-- метро -->
      <div
        v-else-if="data.location.metro && data.location.metro.distance > 0"
        class="object-location"
      >
        <a
          :href="{
            id: data.id,
            occupied: occupied,
            guests_adults: guests.adults,
            guests_childrens: guests.childrens.join(','),
            id: getQuery.query.id,
            type: getQuery.query.type,
            term: getQuery.query.term,
            showMap: true,
            isShowMetro: 1,
          }"
          :target="targetOpt"
          @mousedown="clickObjectBronevik($event, linkBronevik)"
        >
          <span
            class="icon-app-metro-circle"
            :style="{ color: data.location.metro.line_color || '#ADACAC' }"
          />
          {{
            $t("metro_description", {
              metroName: data.location.metro.title,
              distance: meterToMinWalking(data.location.metro.distance),
            })
          }}
        </a>
      </div>
      <!-- метро -->

      <!-- район -->
      <span v-else-if="data.location.place">
        <a
          :href="{
            id: data.id,
            occupied: occupied,
            guests_adults: guests.adults,
            guests_childrens: guests.childrens.join(','),
            id: getQuery.query.id,
            type: getQuery.query.type,
            term: getQuery.query.term,
            showMap: true,
          }"
          class="object-place"
          :target="targetOpt"
          @mousedown="clickObjectBronevik($event, linkBronevik)"
        >
          {{ data.location.place.title }} {{ $t("place") }}
        </a>
      </span>
      <!-- район -->

      <div class="card-content__facilities">
        <div class="facilities__main">
          <p v-if="data.guests" class="facilities__facility tmp-font--small">
            {{ $tc("guests", data.guests) }}
          </p>
          <p
            v-if="data.count_beds"
            class="facilities__facility tmp-font--small"
          >
            {{ $tc("beds", data.count_beds) }}
          </p>
          <p
            v-if="data.bedrooms"
            class="facilities__facility tmp-font--small"
          >
            {{ $tc("bedrooms", data.bedrooms) }}
          </p>
        </div>
        <div v-if="data.area" class="facilities__size">
          <span> {{ data.area }} {{ $t("meter") }} </span>
        </div>
      </div>
    </span>
    <!-- временно убрал плашки занятости объектов пока не починят запрос цен -->
    <!-- <BaseWarningBronevik v-if="isDisabledLink" /> -->
  </a>
</template>

<script>
import { mapState, mapGetters } from "vuex";
import _ from "lodash";
import BaseWarningBronevik from "@/components/base/BaseWarningBronevik";

export default {
  name: "ObjectBlockContent",
  props: {
    data: {
      type: Object,
    },
    showDetail: {
      required: true,
    },
    linkBronevik: {
      type: String,
      default: "",
    },
    isDisabledLink: {
      type: Boolean,
      default: false,
    },
  },
  components: {
    BaseWarningBronevik,
  },
  computed: {
    ...mapState("search", ["guests"]),
    ...mapGetters("search", ["occupied", "getQuery"]),
    targetOpt() {
      return "_blank";
    },
    conveniencesList() {
      if (!this.data.conveniences) return [];
      return Object.entries(this.data.conveniences)
        .filter(([key, value]) => value && key)
        .map(([key]) => key);
    },
    place() {
      if (this.data.sea_distance && this.data.sea_distance.distance != null) {
        const dist =
          this.data.sea_distance.distance < 1000
            ? `${this.data.sea_distance.distance} м`
            : `${(this.data.sea_distance.distance / 1000).toFixed(1)} км`;
        return `${dist} ${this.data.sea_distance.sea}`;
      }
      return this.data.location;
    },
    textPlace() {
      if (this.data.sea_distance) {
        return this.$t("hint_sea");
      }
      return "none";
    },
    hintMetro() {
      return this.$t("hint_metro");
    },
    hintDistrict() {
      return this.$t("hint_district");
    },
    hintRoomInfo() {
      const bedrooms = this.data.bedrooms
        ? `${this.$tc("bedrooms", this.data.bedrooms)}, `
        : "";
      const beds = this.data.count_beds
        ? `${this.$tc("beds", this.data.count_beds)}, `
        : "";
      const person = this.data.guests
        ? `${this.$t("posible_living")} ${this.$tc(
            "person",
            this.data.guests
          )} `
        : "";
      return `${this.$t("in_room")} ` + bedrooms + beds + person;
    },
  },
  mounted() {},
  methods: {
    linkTo() {
      this.$parent.showDetail = !this.showDetail;
      document.body.style.overflow = "hidden";
    },
    getFormatOccupied(date) {
      let format = (date) => {
        let month = (date.getMonth() + 1).toString().padStart(2, 0),
          day = date.getDate().toString().padStart(2, 0),
          year = date.getFullYear();
        return year + "-" + month + "-" + day;
      };
      return date.in && date.out
        ? format(date.in) + ";" + format(date.out)
        : null;
    },
    meterToMinWalking(distance) {
      const SPEED_WALKING = 0.012; // величина времени пройденый за 1 метр
      return Math.round(distance * SPEED_WALKING);
    },
    disatanceDozen(distance) {
      if (distance < 50) {
        return this.$t("distance_meters", {
          distance: "< 50",
        });
      } else if (distance < 991) {
        return this.$t("distance_meters", {
          distance: (Math.ceil(distance / 10) * 10).toLocaleString(),
        });
      } else {
        return this.$t("distance_kilometers", {
          distance: (
            (Math.round(distance / 100) * 100) /
            1000
          ).toLocaleString(),
        });
      }
    },
    clickObjectBronevik(event, link) {
      if (link || (link && event.which == 1) || (link && event.which == 2)) {
        event.preventDefault();
        window.open(link);
      }
    },
  },
};
</script>

