    flex-direction: column;
    justify-content: left;
    align-items: left;
    font-size: 13px;
    color: #444;
    display: none;
    @media only screen and (min-width: 1490px) {
      display: flex;
    }

    &-size {
      border: 1px solid #d8d8d8;
      box-sizing: border-box;
      border-radius: 2px;
      height: 18px;
      display: inline-block;
      font-size: 12px;
      line-height: 18px;
      padding: 0 6px;
    }
    &-list {
      line-height: 1;
      padding: 0;
      margin-top: auto;
      display: none;
      @media only screen and (min-width: 1490px) {
        display: block;
      }

      & > span {
        display: inline-block;
        width: 28px;
        height: 28px;
        font-size: 28px;
        color: #444;
        margin-right: 6px;
        vertical-align: top;
        div {
          font-size: 28px;
        }
      }
      .more {
        width: 28px;
        height: 28px;
        line-height: 28px;
