        letter-spacing: -1px;
      }
    }

    &-list-elevator {
      margin-top: 4px;
      display: flex;
      font-size: 14px;
      line-height: 18px;
      color: #444444;

      .img-elevator {
        margin-right: 10px;
        width: 14px;
        height: 14px;
        background-image: url(~assets/img/icon/elevator.svg);
        background-position: center;
      }

      p {
        width: calc(100% - 30px);
        padding: 0;
      }

      p:not(:first-child) {
        margin-top: 0;
      }
    }
    .info-block {
      width: 89%;
      position: absolute;
      bottom: 20px;
      left: 20px;
      display: flex;
      background: #fdd2d2;
      color: #e10600;
      font-size: 13px;
      padding: 10px;
      &-ico {
        margin-right: 10px;
        padding-top: 2px;
        .icon-app-clock {
          font-size: 18px;
        }
      }
    }
  }
}
.icon-app-sea {
  font-size: 13px;
  color: #000;
  margin-right: 10px;
}
.info-sea {
  color: #000;
}
</style>
