export default function (Component) {
  Component.__i18n = Component.__i18n || []
  Component.__i18n.push({
    "locale": "",
    "resource": {
      "ru": {
        "title": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Еще чуть-чуть…"])},
        "note": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Проверяем актуальные цены и доступность"])}
      },
      "en": {
        "title": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["A little bit more…"])},
        "note": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["We check the current prices and availability"])}
      }
    }
  })
}
