<i18n>
{
	"ru": {
		"showGoldPartnerLabelAbility": "Золотой партнер",
		"showSilverPartnerLabelAbility": "Серебряный партнер",
    "superhost": "Суперхозяин",
    "favoriteAdd": "Добавить в избранное",
    "favoriteRemove": "Убрать из избранного",
    "meter": "м<sup><small>2</small></sup>",
    "cashback": "Кэшбэк"
	},
	"en": {
		"showGoldPartnerLabelAbility": "Gold partner",
		"showSilverPartnerLabelAbility": "Silver partner",
    "superhost": "Superhost",
    "favoriteAdd": "Add to favorites",
    "favoriteRemove": "Remove from favorites",
    "meter": "m<sup><small>2</small></sup>",
    "cashback": "Cashback"
	}
}
</i18n>

<template>
  <div class="carousel">
    <div class="carousel__wrap">
      <div class="object-info">
        <div
          v-if="data.owner.permissions.includes('showSuperhostLabelAbility')"
          class="object-info__superhost object-info__item"
        >
          {{ $t("superhost") }}
        </div>
      </div>
      <a
        :target="targetOpt"
        :href="{
          name: 'Detail',
          params: { id: data.id },
          query: { ...getQuery.query },
        }"
        @click="setSelectedSlide(selectedSlide)"
        @mousedown="clickObjectBronevik($event, linkBronevik)"
      >
        <SliderLight
          :media="data.media"
          :dots="true"
          :arrows="false"
          :selected-slide="slideIndex"
          @currentSlide="$emit('update:selectedSlide', $event)"
        />
        <div
          v-if="data.is_recommended"
          class="guests-recommend-icon carousel__recommend-icon"
        >
          <img
            src="~/assets/img/icon/finger-like.svg"
            alt="finger-like"
            class="guests-recommend-icon__img"
          />
        </div>
      </a>
    </div>
  </div>
</template>

<script>
import SliderLight from "@sutochno/sliderlight";
import Rating from "@/components/base/BaseRating";
import { mapState, mapGetters, mapActions } from "vuex";
export default {
  components: {
    SliderLight,
    Rating,
  },
  props: {
    data: {
      type: Object,
    },
    targetOpt: {
      type: String,
    },
    selectedSlide: {
      type: Number,
      default: 0,
    },
    linkBronevik: {
      type: String,
      default: "",
    },
    isBronevik: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    ...mapActions("detail", ["setSelectedSlide"]),
    clickObjectBronevik(event, link) {
      if (link) {
        event.preventDefault();
        this.$gtag.event("click", {
          event_category: "search_sa",
          event_label: "object_b_link",
          value: this.data.location.city.title
        });
        window.open(link);
      }
    },
    onClickPopoverGA() {
      this.$gtag.event("click", {
        event_category: "search_sa_m",
        event_label: "cashback",
      });
      window.open("https://promo.sutochno.ru/bonus-za-prozhivanie/", "_blank");
    },
    formatedCurrensies(price) {
      return (
        new Intl.NumberFormat(this.$i18n.locale, {
          style: "decimal",
          currency: "RUB",
          minimumFractionDigits: 0,
          useGrouping: true,
        })
          .format(price)
          .replace(" ", "&#8239;") + "&#8239;₽"
      );
    },
  },
  computed: {
    ...mapState("search", ["guests", "isPosition"]),
    ...mapGetters("search", ["occupied", "getQuery"]),
    isDate() {
      return this.occupied ? true : false;
    },
    cashback() {
      const price = this.data.price.full;
      const cashback = Math.ceil(price * 0.3);
      this.isCashback = cashback >= 1 && cashback <= 20000 ? true : false;
      return cashback;
    },
    message() {
      return this.isFavorite === true
        ? this.$t("favoriteRemove")
        : this.$t("favoriteAdd");
    },
  },
  mounted() {
    this.slideIndex = this.selectedSlide;
  },
  data() {
    return {
      page: 1,
      isFavorite: this.data.favorite,
      isVisible: false,
      slideIndex: 0,
      isCashback: false,
    };
  },
};
</script>

<style lang="scss" scoped>
.preview-image {
  display: block;
  width: 100%;
  height: 100%;
  &[data-pages]:before {
    display: block;
    content: attr(data-pages);
    position: absolute;
    left: 50%;
    bottom: 10px;
    z-index: 1;
    display: block;

    background: #00000080;
    border-radius: 10px;
    height: 20px;
    color: white;
    font-size: 13px;
    line-height: 20px;
    text-align: center;
    width: 40px;
    padding: 0 4px;
    transform: translateX(-50%);
  }

  &[data-pages]:before {
    display: none;

    @media only screen and (min-width: 1490px) {
      display: block;
    }
  }
}

.carousel {
  .sc-sliderlight {
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;
    &.shadow:before {
      content: unset;
    }
  }
}

@keyframes loader {
  from {
    left: -5%;
    opacity: 0;
  }
  10% {
    left: 10%;
    opacity: 1;
  }
  90% {
    left: 90%;
    opacity: 1;
  }
  to {
    left: 99%;
    opacity: 0;
  }
}
.carousel {
  position: relative;
  height: 100%;
  z-index: 1;

  &__wrap {
    height: 210px;
  }

  .object-info {
    z-index: 99;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin: 0 11px;
    position: absolute;
    top: -8px;
    @media only screen and (max-width: 340px) {
      margin: 0 9px;
    }
    &__item:last-child {
      margin-right: 0;
    }
    &__superhost {
      background-color: #ffa900;
    }
    &__recommended {
      background-color: #0659ff;
    }
    &__cashback {
      background-color: #f51449;
      .shadow-cashback {
        color: #fff;
        font-size: 9px;
        padding: 0;
        span {
          line-height: 18px;
        }
      }
    }
    &__item {
      width: auto;
      height: 18px;
      font-weight: 600;
      border-radius: 9px;
      color: #fff;
      font-size: 9px;
      line-height: 18px;
      margin-right: 10px;
      padding: 0 9px 0 10px;
      position: relative;
      text-align: center;
      cursor: default;
    }
  }

  img {
    height: 100%;
    width: 100%;
    max-width: 100%;
    object-fit: cover;
  }
  :deep(.VueCarousel) {
    // height: 100%;
    .VueCarousel-slide {
      height: inherit;
    }
    .VueCarousel-inner {
      max-height: 100%;
      width: 100%;
      max-width: 100%;
      object-fit: cover;
    }
  }

  &:hover
    :deep(.VueCarousel-navigation-button.VueCarousel-navigation-next::before) {
    right: 0;
    margin: 0 0 0 auto;
    background-position: -44px 0;
  }
  &:hover :deep(.VueCarousel-navigation-button::before) {
    display: block;
    content: "";
    background-image: url("~assets/img/spr_ss_arrows.png");
    width: 44px;
    height: 44px;
  }
  & :deep(.VueCarousel-navigation-button) {
    outline: none !important;
    top: 0;
    bottom: 0;
    margin: 0;
    width: 50%;
    transform: translateY(0) translateX(0%);
  }

  .pr {
    align-items: center;
    background-color: rgba(255, 255, 255, 0.7);
    border-radius: 16px;
    bottom: 10px;
    color: #000;
    display: flex;
    font-size: 14px;
    font-weight: 400;
    justify-content: center;
    line-height: 22px;
    padding: 8px 8px 7px;
    position: absolute;
    right: 10px;
    text-align: center;
    z-index: 2;
    &-icon {
      color: #6fa759;
      font-size: 12px !important;
      margin-right: 6px;
    }
    .pr-icon {
      width: 16px;
      height: 16px;
      margin-left: 5px;
      &::before {
        content: "";
        display: block;
        width: 100%;
        height: 100%;
        background-image: url(~assets/img/icon/shuttle-1.svg);
        background-position: center;
        background-size: contain;
        background-repeat: no-repeat;
      }
    }
  }

  &__owner-options {
    display: flex;

    position: absolute;
    top: 10px;
    left: 10px;
    z-index: 2;
  }

  &__size {
    position: absolute;
    bottom: 10px;
    left: 10px;
    z-index: 2;

    display: flex;
    align-items: center;
    justify-content: center;

    border-radius: 2px;
    background-color: #fff;
    padding: 2px 8px;
    font-size: 12px;
    line-height: 14px;
    color: #444;
    span {
      display: block;
    }
    span:first-child {
      margin-right: 3px;
    }
  }

  &__relev {
    position: absolute;
    bottom: 34px;
    left: 10px;
    z-index: 2;

    display: flex;
    align-items: center;
    justify-content: center;

    border-radius: 2px;
    background-color: #ffa900;
    padding: 2px 8px;
    font-size: 12px;
    line-height: 14px;
    color: #000;
  }

  &__rating {
    ::v-deep span {
      color: #fff;
    }

    @media only screen and (min-width: 1280px) {
      display: flex;
    }
    @media only screen and (min-width: 1490px) {
      display: none;
    }
  }

  &__favorite {
    margin-left: 0;
    margin-top: 0;
    position: absolute;
    right: 15px;
    top: 15px;
  }

  ::v-deep .hooper-pagination {
    display: none;

    @media only screen and (min-width: 1490px) {
      display: block;
    }
  }

  ::v-deep .hooper-next,
  ::v-deep .hooper-prev {
    // &:after{
    //   opacity: 0;
    // }
  }

  &__recommend-icon {
    position: absolute;
    right: 10px;
    top: 100%;
    transform: translateY(-50%);
  }
  .guests-recommend-icon__img {
    width: 13px;
    height: auto;
  }
}

.guests-recommend-icon {
  width: 28px;
  height: 28px;
  background-color: #f51449;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  &__img {
    transform: translate(1px, -1px);
  }
}
</style>
