<i18n>
{
	"en": {
		"bookingGuarantee": "Let's find a place to stay!",
		"cityOrCountry": "City, country",
		"cityTitle": "Choose direction",
		"guest": "0|{n} guest|{n} guests|{n} guests",
		"idea": "Travel ideas",
		"moreThanVariants1": "200 thousand housing options",
    "moreThanVariants2": "for trips in Russia and abroad",
    "moreThanVariants3": "Apartments, hotels and guest houses",
		"occupiedIn": "Check-in",
		"occupiedOut": "Check-out",
    "startSearch": "Search",
    "payInfoTitle": "Payment information",
    "payInfoBody": "To view information about payments, go to your personal account under your phone number and use the login password sent by SMS"
  },
	"ru": {
		"bookingGuarantee": "Найдём, где остановиться!",
		"cityOrCountry": "Курорт, город или адрес",
		"cityTitle": "Выбрать направление",
		"guest": "0|{n} гость|{n} гостя|{n} гостей",
		"idea": "Идеи для поездок",
		"moreThanVariants1": "200 тысяч вариантов жилья",
    "moreThanVariants2": "для поездок по России и зарубежью",
    "moreThanVariants3": "Квартиры, отели и гостевые дома ",
		"occupiedIn": "Заезд",
		"occupiedOut": "Отъезд",
    "startSearch": "Найти",
    "payInfoTitle": "Информации об оплатах",
    "payInfoBody": "Для просмотра информации об оплатах, перейдите в личный кабинет под вашим номером телефона и используйте пароль для входа, высланный по СМС"
  }
}
</i18n>
<template>
  <div class="block-search" ref="blockSearch">
    <div
      :style="{ zIndex: 1 }"
      class="block-search--field search"
      :class="{ 'main-block-field--city-fill': term }"
      @click="modalRefCity = true"
    >
      <div>
        <div class="title" :class="{'active': term}">
          {{ $t("cityOrCountry") }}
        </div>
        <div class="val">
          {{term}}
        </div>
      </div>
    </div>
    <div class="block-search--occupied">
      <div
        class="occupied"
        @click="checkInOpen()"
      >
        <div class="title" :class="{'active': occupied.in}">
          {{$t("occupiedIn")}}
        </div>
        {{ occupied.in ? format(occupied.in) : "" }}
      </div>
      <div
        class="occupied"
        @click="checkOutOpen()"
      >
        <div class="title" :class="{'active': occupied.out}">
          {{$t("occupiedOut")}}
        </div>
        {{ occupied.out ? format(occupied.out) : "" }}
      </div>
    </div>
    <div class="block-search--field guest">
      <SelectGuests />
    </div>
    <button
      class="block-search--btn"
      :class="{
        loading:
          isBtnDisabled || listSuggestions.isLoading || isLoadingLocation,
        disabled:
          isBtnDisabled || listSuggestions.isLoading || isLoadingLocation
      }"
      :disabled="
        isBtnDisabled || listSuggestions.isLoading || isLoadingLocation
      "
      @click="doSearch()"
      >{{ $t("startSearch") }}
    </button>
    <DatePicker ref="modalRefCalendar" v-model="occupied" />
    <BottomSheet v-model="modalRefCity" class="modal-warning" :title="$t('cityTitle')">
      <BaseSuggestionsSearchModal @choice="modalRefCity = false" />
    </BottomSheet>
  </div>
</template>
<script>
import _ from "lodash";
import { mapGetters, mapState, mapMutations } from "vuex";
import Modal from "@sutochno/modal";
import CityChoice from "@/components/Main/MainCityChoice";
import DatePicker from "@/components/Main/MainDatePicker";
import SelectGuests from "@/components/base/SelectGuests/Mobile";
import BaseSuggestionsSearchModal from "@/components/base/BaseSuggestions/SearchModal";
import BottomSheet from "@/components/base/BottomSheet";

export default {
  name: "SearchMainAdaptive",
  components: {
    Modal,
    DatePicker,
    CityChoice,
    SelectGuests,
    BaseSuggestionsSearchModal,
    BottomSheet,
  },
  watch: {
    term(val) {
      if (val && !this.checkIn && !this.checkOut) {
        this.$refs.modalRefCalendar.show();
      }
    }
  },
  data() {
    return {
      query: this.term,
      occupied: {},
      isBtnDisabled: false,
      backgroundIndex: Math.floor(Math.random() * 7),
      isMainNotParams: false,
      isMainNoPath: false,
      modalRefCity: false,
    };
  },
  watch: {
    dateFromHistorylist(val, oVal) {
      if (val.in != oVal.in || val.out != oVal.out) {
        this.occupied = val;
      }
    }
  },
  computed: {
    ...mapGetters("search", ["maxGuests", "termEmpty", "getTypeOfSelectedVariant"]),
    ...mapGetters("search", { getterOccupied: "occupied" }),
    ...mapState("search", [
      "location",
      "term",
      "guests",
      "listSuggestions",
      "isLoadingLocation",
      "dateFromHistorylist",
      "analiticParamForSearchWidList",
      "checkIn",
      "checkOut",
    ]),
  },
  methods: {
    ...mapMutations("search", ["setVisibleResult"]),
    checkInOpen() {
      this.$refs.modalRefCalendar.show();
    },
    checkOutOpen() {
      this.$refs.modalRefCalendar.show();
    },
    format(date) {
      return date.toLocaleString("ru-RU", {
        day: "numeric",
        month: "short",
        year: "numeric",
      });
    },
    getFormatOccupied(date) {
      let format = (date) => {
        let month = (date.getMonth() + 1).toString().padStart(2, 0),
          day = date.getDate().toString().padStart(2, 0),
          year = date.getFullYear();
        return year + "-" + month + "-" + day;
      };
      return date.in && date.out
        ? format(date.in) + ";" + format(date.out)
        : null;
    },
    async doSearch() {
      if (this.termEmpty) {
        this.modalRefCity = true;
      } else {
        if (!this.checkIn && !this.checkOut) {
          this.$refs.modalRefCalendar.show();
          return false;
        }
        this.isBtnDisabled = true;
        const query = _.pickBy({
          occupied: this.getterOccupied,
          guests_adults: this.guests.adults,
          guests_childrens: this.guests.childrens.join(","),
          term: this.term,
        });

        this.setVisibleResult(true);

        this.isBtnDisabled = false;

      }
    },
  },
};
</script>

<style lang="scss" scoped>


.block-search {
  display: grid;
  column-gap: 0;
  row-gap: 10px;
  grid-template-columns: 1fr;
  grid-template-areas:
    "search"
    "occupied"
    "guest"
    "button"
  ;
  &--field {
    cursor: pointer;
    border-radius: 4px;
    border: solid 1px var(--input-border);
    display: flex;
    align-items: center;
    height: 60px;
    &.search {
      grid-area: search;
      padding: 0 20px;
      .title {
        color: #717171;
        font-size: 16px;
        &.active {
          font-size: 14px;
        }
      }
      .val {
        color: #000;
        font-size: 16px;
      }
    }
    &.guest {
      grid-area: guest;
    }
    button.guest {
      display: block;
      width: 100%;
      height: 100%;
      cursor: pointer;
      padding: 0 20px;
      margin: 0;
      outline-style: none;
      background: transparent;
      border: none;
      text-align: left;
      .title {
        color: var(--brownish-grey);
        font-size: 14px;
        margin-bottom: 4px;
      }
      .body {
        color: var(--black);
        font-size: 16px;
      }
      &.active {
        box-shadow: inset 0 0 0 1px #f51449;
      }
    }
  }
  &--occupied {
    grid-area: occupied;
    display: flex;
    justify-content: space-between;
    position: relative;
    .occupied {
      cursor: pointer;
      padding: 0 20px;
      width: 50%;
      height: 60px;
      border-radius: 4px;
      border: solid 1px var(--input-border);
      display: flex;
      flex-direction: column;
      justify-content: center;
      font-size: 15px;
      &:nth-child(1) {
        margin-right: 5px;
      }
      &:nth-child(2) {
        margin-left: 5px;
      }
      &.active {
        box-shadow: inset 0 0 0 1px #f51449;
      }
      .title {
        color: var(--brownish-grey);
        font-size: 16px;
        margin-bottom: -11px;
        padding-top: 16px;
        transform: translate(0, -13px) scale(1);
        transform-origin: 20px 0;
        transition: transform 0.15s, opacity 0.1s;
        &.active {
          transform: translate(-3px, -10px) scale(0.85714);
        }
      }
      .body {
        color: var(--black);
        font-size: 16px;
      }
    }
    .calendar-in {
      position: absolute;
      top: 100%;
      margin-top: 10px;
    }
  }
  &--btn {
    display: inline-block;
    cursor: pointer;
    outline-style: none;
    padding: 0 20px;
    margin: 0;
    height: 60px;
    min-width: 124px;
    width: 100%;
    border-radius: 4px;
    background: var(--bg-button);
    color: var(--color-button);
    font-size: 16px;
    border: none;
    position: relative;
  }
}










::v-deep .sc-searchwidget__guest {
  display: block !important;
}

.main-block {
  background-color: #000000;
  color: #ffffff;
  padding: 20px 15px 80px;
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 450px;
  max-height: 700px;
  height: 80vh;

  .lazy-background {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 100%;
    width: 100%;
    object-fit: cover;
    opacity: 1;
    &-wl {
      opacity: 0.8;
    }
  }
  &:after {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: #000;
    opacity: 0.3;
  }
  a {
    color: #ffffff;
    &:hover {
      color: #ffffff;
    }
  }
  &-title {
    font-size: 22px;
    line-height: 1;
    margin-bottom: 10px;
    position: relative;
    z-index: 1;
    margin-bottom: 14px;
    text-align: center;
    font-weight: bold;
    display: block;
    white-space: nowrap;
  }
  &-note {
    font-size: 14px;
    line-height: normal;
    font-weight: normal;
    position: relative;
    z-index: 1;
    margin-bottom: 40px;
    text-align: center;
    display: block;
    white-space: nowrap;
  }
  &-down {
    margin-top: 43px;
    display: block;
    white-space: nowrap;
    text-align: center;
    font-weight: normal;
  }
  &-field {
    position: relative;
    z-index: 1;
    width: 100%;
    &--city {
      background: #ffffff;
      border-radius: 2px;
      height: 56px;
      color: #717171;

      display: flex;
      align-items: center;
      padding: 0 12px;
      margin-bottom: 2px;

      position: relative;
      z-index: 1;
      width: 100%;
      &::after {
        content: "";
        background-image: url("~assets/img/maps-bubble-icon.svg");
        background-repeat: no-repeat;
        display: block;
        width: 14px;
        height: 18px;
        margin-left: auto;
      }
      &-fill {
        color: #444444;
      }
    }
    &--occupied {
      margin-bottom: 2px;
      &__in,
      &__out {
        white-space: nowrap;
        background-color: white;
        width: calc(50% - 1px);
        padding: 0 12px;
        height: 56px;
        color: #717171;
        display: inline-flex;
        align-items: center;
        .icon-app-calendar {
          font-size: 20px;
          color: #dadada;
          margin-left: auto;
          box-shadow: -9px 0px 4px 2px #ffffff;
        }
        &-fill {
          color: #444444;
        }
      }
      &__in {
        border-radius: 2px 0 0 2px;
        margin-right: 1px;
      }
      &__out {
        border-left: 2px solid transparent;
        border-radius: 0 2px 2px 0;
        margin-left: 1px;
      }
    }
    &--guest {
      height: 56px;
      background-position: right 11px top 19px;
      border-color: #ffffff;
      border-radius: 2px;
      margin-bottom: 10px;
      font-size: 15px;
    }
  }
}
.btn-search-main {
  position: relative;
  border-color: #f51449;
  background: #f51449;
  font-size: 16px;
  padding-top: 18px;
  padding-bottom: 18px;
  &:active,
  &:hover,
  &:focus {
    color: #fff;
    background: #f62c5c;
    border-color: #f51449;
  }
  &:active {
    background-image: none;
    -webkit-box-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
    box-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
  }
  &:disabled,
  &__disabled,
  &.disabled {
    pointer-events: none;
    // background: #8d8d8d;
    // border-color: #8d8d8d !important;
  }
  &.loading {
    opacity: 1;
  }
}
.modal-pay-info {
  padding: 20px;
}
.main-footer {
  padding: 20px 0;
  h4 {
    text-indent: 10px;
  }

  .container {
    margin-right: auto;
    margin-left: auto;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media (max-width: 767px) {
  .main-footer {
    .container {
      max-width: 100%;
    }
  }
}
</style>
