<i18n>
{
	"ru": {
    "clear": "Чистота",
    "price_quality": "Цена - качество",
    "right_photo": "Соответствие фото",
    "service": "Качество обслуживания",
    "infrastructure": "Расположение",
    "accuracy": "Своевременность заселения",
    "ratingGuests": "Оценка гостей",
    "reviewStr": "на основании отзывов | на основании {n} отзыва | на основании {n} отзывов | на основании {n} отзывов",
    "emptyTitle": "Отзывы",
    "emptySubtitle": "В настоящее время пока никто не оставил отзыв на данный объект",
    "middleRateText": "о проживании со средней оценкой",
    "reviews": "отзывов | {n} отзыв | {n} отзыва | {n} отзывов",
    "of": "из",
    "externalRating": "Оценка сформирована по реальным отзывам гостей на зарубежных сервисах, ушедших из России. Мы не публикуем отзывы целиком, т.к. гости не давали на это явного согласия. Возможно, отзывы появятся позже."
	},
	"en": {
    "clear": "Clear",
    "price_quality": "Quality - price",
    "right_photo": "Truth a photo",
    "service": "Quality of service",
    "infrastructure": "Good location",
    "accuracy": "Accuracy",
    "ratingGuests": "Оценка гостей",
    "reviewStr": "on basis reviews | on basis {n} review | on basis {n} reviews | on basis {n} reviews",
    "emptyTitle": "Rewiews",
    "emptySubtitle": "No reviews of this property yet",
    "middleRateText": "with an average rating",
    "reviews": "accommodation reviews | {n} accommodation review | {n} accommodation reviews | {n} accommodation reviews",
    "of": "of",
    "externalRating": "The rating is based on real reviews of guests on foreign services who left Russia. We do not publish full reviews, because the guests did not explicitly agree to this. Perhaps reviews will appear later."
	}
}
</i18n>
<template>
  <div
    class="avg-rating-wrapp"
    @mouseover="$emit('mouseover')"
    @mouseleave="$emit('mouseleave')"
  >
    <div class="avg-rating">
      <p class="avg-rating__text" v-if="isExternalRating && rating">
        <span class="avg-rating__text-b" v-if="countReviews">{{ $tc('reviews', countReviews) }}</span>
        <span>{{ ` ${$t("middleRateText")} ` }}</span>
        <span class="avg-rating__text-b" v-if="dataRating">{{ $filters.newRatingExterna(dataRating) }}</span>
        <span>{{ ` ${$t("of")} ` }}</span>
        <span class="avg-rating__text-b">10,0</span>
      </p>
      <template v-else>
        <p class="avg-rating__text" v-if="rating">
          <span class="avg-rating__text-b" v-if="countReviews">{{ $tc('reviews', countReviews) }}</span>
          <span>{{ ` ${$t("middleRateText")} ` }}</span>
          <span class="avg-rating__text-b" v-if="dataRating">{{ $filters.newRating(dataRating) }}</span>
          <span>{{ ` ${$t("of")} ` }}</span>
          <span class="avg-rating__text-b">10,0</span>
        </p>
        <div class="reviews__rating loading_primary" :class="{ loading: !viewRating, 'loading_primary': !viewRating }">
          <template v-if="viewRating">
            <div class="rating__calc">
              <div class="calc__range">
                <div v-for="(rating, name, i) in ratingArray" :key="i" class="range__item">
                  <div class="range__text">{{ $t(rating.name) }}</div>
                  <div class="range__line">
                    <div class="line">
                      <div class="line_complete" :style="{ width: `${rating.value * 20}%`, backgroundColor: color }"></div>
                    </div>
                    <div class="range__rating">{{ $filters.newRating(rating.value) }}</div>
                  </div>
                </div>
              </div>
            </div>
          </template>
        </div>
      </template>
    </div>
  </div>
</template>
<script>
import { Messages } from "@/api/";
import BaseRating from "@/components/base/BaseRating";
import _ from "lodash";
import { mapState, mapGetters, mapActions } from 'vuex';

export default {
  name: "AvgRating",
  props: {
    id: {
      type: Number,
      require: true
    },
    rating: {
      type: Object
    },
    color: {
      type: String,
      default: '#ffa900'
    },
    isExternalRating: {
      type: Boolean,
      default: false
    },
    countReviews: {
      type: Number,
      default: 0
    },
    dataRating: {
      type: Number,
      required: true,
    },
  },
  components: {
    BaseRating
  },
  data() {
    return {
      avgRating: null,
      viewRating: true
    };
  },
  computed: {
    ratingArray() {
      const rating = _.omit(this.rating?.ratings, ["avg_rating"])
      const rateArray = [
        "clear",
        "price_quality",
        "service",
        "infrastructure",
        "accuracy",
        "right_photo"
      ];
      let newArr = [];
      rateArray.map((r) => {
        return Object.entries(rating)
          .filter(([key, value]) => {
            if ([key] == r) {
              newArr.push({
                name: key,
                value: value,
                translate: this.$t(key),
              });
            }
          });
      });
      return newArr;
    },
  },
  async mounted() {
  },
  methods: {
    ...mapActions('search', ['setObjectRatings', 'setHotelRatings']),
  },
};
</script>

<style lang="scss" scoped>
.avg-rating-wrapp {
  padding-top: 28px;
  position: absolute;
  top: 15px;
  right: 20px;
  z-index: 10;
}
.avg-rating {
  padding: 15px;
  min-width: 430px;
  border-radius: 2px;
  box-shadow: 0 0 6px 0 rgba(0, 0, 0, 0.11);
  background-color: #fff;

  &__text {
    color: #444444;
    font-size: 13px;
    line-height: 16px;

    &-b {
      font-weight: 800;
    }
  }

  .reviews__rating {
    position: relative;
    background: #fff;
    margin-top: 20px;
    min-height: 82px;

    .rating {
      &__stars {
        padding-top: 3px;
      }
      &__calc {
        display: flex;

        .calc {
          &__range {
            display: flex;
            flex-wrap: wrap;
            width: 100%;
            .range {
              &__item {
                display: flex;
                align-items: flex-start;
                min-height: 25px;
                width: 50%;
                &:nth-child(2n) {
                  padding-left: 10px;
                }
                &:nth-child(2n+1) {
                  padding-right: 10px;
                }
              }
              &__text {
                display: flex;
                align-items: center;
                width: 115px;
                font-size: 13px;
                line-height: 16px;
                color: #444444;
                // padding-right: 10px;
              }
              &__line {
                display: flex;
                align-items: center;
                width: calc(100% - 115px);
                height: 16px;

                .range__rating {
                  font-size: 13px;
                  // line-height: 18px;
                  color: #7f7f7f;
                  font-weight: 800;
                }
                .line {
                  background-color: #ededed;
                  display: inline-block;
                  height: 6px;
                  width: 40px;
                  margin-left: 10px;
                  margin-right: 10px;
                  border-radius: 3px;
                  overflow: hidden;

                  &_complete {
                    background-color: #fdb12e;
                    height: 100%;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
</style>
