<i18n>
{
	"ru":{
		"guests": "{n} гостей | {n} гость | {n} гостя | {n} гостей"
	},
	"en":{
		"guests": "{n} guests | {n} guest | {n} guests | {n} guests"
	}
}
</i18n>

<template>
  <div
    :data-variant-id="index"
    class="suggestions-list-elem"
    :class="{
      'suggestions-list-elem__active': index === selectedIndex,
    }"
    @click="$emit('applyVariants', variant, 'history')"
  >
    <span
      class="suggestions-list-elem--icon suggestions-list-elem--history"
    ></span>
    <div class="suggestions-list-elem--main">
      <div class="suggestions-list-elem--top">
        <span class="suggestions-list-elem--title"
          >{{ variant.title }}{{ variant.title_ext ? "," : "" }}</span
        >
        <span class="suggestions-list-elem--location">{{
          variant.title_ext
        }}</span>
        <!-- <span class="suggestions-list-elem--count">{{
					variant.count | format
				}}</span> -->
      </div>
      <div
        v-if="historyDate || historyGuests"
        class="suggestions-list-elem--bottom"
      >
        <span v-if="historyDate" class="suggestions-list-elem--date">{{
          historyDate
        }}</span>
        <span v-if="historyGuests" class="suggestions-list-elem--guests">
          {{ historyGuests }}
        </span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "HistoryListElement",
	filters: {
    format(value) {
      if (Number(value) == 0) return "";
      return Number.isInteger(value)
        ? Number(value).toLocaleString("ru-RU")
        : value;
    },
  },
  props: {
    selectedIndex: {
      type: Number,
    },
    index: {
      type: Number,
    },
    variant: {
      type: Object,
      require: true,
    },
  },
  computed: {
    historyDate() {
      if (this.checkDate(this.variant.date)) {
        const dateIn = new Date(this.variant.date.in);
        const dateOut = new Date(this.variant.date.out);

        if (dateIn.getMonth() == dateOut.getMonth()) {
          return `${dateIn.getDate()} — ${this.formatDate(dateOut)}`;
        }
        return `${this.formatDate(dateIn)} — ${this.formatDate(dateOut)}`;
      }
      return "";
    },
    historyGuests() {
      if (this.variant.maxGuests) {
        return this.$tc("guests", this.variant.maxGuests);
      }
      return "";
    },
  },
  methods: {
    checkDate(date) {
      if (date?.in && date?.out) {
        const currentDate = new Date();
        const dateIn = new Date(date.in);
        const dateOut = new Date(date.out);
        currentDate.setHours("00");
        currentDate.setMinutes("00");
        currentDate.setSeconds("00");
        currentDate.setMilliseconds("00");

        if (currentDate > dateIn || currentDate > dateOut) {
          return false;
        }
        return true;
      }
      return false;
    },
    formatDate(date) {
      return date.toLocaleString(this.$i18n.locale, {
        day: "numeric",
        month: "long",
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.suggestions-list-elem {
  color: #717171;
  padding: 10px 20px;
  display: flex;
  align-items: center;
  cursor: pointer;
  font-size: 14px;
  &__active,
  &:hover {
    background: #f6f6f6;

    .suggestions-list-elem--icon {
      background-color: #fff;
    }
  }
  &--main{
    overflow: hidden;
  }
  &--top{
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  &--top + &--bottom {
    margin-top: 5px;
  }
  &--title {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    color: black;
    padding-right: 4px;
  }
  &--location {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  &--count {
    margin-left: auto;
    padding-left: 10px;
    white-space: nowrap;
  }

  &--bottom {
    font-size: 12px;
  }
  &--date {
    color: #000;
  }
  &--date + &--guests {
    &::before {
      content: " \00B7";
      color: #000;
    }
  }

  // &--icon {
  //   min-width: 30px;
  //   display: inline-block;
  // }
  &--history {
    display: inline-block;
    background-color: #ededed;
    padding: 5px;
    background-image: url("~assets/img/clock-gray.svg");
    background-repeat: no-repeat;
    width: 32px;
    height: 32px;
    min-width: 32px;
    border-radius: 50%;
    background-position: center;
    margin-right: 10px;
  }
}
</style>
