  position: relative;

  &__body {
    display: none;
    position: absolute;
    z-index: 11;
    top: -11px;
    left: 50%;
    padding: 10px 15px;
    background-color: #f6f6f6;
    border: solid 1px #d8d8d8;
    color: #000;
    transform: translate(-50%, -100%);
    max-width: 180px;
    width: 180px;
