    &.left-left {
      transform: translate(-20%, -100%);
    }
    &.left {
      transform: translate(-40%, -100%);
    }
    &.right {
      transform: translate(-60%, -100%);
    }
    &.right-right {
      transform: translate(-80%, -100%);
    }
  }

  &::after,
  &::before {
