<i18n>
{
  "ru": {
    "guests": "Гости",
    "adult": "0|{n} взрослый | {n} взрослых | {n} взрослых",
    "child": " без детей |{n} ребёнок | {n} ребёнка | {n} ребёнка",
    "children": "{n} детей",
    "addChild":"Добавить ребёнка",
    "done":"Готово",
    "upToYears": "до 17 лет"
  },
  "en": {
    "guests": "Guests",
    "adult": "0|{n} adult | {n} adult | {n} adult",
    "child": " without children |{n} child | {n} child's | {n} child's",
    "children": "{n} children's",
    "addChild":"Add child",
    "done":"Done",
    "upToYears": "up to 17 years old"
  }
}
</i18n>
<template>
  <div v-click-outside="close" class="select-guests" :class="view">
    <button
      class="select-guests--btn"
      :class="{
        active: isSelect,
        'active-simple': isSelect,
        'select-guests_current': occupied > 1
      }"
      :style="
        guests.childrens?.length !== 0 ? 'minWidth: 248px' : 'minWidth: 140px'
      "
      @click="showSelect()"
    >
      <div class="title">
        {{ $t("guests") }}
      </div>
      <div class="body">{{ $tc("adult", guests.adults) }}<span :class="{ empty: this.guests.childrens.length === 0 }">
          <template v-if="this.guests.childrens.length !== 0">,</template> {{childrenText}}
        </span>
      </div>
      <span class="ico-arrow"></span>
    </button>
    <template v-if="isSelect">
      <transition name="select-guests--fade">
        <div
          v-show="isSelect"
          id="sel"
          ref="selectGuests"
          class="select-guests--main tmp-font--medium"
        >
          <template v-if="selectGuests">
            <GuestsCounter
              v-if="selectGuests.adults"
              v-model="selectGuests.adults"
              :text="$tc('adult', selectGuests.adults)"
              class="adults"
            />
            <template
              v-for="(child, index) in selectGuests.childrens"
              :key="index"
            >
              <ChildrenSelect
                :ref="`guest_${index}`"
                v-model="selectGuests.childrens[index]"
                class="guests__item"
                :add-child-text="$t('addChild')"
                :is-static="true"
                @delete="selectGuests.childrens.splice(index, 1)"
                @isOpen="childrensOpen($event, index)"
              />
            </template>
            <div class="select-guests__text">
              {{ $t("upToYears") }}
            </div>
          </template>
          <div class="select-guests__buttons">
            <button
              class="js-button-close button button_dark-gray button_w-100"
              @click="close($event)"
            >
              {{ $t("done") }}
            </button>
          </div>
        </div>
      </transition>
    </template>
  </div>
</template>

<script>
import SelectMain from "./SelectMain";
import ChildrenSelect from "./ChildrenSelect";
import GuestsCounter from "./GuestsCounter";
import { mapState, mapGetters, mapActions } from "vuex";
import getHost from "@/utils/getHost";

export default {
  name: "SelectGuestsDesktop",
  components: {
    SelectMain,
    ChildrenSelect,
    GuestsCounter,
  },
  props: {
    view: {
      type: String,
    },
    maxGuestsObject: {
      type: Number,
    },
    isBlockingClick: {
      type: Boolean,
      default: false,
    },
    isChangeByClose: {
      type: Boolean,
      default: false,
    },
    isDetail: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    ...mapState("search", ["guests"]),
    ...mapGetters("search", ["maxGuests", "occupied"]),
    childrenText() {
      return this.guests.childrens.length >= 5
        ? this.$t("children", { n: this.guests.childrens.length })
        : this.$tc("child", this.guests.childrens.length, { n: this.guests.childrens.length });
    },
  },
  watch: {
    selectGuests: {
      deep: true,
      handler(val) {
        if (!this.isChangeByClose) this.sendToStore(val);
      },
    },
    "selectGuests.childrens": {
      deep: true,
      handler(val) {
        if (val[val.length - 1] !== null) {
          val.push(null);
        }
      },
    },
    isSelect(val) {
      this.$emit(val ? "show" : "close");
    },
    guests(val){
      if(val.adults != this.selectGuests?.adults || val.childrens?.length != this.selectGuests?.childrens?.length){
        this.selectGuests = JSON.parse(JSON.stringify(this.guests))
      }
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.selectGuests = JSON.parse(JSON.stringify(this.guests));
    });
  },
  methods: {
    ...mapActions("search", ["updateAdults", "updateChildrens"]),
    sendToStore(val) {
      if (val.adults != this.guests.adults) {
        this.$store.commit("search/setAdults", val.adults);
        this.saveLocalStorage();
      }
      let childs = val.childrens.filter((x) => x !== null);
      if (childs.length != this.guests.childrens.length) {
        if (childs.length > this.guests.childrens.length) {
          // childs.filter((x) => x !== null)
          for (let i = 0; i < childs.length; i++) {
            if (!this.guests.childrens[i]) {
              this.$store.commit("search/addChildrens", 10);
              this.saveLocalStorage();
            }
          }
        } else if (childs.length < this.guests.childrens.length) {
          for (let i = this.guests.childrens.length; i > childs.length; i--) {
            this.$store.commit("search/deleteChildrens", i - 1);
            this.saveLocalStorage();
          }
        }
      }
      if (val.age) {
        this.$store.commit("search/updateChildrens", {
          index: val.age.index,
          val: val.age.val,
        });
        this.saveLocalStorage();
      }
      this.updateChildrens(childs);
    },
    showSelect() {
      if (this.isBlockingClick) {
        this.$emit("click");
      } else {
        if (!this.isSelect) {
          this.$emit("click");
        }
        this.isSelect = !this.isSelect;
      }
      this.$emit("clickGuests");
    },
    saveLocalStorage() {
      const EXPIRED = "10y";

      const PAYLOAD = {
        guests: this.guests,
      };
    },
    close(event) {
      this.isSelect = false;
      const local = JSON.parse(JSON.stringify(this.selectGuests));
      local.childrens.splice(local.childrens.length - 1, 1);

      if (
        this.isChangeByClose &&
        JSON.stringify(local) !== JSON.stringify(this.guests)
      ) {
        this.sendToStore(this.selectGuests);
      }
    },
    childrensOpen(val, i) {
      this.isSelectOpen = val;
      if (!this.isDetail && val) {
        this.$nextTick(() => {
          setTimeout(() => {
            this.$refs.selectGuests.scrollTo({
              top: this.$refs[`guest_${i}`][0].$el.offsetTop - 15,
              behavior: "smooth",
            });
          }, 150);
        });
      }
    },
  },
  data() {
    return {
      isSelect: false,
      selectGuests: null,
      isSelectOpen: false,
    };
  },
};
</script>

<style lang="scss" scoped>
.guests {
  &__item {
    margin-top: 10px;
  }
}
.select-guests {
  position: relative;
  &--fade-enter-active,
  &--fade-leave-active {
    opacity: 1;
    transition: opacity 0.15s ease;
  }
  &--fade-enter,
  &--fade-leave-to {
    opacity: 0;
  }

  &__text {
    text-align: center;
    margin-top: 8px;
  }

  &__child {
    padding-left: 5px;
  }
  &__buttons {
    background: #fff;
    padding: 20px 0;
    position: sticky;
    bottom: 0;
    z-index: 20;
  }
  &--btn {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    cursor: pointer;
    padding: 12px 46px 12px 20px;
    margin: 0;
    outline-style: none;
    background: #fff;
    line-height: 1;
    color: #000;
    height: 60px;
    width: 100%;
    position: relative;
    white-space: nowrap;
    box-shadow: inset 0 0 0 1px #fff;
    transition: box-shadow 0.3s;
    border-radius: 4px;
    border: solid 1px var(--input-border);
    .title {
      font-size: 14px;
      margin-bottom: 4px;
    }
    .body {
      span {
        &.empty {
          color: #717171;
          padding-right: 10px;
        }
      }
    }
    &.active-simple {
      .ico-arrow {
        transform: translateY(-50%) scaleY(-1);
      }
    }
    .ico-arrow {
      background-repeat: no-repeat;
      background-size: contain;
      display: block;
      height: 14px;
      // margin-top: -6px;
      position: absolute;
      right: 14px;
      top: 50%;
      transform: translateY(-50%) scaleY(1);
      width: 14px;
      background-image: url("~assets/img/arrow-main.svg");
      transition: transform 0.3s;
    }
    &.active {
      box-shadow: inset 0 0 0 1px #f51449;
      .ico-arrow {
        //transform: rotate(180deg);
      }
    }
    &.page-inner {
      flex-direction: row;
      justify-content: flex-start;
    }
  }
  &--main {
    position: absolute;
    top: 100%;
    right: 0;
    z-index: 4;
    width: 300px;
    margin-top: 4px;
    background: #fff;
    border-radius: 8px;
    box-shadow: 0 0 12px 0 rgba(0, 0, 0, 0.15);
    padding: 25px 15px 0 15px;
    overflow: auto;
    max-height: 400px;
  }
  &.page-search {
    .select-guests--btn {
      height: 60px;
      flex-direction: row;
      justify-content: flex-start;
      min-width: 100%;
      transition: border 0.3s;
      &.select-guests_current {
        color: #000;
        border: 1px solid #000;
      }
    }
  }
  &.page-search,
  &.page-hotel {
    .select-guests--btn {
      height: 42px;
      border: 1px solid #000000;
      border-radius: 3px;
      outline: 0;
      box-shadow: none;
      background-color: transparent;
      display: flex;
      align-items: center;
      padding: 15px 20px;
    }
    .select-guests--main {
      margin-left: 0;
      box-shadow: 0 0 12px 0 rgba(0, 0, 0, 0.15);
      border-radius: 8px;
    }
  }
  &.page-hotel {
    margin-left: 20px;
    .select-guests--main {
      margin-top: 4px;
    }
    .select-guests--btn {
      height: 42px;
    }
  }
  &.page-detail {
    .select-guests--btn {
      height: 42px;
      border: 1px solid #d1d1d1;
      border-radius: 3px;
      font-size: 14px;
      padding: 12px 46px 12px 15px;
    }
    .select-guests--main {
      outline: 1px solid #d1d1d1;
      margin-left: 0;
      margin-top: 3px;
      left: auto;
      right: 0;
      width: 100%;
    }
  }
}
::-webkit-scrollbar {
  width: 10px;
  background: transparent;
}
::-webkit-scrollbar-track {
  // -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
  // border-radius: 10px;
}
::-webkit-scrollbar-thumb {
  width: 4px;
  background: transparent;
  // border-radius: 10px;

  border-radius: 3px;
  box-shadow: inset 0 0 0 2px grey;
  border: 3px solid transparent;
}
</style>
