<template>
  <label class="sc-checkbox" :class="{ 'sc-checkbox--disabled': disabled }" cy-checkbox>
    <input class="sc-checkbox--input" :disabled="disabled" type="checkbox" v-model="isChecked" :value="modelValue" />
    <span class="sc-checkbox--mark"></span>
    <span>
      <slot></slot>
    </span>
  </label>
</template>

<script>
export default {
  inheritAttrs: false,
  emits: ['update:modelValue'],
  props: {
    modelValue: {
      // type: [String, Number, Boolean, Object],
      default: true
    },
    checkedValue: {
      // type: [String, Number, Boolean, Object],
      default: true
    },
    uncheckedValue: {
      // type: [String, Number, Boolean, Object],
      default: false
    },
    checked: {
      // type: [String, Number, Boolean, Object],
      default: false
    },
    disabled: {
      type: Boolean,
    }
  },
  data() {
    return {
      isChecked: false
    };
  },
  mounted() {
    this.selected = this.modelValue;
    if (this.checkedValue == this.checked) {
      this.isChecked = true;
    } else if (this.uncheckedValue == this.checked) {
      this.isChecked = false;
    } else {
      this.isChecked = !!this.checked;
    }
  },
  watch: {
    isChecked() {
      this.$emit('update:modelValue', this.isChecked ? this.checkedValue : this.uncheckedValue);
    },
    checked(value) {
      if (this.checkedValue == value) {
        this.isChecked = true;
      } else if (this.uncheckedValue == value) {
        this.isChecked = false;
      } else {
        this.isChecked = !!value;
      }
    }
  }
};
</script>

<style lang="scss">
.sc-checkbox {
  font-size: 14px;
  line-height: 16px;
  color: #464646;
  vertical-align: top;
  min-height: 1.5rem;
  margin-bottom: 0px;
  position: relative;
  display: flex;
  input {
    display: none;
  }
  &--mark {
    display: inline-block;
    height: 15px;
    width: 15px;
    min-width: 15px;
    margin-right: 6px;
    position: relative;
    top: 1px;
    background-image: url("assets/unchecked.svg");
  }
  & input:checked ~ .sc-checkbox--mark {
    background-image: url("assets/checked.svg");
  }
  & input:active ~ .sc-checkbox--mark {
    background-image: url("assets/checked.svg");
    opacity: 0.5;
  }
  &--disabled {
    filter: contrast(0.4);
    opacity: 0.75;
  }
}
</style>
