<i18n>
{
	"ru": {
    "recommend": "Гости рекомендуют",
    "seeReviews": "Читать отзывы гостей"
	},
	"en": {
    "recommend": "Guest's choice",
    "seeReviews": "Read guest reviews"
	}
}
</i18n>

<template>
  <div
    class="card-map"
    :class="{ 'disabled-link': isDisabledLink }"
    @click="clickObjectWrap"
  >
    <div class="card-map__img">
      <Img
        :linkBronevik="linkBronevik"
        :isBronevik="isBronevik"
        :data="objectData"
      />
    </div>
    <Content
      :linkBronevik="linkBronevik"
      :isDisabledLink="isDisabledLink"
      :data="objectData"
      :show-detail="showDetail"
    />
    <Prices
      :linkBronevik="linkBronevik"
      :data="objectData"
      :show-detail="showDetail"
    />
    <ObjectDetail
      v-if="showDetail"
      :id="String(objectData.id)"
      :show-detail="showPage"
    />
    <LoadingPrice v-if="bronevikLoadingPrice" />
  </div>
</template>
<script>
import { mapState, mapGetters } from "vuex";
import Img from "./Img";
import Content from "./Content";
import Prices from "./Prices";
import ObjectDetail from "./ObjectDetail";
import LoadingPrice from "@/components/base/LoadingPrice.vue";

export default {
  name: "ObjectBlock",
  components: {
    Img,
    Content,
    Prices,
    ObjectDetail,
    LoadingPrice,
  },
  props: {
    objectData: {
      type: Object,
    },
    isBronevik: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      showDetail: false,
      hoveredTop: false,
      bronevikLoadingPrice: false,
    };
  },
  mounted() {
    this.$nextTick(() =>
      this.$emit("onRender", this.$el.getBoundingClientRect())
    );
  },
  computed: {
    ...mapGetters("search", ["occupied", "getQuery", "maxGuests"]),
    ...mapState("search", [
      "objectsBronevik",
      "pricesBronevik",
      "loadBronevikPrice",
    ]),
    conveniencesList() {
      if (!this.objectData.conveniences) return [];
      return Object.entries(this.objectData.conveniences)
        .filter(([key, value]) => value && key)
        .map(([key]) => key);
    },
    dataBronevik() {
      let idBronevik = this.objectsBronevik.find(
        (item) => item == this.objectData.id
      );
      if (idBronevik) {
        return this.pricesBronevik &&
          this.pricesBronevik[idBronevik] &&
          this.pricesBronevik[idBronevik]["prices"]
          ? this.pricesBronevik[idBronevik]["prices"]
          : [];
      } else {
        return [];
      }
    },
    linkBronevik() {
      if (this.dataBronevik.length) {
        return this.dataBronevik[0].link;
      } else {
        return "";
      }
    },
    isDisabledLink() {
      let idBronevik = this.objectsBronevik.find(
        (item) => item == this.objectData.id
      );
      if (idBronevik) {
        if (this.dataBronevik.length) {
          return false;
        } else {
          return true;
        }
      } else {
        return false;
      }
    },
  },
  methods: {
    showPage() {
      this.showDetail = !this.showDetail;
      document.body.style.overflow = "hidden";
      return this.showDetail;
    },
    mouseOver() {
      this.hoveredTop = true;
    },
    mouseOut() {
      this.hoveredTop = false;
    },
    clickObjectWrap(event) {
      if (this.isDisabledLink) {
        event.preventDefault();
        if (this.occupied) {
        }
      }
      if (this.loadBronevikPrice) {
        event.preventDefault();
        this.bronevikLoadingPrice = true;
      }
    },
  },
  watch: {
    objectData() {
      this.$nextTick(() =>
        this.$emit("onRender", this.$el.getBoundingClientRect())
      );
    },
    loadBronevikPrice(val) {
      if (!val) {
        if (this.bronevikLoadingPrice) {
          this.bronevikLoadingPrice = false;
        }
      }
    },
  },
};
</script>
<style lang="scss" scoped>
.card-map {
  position: absolute;
  z-index: 1;
  width: auto;
  border: none;
  margin: 0px 0 20px 0;
  display: grid;
  grid-template-areas:
    "carousel"
    "content"
    "prices";
  grid-template-rows: 180px auto auto;
  grid-template-columns: 270px;
  // animation: fadein 0.8s;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  overflow: hidden;
  // transition: top 0.5s, left 0.5s;

  @keyframes fadein {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }

  &__sticker {
    grid-area: top;
  }

  &__link {
    color: inherit;
  }
  &__sticker-text {
    background: #d8d8d8;
    border-radius: 2px 2px 0px 0px;
    font-size: 13px;
    line-height: 17px;
    padding: 3px 10px;
    color: inherit;
  }

  .card-map__img {
    grid-area: carousel;
  }
  .object-content {
    grid-area: content;
  }
  ::v-deep .object-prices {
    grid-area: prices;
    // display: flex;
    // flex-direction: column;
    // align-items: center;
  }
  &.disabled-link {
    a,
    ::v-deep .carousel > a {
      pointer-events: none;
    }
    a {
      cursor: default;
    }
    ::v-deep {
      .price-bronevik,
      .price-order {
        opacity: 0.7;
      }
    }
  }
}
</style>
