<i18n>
{
	"ru": {
		"calendarTitle": "Заезд — отъезд",
		"found": "нет вариантов|Показать {n} вариант|Показать {n} варианта|Показать {n} вариантов",
		"in": "Заезд",
		"btn": "Сохранить",
		"out": "Отъезд",
		"reset": "Сбросить даты"
	},
	"en": {
		"calendarTitle": "Choose dates",
		"found": "no variants|Show {n} variant|Show {n} variants|Show {n} variants",
		"in": "Check-in",
		"btn": "Save",
		"out": "Check-out",
		"reset": "Clear dates"
	}
}
</i18n>
<template>
  <BottomSheet v-model="modalRefCalendar" class="modal-warning" :title="`${showReset ? '' : $t('calendarTitle')}`">
    <template #side-action>
      <div v-show="showReset" class="main-datepicker-reset" @click="reset">
        {{ $t("reset") }}
      </div>
    </template>

    <div class="main-datepicker">
      <div class="main-datepicker-calendar">
        <CalendarMobile
          ref="CalendarRef"
          @choice="showReset = $event"
          v-model:dateViews="dateViews"
        />

        <transition name="main-datepicker-calendar--footer--fade">
          <div v-show="isSelectedDate" class="main-datepicker-calendar--footer">
            <button
              class="button button_size_md button_dark-gray button_w-100"
              @click="apply"
            >
              {{
                button
                  ? totalFound === null
                    ? button
                    : $t("found", { n: totalFound })
                  : $t("btn")
              }}
            </button>
          </div>
        </transition>
      </div>
    </div>
  </BottomSheet>
</template>
<script>
import CalendarMobile from "@/components/base/CalendarMobile.vue";
import Modal from "@sutochno/modal";
import BottomSheet from "@/components/base/BottomSheet";

import { HTTP } from "@/api/index.js";
import { mapState, mapGetters } from "vuex";
import { CancelToken } from "axios";
import _ from "lodash";

export default {
  inheritAttrs: false,
  name: "DatePicker",
  components: {
    Modal,
    BottomSheet,
    CalendarMobile
  },
  props: {
    isCounter: {
      default: false,
      type: Boolean,
    },
    button: {
      default: null,
      type: String,
    },
    isAnimateModal: {
        default: true,
        type: Boolean,
    },
  },
  watch: {
    modalRefCalendar(val) {
      if (!val) {
        this.closeModal();
      }
    },
    dateViews: {
      deep: true,
      handler() {
        if (!this.occupied || !this.isCounter) {
          this.totalFound = null;
          return;
        }
        if (this.cancelSource) {
          this.cancelSource.cancel("Operation canceled by the user.");
        }
        this.cancelSource = CancelToken.source();
        HTTP.get("rest/search/getTotalFound", {
          params: {
            ..._.omit(this.filters, "id", "type"),
            max_guests: this.maxGuests,
            location_id: this.location.id,
            location_type: this.location.type,
            occupied: this.occupied,
          },
          cancelToken: this.cancelSource.token,
        })
          .then((response) => {
            this.totalFound = response.data.data.total_found;
          })
          .catch((error) => {
            console.log("Ajax error: ", error);
          });
      },
    },
    isSelectedDate(value) {
      this.$emit("choice", value);
    },
  },
  methods: {
    show() {
      this.modalRefCalendar = true;
    },
    format(date) {
      return date.toLocaleString("ru-RU", {
        day: "numeric",
        month: "long",
        year: "numeric",
      });
    },
    apply() {
      this.modalRefCalendar = false;
      this.$emit("update:modelValue", {
        in: this.checkIn,
        out: this.checkOut
      });
    },
    reset() {
      this.$refs.CalendarRef.reset();
      this.setOccupied();
      this.$emit("update:modelValue", {
        in: this.checkIn,
        out: this.checkOut
      });
    },
    setOccupied() {
      // Нужно хранить нормальную дату, а не калечную
      this.$store.commit("search/setCheckIn", null);
      this.$store.commit("search/setCheckOut", null);
    },
    closeModal() {
      this.$emit("update:modelValue", {
        in: this.checkIn,
        out: this.checkOut
      });
    }
  },
  computed: {
    ...mapGetters("search", ["occupied", "maxGuests"]),
    ...mapState("search", ["filters", "location", "checkIn", "checkOut"]),
    isSelectedDate() {
      return this.dateViews.in && this.dateViews.out
    }
  },
  data() {
    return {
      modalRefCalendar: false,
      dateViews: {
        in: null,
        out: null
      },
      totalFound: null,
      showReset: false
    };
  },
};
</script>
<style lang="scss" scoped>
.main-datepicker-calendar--footer--fade-enter-active,
.main-datepicker-calendar--footer--fade-leave-active {
  transform: translateY(0%);
  transition: transform 0.15s ease;
}
.main-datepicker-calendar--footer--fade-enter,
.main-datepicker-calendar--footer--fade-leave-to {
  transform: translateY(100%);
}
.main-datepicker {
  color: black;
  &-reset {
    position: absolute;
    top: 0px;
    right: 10px;
    color: white;
    z-index: 201;
  }
  &-input {
    color: #717171;
    position: sticky;
    top: 0px;
    z-index: 201;
    background: white;
    &--in,
    &--out {
      text-align: center;
      display: inline-block;
      width: 50%;
      box-sizing: border-box;
      padding: 12px 0;
      border: 1px solid #d8d8d8;
      &__fill {
        text-transform: capitalize;
        color: black;
      }
    }
    &--out {
      border-left-width: 0;
    }
  }
  &-calendar {
    &--footer {
      position: fixed;
      bottom: 0;
      left: 0;
      right: 0;

      // position: sticky;
      // bottom: 0;
      padding: 15px 20px calc(15px + env(safe-area-inset-bottom));
      background-color: white;
      box-shadow: 0 0px 4px 0px #757575;
    }
  }
}
</style>
