export default function (Component) {
  Component.__i18n = Component.__i18n || []
  Component.__i18n.push({
    "locale": "",
    "resource": {
      "ru": {
        "title": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Выберите даты заезда и отъезда"])},
        "text": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Доступность жилья и стоимость зависят от дат проживания."])}
      },
      "en": {
        "title": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Select arrival and departure dates"])},
        "text": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Availability and cost depend on dates of stay."])}
      }
    }
  })
}
