<i18n>
{
	"ru": {
		"onNoMore": "К сожалению больше объектов нет",
    "onNoResults": "К сожалению ничего не найдено",
    "find": "Найдено"
	},
	"en": {
		"onNoMore": "Unfortunately there are no more objects",
    "onNoResults": "Sorry, nothing was found",
    "find": "Found"
	}
}
</i18n>
<template>
  <div class="card-list" :class="{ 'card-list_desktop': isDesktop }">
    <div v-if="results" class="card-list__wrapper">
      <template v-for="(list, val) in results" :key="val">
        <template :key="index" v-for="(item, index) in list">
          <div class="card-list__item" @mouseenter="onEnter(item.id)" @mouseleave="onLeave">
            <ObjectBlock
              :key="item.id"
              :class="{
                'card--selected': item.id === selectedMapPointId,
                'card--selected': item.id === hoverMapPointId,
              }"
              :ref="`object_${item.hotel && item.hotel.id ? item.hotel.id : item.id}`"
              :object-data="item"
              :rating="rating(item)"
              :isBronevik="
                item.owner
                && item.owner.id
                && item.owner.id === 4784337
                  ? true : false
              "
            />
          </div>
        </template>
      </template>
    </div>
  </div>
</template>
<script>
import ObjectBlock from "./ObjectBlock.vue";
import { mapState } from "vuex";
import { Messages } from "@/api/index.js";
import _ from "lodash";

export default {
  name: "ObjectsList",
  components: {
    ObjectBlock,
  },
  props: {
    results: {
      type: Array,
    },
    adblock: {
      type: Number,
      default: 0,
    },
    isDesktop: {
      type: Boolean,
      default: false
    },
    scrollStop: {
      type: Boolean,
      default: false
    },
    page: {
      type: Number,
      default: 1
    }
  },
  data() {
    return {
      avgReviewsRatings: []
    }
  },
  async created () {
    await this.getAvgReviewsRatings();
  },
  methods: {
    async getAvgReviewsRatings() {
      await Messages.getAvgReviewsRatings(this.objectIds)
        .then((res) => {
          this.avgReviewsRatings = res.data.data.objects;
        })
    },
    onEnter(ID) {
      this.$emit("hoverObject", ID);
    },
    onLeave() {
      this.$emit("hoverObject", null);
    },
    rating (item) {
      if (this.avgReviewsRatings) {
        let i = this.avgReviewsRatings.find(rate => rate.object_id == item.id);
        return i ?? null;
      }
      return null;
    }
  },
  computed: {
    ...mapState("search", [
      "selectedMapPointId",
      "hoverMapPointId",
      "totalFound",
    ]),
    ...mapState("detailBooking", ["isChangeForSave", "isChangeForSaveGuests"]),
    totalFoundText () {
      return `${this.$t("find")} ${this.totalFound}`;
    },
    objectIds() {
      const objectIds = [];
      Object.entries(this.results).forEach(list => {
        list[1].map((item) => {
          objectIds.push(Number(item.id));
          return item;
        });
      });
      return objectIds;
    },
  },
  watch: {
    results(val) {
      if (val) {
        this.getAvgReviewsRatings();
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.card-list__item {
  width: 100%;
}
.card-list {
  width: 100%;
  padding: 0 20px;

  &_desktop {
    padding: 0px;
  }
  &__options {
    height: 30px;
    display: none;
    align-items: center;
    @media screen and (max-width: 1459px) {
      display: flex;
    }
  }
  &__total-found {
    color: #7f7f7f;
    font-size: 14px;
    line-height: 18px;
  }
  &__wrapper .card_recommended:first-child {
    margin: 40px 0 20px 0;
  }
  &__wrapper {
    @media screen and (min-width: 768px) {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
    }
  }
}
</style>
