<i18n>
{
	"ru": {
		"showGoldPartnerLabelAbility": "Золотой партнер",
		"showSilverPartnerLabelAbility": "Серебряный партнер",
    "superhost": "Суперхозяин",
    "favoriteAdd": "Добавить в избранное",
    "favoriteRemove": "Убрать из избранного",
    "meter": "м<sup><small>2</small></sup>",
    "recommended": "Гости рекомендуют",
    "cashback": "30% Кэшбэк"
	},
	"en": {
		"showGoldPartnerLabelAbility": "Gold partner",
		"showSilverPartnerLabelAbility": "Silver partner",
    "superhost": "Superhost",
    "favoriteAdd": "Add to favorites",
    "favoriteRemove": "Remove from favorites",
    "meter": "m<sup><small>2</small></sup>",
    "recommended": "Guest's choice",
    "cashback": "30% Cashback"
	}
}
</i18n>

<template>
  <div class="carousel">
    <a
      :href="{
        id: data.id,
        occupied: occupied,
        guests_adults: guests.adults,
        guests_childrens: guests.childrens.join(','),
        id: getQuery.query.id,
        type: getQuery.query.type,
        term: getQuery.query.term,
      }"
      target="_blank"
      @mousedown="clickObjectBronevik($event, linkBronevik)"
    >
      <SliderLight :media="data.media || []" :arrows="true" :dots="true" />
    </a>
  </div>
</template>

<script>
import SliderLight from "@sutochno/sliderlight";
import Rating from "@/components/base/BaseRating";
import { mapState, mapGetters } from "vuex";
export default {
  components: {
    SliderLight,
    Rating,
  },
  props: {
    data: {
      type: Object,
    },
    linkBronevik: {
      type: String,
      default: "",
    },
    isBronevik: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    setFavorite(payload) {
      this.isFavorite = payload;
    },
    eventCloseCard() {
      this.$store.commit("search/setCardVisible", false);
      this.$store.commit("search/setSelectedMapPointId", null);
    },
    clickObjectBronevik(event, link) {
      if (link || (link && event.which == 1) || (link && event.which == 2)) {
        event.preventDefault();
        window.open(link);
      }
    },
  },
  computed: {
    ...mapState("search", ["guests"]),
    ...mapGetters("search", ["occupied", "getQuery"]),
    partnerLabel() {
      return this.data.owner.permissions.find(
        (v) =>
          v == "showGoldPartnerLabelAbility" ||
          v == "showSilverPartnerLabelAbility"
      );
    },
    message() {
      return this.isFavorite === true
        ? this.$t("favoriteRemove")
        : this.$t("favoriteAdd");
    },
  },
  data() {
    return {
      page: 1,
      isFavorite: this.data.favorite,
    };
  },
};
</script>

<style lang="scss" scoped>
.carousel {
  background: #fff;
  position: relative;
  height: 100%;
  z-index: 0;
  > a {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 2;
  }
  &:hover {
    ::v-deep .sc-sliderlight__arrows.arrows {
      opacity: 1;
      button {
        z-index: 100;
      }
    }
  }
  img {
    height: 100%;
    width: 100%;
    max-width: 100%;
    object-fit: cover;
  }
  :deep(.VueCarousel) {
    // height: 100%;
    .VueCarousel-slide {
      height: inherit;
    }
    .VueCarousel-inner {
      max-height: 100%;
      width: 100%;
      max-width: 100%;
      object-fit: cover;
    }
  }

  &:hover
    :deep(.VueCarousel-navigation-button.VueCarousel-navigation-next::before) {
    right: 0;
    margin: 0 0 0 auto;
    background-position: -44px 0;
  }
  &:hover :deep(.VueCarousel-navigation-button::before) {
    display: block;
    content: "";
    background-image: url("~assets/img/spr_ss_arrows.png");
    width: 44px;
    height: 44px;
  }
  & :deep(.VueCarousel-navigation-button) {
    outline: none !important;
    top: 0;
    bottom: 0;
    margin: 0;
    width: 50%;
    transform: translateY(0) translateX(0%);
  }

  &__size {
    position: absolute;
    bottom: 10px;
    left: 10px;
    z-index: 2;

    border: 1px solid #fff;
    border-radius: 2px;
    color: #fff;
    padding: 2px 8px;
    font-size: 12px;
    line-height: 16px;
  }

  .close {
    cursor: pointer;
    position: absolute;
    right: 15px;
    top: 15px;
    width: 15px;
    height: 15px;
    z-index: 2;
  }
  .close:hover {
    opacity: 1;
  }
  .close:before,
  .close:after {
    position: absolute;
    left: 7px;
    content: " ";
    height: 15px;
    width: 2px;
    background-color: #fff;
  }
  .close:before {
    transform: rotate(45deg);
  }
  .close:after {
    transform: rotate(-45deg);
  }
}

.object-info {
  z-index: 99;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin: 0 9px;
  position: absolute;
  top: -8px;
  &__item:last-child {
    margin-right: 0;
  }
  &__superhost {
    background-color: #ffa900;
  }
  &__recommended {
    background-color: #0659ff;
  }
  &__cashback {
    background-color: #f51449;
    .shadow-cashback {
      color: #fff;
      font-size: 9px;
      padding: 0;
      span {
        line-height: 18px;
      }
    }
  }
  &__item {
    width: auto;
    height: 18px;
    border-radius: 9px;
    color: #fff;
    font-size: 9px;
    line-height: 18px;
    margin-right: 10px;
    padding: 0 5px 0 5px;
    position: relative;
    text-align: center;
    cursor: default;
  }
}

//фикс бага свайпа слайдера на карте
::v-deep.sc-sliderlight {
  pointer-events: none;

  .arrows__btn-wrapp--left {
    pointer-events: auto;
  }
  .arrows__btn-wrapp--right {
    pointer-events: auto;
  }
}
</style>
