export default function (Component) {
  Component.__i18n = Component.__i18n || []
  Component.__i18n.push({
    "locale": "",
    "resource": {
      "ru": {
        "onNoMore": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["К сожалению больше объектов нет"])},
        "onNoResults": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["К сожалению ничего не найдено"])},
        "find": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Найдено"])},
        "button": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Выберите даты"])}
      },
      "en": {
        "onNoMore": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Unfortunately there are no more objects"])},
        "onNoResults": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Sorry, nothing was found"])},
        "find": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Found"])},
        "button": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Select dates"])}
      }
    }
  })
}
