<template>
  <div class="block-filter" :class="viewWidget">
    <div class="block-filter--more-btn">
      <button @click="showFilter = !showFilter">
        <img src="~assets/img/icon-settings-filter.svg" alt="">
        {{ filterBtn }}
      </button>
      <div>
        <div v-if="viewWidget !== 'adaptive'" class="block-filter--price-title">Цена</div>
        <template v-if="viewWidget === 'adaptive'">
          <BottomSheet v-model="showFilter" class="modal-warning" title="Фильтры">
            <FilterMain
              ref="refFilters"
              v-model="filters"
              @clearFilters="clearFilters"
              :view-filters="isArmorClick"
            />
            <div class="block-filter--btn">
              <div>
                <button v-show="Object.keys(filters).length" class="btn-reset" @click="clearFilters">Сбросить</button>
              </div>
              <button class="btn" @click="applyFilters()">Показать варианты</button>
            </div>
          </BottomSheet>
        </template>
        <template v-else>
          <div v-if="showFilter" class="block-filter--modal">
            <FilterMain
              ref="refFilters"
              v-model="filters"
              @clearFilters="clearFilters"
              :view-filters="isArmorClick"
            />
            <div class="block-filter--btn">
              <div>
                <button v-show="Object.keys(filters).length" class="btn-reset" @click="clearFilters">Сбросить</button>
              </div>
              <button class="btn" @click="applyFilters()">Показать варианты</button>
            </div>
          </div>
        </template>        
      </div>
    </div>
    <div class="price-wrap">
      <div v-if="viewWidget === 'adaptive'" class="price-wrap--title">Цена</div>
      <FilterPrice @switching="setPriceFilter($event)" />
    </div>
  </div>
</template>

<script>
import FilterMain from "@/components/Search/Filter/FilterMain";
import FilterPrice from "@/components/Search/Filter/FilterPrice";
import BottomSheet from "@/components/base/BottomSheet";
import { mapState, mapGetters, mapMutations, mapActions } from "vuex";

export default {
  name: "Filter",
  components: {
    FilterMain,
    FilterPrice,
    BottomSheet,
  },
  data() {
    return {
      filters: {},
      cancelSource: null,
      modalDown: false,
      modalDownScroll: false,
      isVisibleModal: false,
      showFilter: false,
      currency: {
        id: 1,
        title: "RUB"
      }
    }
  },
  watch: {
    filters: {
      deep: true,
      handler(value) {
        this.$store.commit(
          "search/setFiltersMobile",
          JSON.parse(JSON.stringify(value))
        );
      }
    },
  },
  computed: {
    ...mapState("search", ["location", "bound", "isLoadFilter", "viewWidget"]),
    ...mapState("search", { storeFilters: "filters", totalFound: "filtersTotalFound" }),
    ...mapGetters("search", ["occupied", "maxGuests", "days"]),
    filterBtn() {
      return this.viewWidget === 'adaptive' ? 'Фильтры' : 'Ещё фильтры';
    },
    countFilters() {
      let count = Object.keys(
        _.omit(
          this.storeFilters,
          "id",
          "type",
          "max_guests",
          "occupied",
          "lat",
          "lng",
          "zoom",
          "modal-calendar",
          "modal-guests",
          "modal-filter",
        )
      ).length;
      return count;
    },
    filtersTitle() {
      return this.countFilters > 0
        ? this.$t("name") + ` (${this.countFilters})`
        : this.$t("name");
    },
  },
  methods: {
    ...mapMutations("search", ["setFiltersView", "setSelectedFilters"]),
    setPriceFilter(evt) {
      if (evt.price_per !== null) {
        let pricePer = evt.price_per === true ? 2 : 1;
        this.filters["price_per"] = pricePer;
      }
      if (evt.price[0] === null && evt.price[1] === null) {
        delete this.filters["price"];
      } else {
        this.filters["price"] = evt.price;
      }
    },
    async clearFilters() {
      this.setSelectedFilters([]);
      this.$store.commit("search/setFiltersMobile", {});
      this.$refs.refFilters.reset();
    },
    applyFilters() {
      //reset filter to default values before
      // this.$store.commit("search/setFilters");
      this.$store.commit(
        "search/setFiltersMobile",
        JSON.parse(JSON.stringify(this.filters))
      );
      this.$refs.refFilters.queryObject(true);
      this.showFilter = false;
    },
    showFilters() {
      // this.showFiltersBlock = true;
      this.$refs.modalRef.show();
    },
    hideFilters() {
      // this.showFiltersBlock = false;
      this.$refs.modalRef.close();
    },
    modalDownMethod(val) {
      // хак для ios, т.к. модалки перекрывают друг друга
      if (val == "down") {
        this.modalDown = true;
      } else if (val == "up") {
        this.modalDown = false;
      }
    },
    triol(val) {
      return val ? val.toLocaleString("ru", { useGrouping: true }) : 0;
    },
  },
}
</script>

<style lang="scss" scoped>
.block-filter {
  display: grid;
  grid-template-columns: 274px 274px;
  grid-template-areas: "more-btn price";
  column-gap: 10px;
  position: relative;
  &.adaptive {
    grid-template-columns: 1fr;
    grid-template-areas:
      "price"
      "more-btn";
  }
  &--more-btn {
    grid-area: more-btn;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 14px;
    color: var(--black);
    > button {
      cursor: pointer;
      border-radius: 4px;
      background: #f1f0ed;
      font-size: 14px;
      height: 36px;
      padding: 0 15px;
      margin: 0;
      border: none;
      outline-style: none;
      display: flex;
      align-items: center;
      justify-content: center;
      img {
        margin-right: 10px;
        margin-top: 1px;
      }
      .adaptive & {
        width: 100%;
      }
    }
  }
  &--modal {
    position: absolute;
    top: 100%;
    left: 0;
    margin-top: 4px;
    border-radius: 8px;
    box-shadow: 0 0 12px 0 rgba(0, 0, 0, 0.15);
    background-color: var(--white);
    width: 350px;
    padding: 14px 0 20px;
    z-index: 4;
    ul {
      list-style: none;
      padding: 0;
      margin: 0 0 18px;
      li {
        .sc-checkbox {
          padding: 8px 20px;
        }
        &:hover {
          background: #f8f7f5;
        }
      }
    }
  }
  &--btn {
    display: flex;
    justify-content: space-between;
    padding: 0 20px;
    .btn-reset {
      cursor: pointer;
      border: none;
      background: transparent;
      outline-style: none;
      font-size: 14px;
      color: #2d6cb4;
      padding: 0;
      margin: 0;
    }
    .btn {
      cursor: pointer;
      border: 1px solid #444444;
      background: transparent;
      outline-style: none;
      font-size: 14px;
      color: #000;
      height: 35px;
      line-height: 35px;
      white-space: nowrap;
      border-radius: 3px;
      padding: 0 20px;
      margin: 0;
    }
  }
}
.price-wrap {
  display: flex;
  align-items: center;
  width: 100%;
  .adaptive & {
    margin-bottom: 20px;
  }
  &--title {
    font-size: 14px;
    color: #000000;
    margin-right: 10px;
  }
}
</style>
