
<template>
  <div :id="id || `yandex-ads-${blockid}`" :blockid="blockid"></div>
</template>

<script>
import YandexAds from './YandexAdsClass';
export default {
  props: {
    blockid: {
      type: String,
      required: false
    },
    id: {
      type: String,
      required: false
    },
  },
  mounted() {
    this.$nextTick(()=> {
      new YandexAds(this.blockid,  this.id || `yandex-ads-${this.blockid}`)
    })
  },
}
</script>