<i18n>
{
	"ru": {
		"label": "Курорт, город или адрес"
	},
	"en": {
	}
}
</i18n>
<template>
  <div class="suggest">
    <div v-click-outside.mousedown="clickOutside" class="suggest-wrapper">
      <div class="suggest-inner">
        <input
          ref="input"
          class="suggest-input"
          :placeholder="$t('label')"
          :class="{ 'suggest-input--fill': query && query.length }"
          autocomplete="off"
          type="text"
          name="suggest"
          :value="query"
          @input="(evt) => (query = evt.target.value)"
        />
        <div class="suggest-close icon-app-cross-min" @click="onClear"></div>
        <div class="suggest-icon" @click="onClear"></div>
      </div>
    </div>
    <List
      class="suggest-list"
      :is-modal="true"
      :value="query"
      :is-complete="false"
      @choice="onChoice"
    />
    <ModalReload :show="listSuggestions.isError" />
  </div>
</template>
<script>
import List from "./List";
import { mapState } from "vuex";
import ModalReload from "./ModalReload";

export default {
  name: "BaseSuggestionsSearchModal",
  components: {
    List,
    ModalReload,
  },
  data() {
    return {
      query: "",
      focused: false,
    };
  },
  mounted() {
    // this.query = this.term;
  },
  computed: {
    ...mapState("search", ["term", "listSuggestions"]),
  },
  watch: {
    term: {
      deep: true,
      handler(term) {
        if (this.query != term) {
          this.query = term;
          this.$nextTick(() => {
            this.focused = false;
          });
        }
      },
    },
  },
  methods: {
    clickOutside() {
      this.focused = false;
    },
    onChoice(variant) {
      this.query = variant.title;
      this.$nextTick(() => {
        this.$refs.input?.blur();
        this.focused = false;
      });
      this.$emit("choice");
    },
    onClear() {
      this.query = "";
      this.$refs.input.focus();
    },
  },
};
</script>

<style lang="scss" scoped>
.suggest {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-height: calc(100vh - 60px);

  &-wrapper {
    background-color: #f6f6f6;
    padding: 20px;
  }
  &-inner {
    position: relative;
  }
  &-icon {
    position: absolute;
    left: 17px;
    top: 50%;
    transform: translateY(-50%);
    z-index: 1;
    background-image: url("~assets/img/search.svg");
    height: 16px;
    width: 16px;
  }
  &-input {
    padding: 0 20px 0 50px;
    width: 100%;
    height: 42px;
    background: transparent;
    font-size: 14px;
    border-radius: 3px;
    border: 1px solid #d1d1d1;
    box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.2);
    background-color: #fff;
    transition: box-shadow 0.3s ease-in;
    &:focus {
      outline: none;
      box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.2);
    }
  }
  &-close {
    position: absolute;
    right: 0px;
    top: 50%;
    font-size: 16px;
    padding: 10px;
    transform: translateY(-50%);
    cursor: pointer;
    opacity: 0;
  }
  &-input--fill ~ &-close {
    opacity: 1;
  }
  &-list {
    max-height: initial;
    flex-grow: 1;
    position: static;
    box-shadow: none;
    margin-top: 5px;
    // right: 0;
    width: 100%;
    &.suggestions-list--wrapper {
      font-size: 14px;
    }
    ::v-deep .suggestions-list {
      &-elem--title {
        text-overflow: none;
        text-overflow: initial;
        overflow: initial;
      }
      &-title {
        padding-bottom: 10px;
        margin-bottom: 0;
      }
    }
  }
}
</style>
