    align-items: center;
    flex-direction: column;
    padding: 0 20px;
  }
}
.loading {
  &:before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0.8;
    background-color: #f6f6f6;
    z-index: 1;
  }
  &:after {
    width: 25px;
    height: 25px;
    border: 3px solid #d8d8d8;
    border-top-color: #f51449;
  }
  &:after {
    content: "";
    border-radius: 50%;
    -webkit-animation: spin 1s infinite linear;
    animation: spin 1s infinite linear;
    position: absolute;
    left: 50%;
    margin-left: -12.5px;
    top: 50%;
    margin-top: -12.5px;
    z-index: 2;
  }

  @keyframes spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  &_full {
    &:before {
      opacity: 1;
    }
  }
  &_primary {
    &:before {
      background: #fff;
    }
  }
}
</style>
