<i18n>
{
	"ru": {
    "title": "В этой области нет вариантов",
    "text": "Сдвиньте карту или сбросьте фильры, чтобы увидеть предложения.",
    "resetAllFilters": "Показать варианты"
	}
}
</i18n>

<template>
  <div class="warn-map">
    <div class="warn-map--title">{{ $t("title") }}</div>
    <div class="warn-map--text">{{ $t("text") }}</div>
    <button
      class="btn btn-reset clear-filters btn-object-more"
      @click="clearFilters()"
      >{{ $t("resetAllFilters") }}</button
    >
  </div>
</template>
<script>

import { mapState } from "vuex";


export default {
  name: "WarnMap",
  computed: {
    ...mapState("search", ["location"]),
  },
  methods: {
    clearFilters() {
      this.$store.commit("search/setFilters");
      // Востанавливаем BBOX локации
      this.$store.commit("search/setBound", this.location.bbox);
    },
  },
};
</script>
<style lang="scss" scoped>
.warn-map {
  position: absolute;
  z-index: 1;
  bottom: 50px;
  left: 20px;
  width: 100%;
  width: calc(100% - 40px);
  border-radius: 3px;
  background-color: #ffffff;
  text-align: center;
  padding: 10px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
  &--title {
    color: #000;
    font-size: 16px;
  }
  &--text {
    color: #444444;
    font-size: 14px;
    margin-bottom: 10px;
    line-height: normal;
  }
}
</style>
