<i18n>
  {
  "ru": {
    "so_discount_today":"со скидкой сегодня",
    "cntReviews": "0|{n} отзыв|{n} отзыва|{n} отзывов",
    "rating": "о проживании со средней оценкой {n} из 5",
    "noRating": "Пока еще нет отзывов",
    "fastBooking": "Быстрое бронирование",
    "answerQuickly": "Быстро отвечает",
    "answerAlways": "Всегда отвечает",
    "online": "Сейчас онлайн",
    "clear": "Идеальная чистота",
    "infrastructure": "Удобное расположение",
    "accuracy": "Гостеприимный хозяин",
    "profitable": "Выгодный вариант",
    "freeCancellation": "Бесплатная отмена",
    "recommended": "Гости рекомендуют"
  },
  "en": {
    "so_discount_today":"discount today",
    "cntReviews": "0|{n} review |{n} reviews |{n} reviews",
    "rating": "about a stay with an average rating of {n} out of 5",
    "noRating": "No reviews yet",
    "fastBooking": "Fast booking",
    "answerQuickly": "Responds quickly",
    "answerAlways": "Always answers",
    "online": "Online now",
    "clear": "Perfect cleanliness",
    "freeCancellation": "Free Cancellation",
    "infrastructure": "Convenient location",
    "accuracy": "Hospitable host",
    "profitable": "Profitable option",
    "freeCancellation": "Free Cancellation",
    "recommended": "Guest's choice"
  }
  }
</i18n>
<template>
  <a
    ref="link"
    class="card-prices"
    target="_blank"
    :href="href"
    @mousedown="mousedownComponent"
  >
    <div class="card-prices__top">
      <a :href="`&showComments=1`" target="_blank">
        <Rating
          v-if="isRating"
          class="card-prices__rating"
          :data-rating="dataRating"
          :count-reviews="countReviews"
          :view-count="true"
          viewPort="desktop"
          @mouseover="hoverAvgRating()"
          @mouseleave="viewAvgRating = false"
          color="#F51449"
          :is-external-rating="isExternalRating"
        >
          <transition name="fade">
            <AvgRating
              v-if="viewAvgRating"
              :id="Number(data.id)"
              :rating="rating"
              :is-external-rating="isExternalRating"
              color="#F51449"
              :count-reviews="countReviews"
              :data-rating="dataRating"
            />
          </transition>
        </Rating>
      </a>
      <transition-group name="fade" tag="div" class="card-prices__primary" v-if="!isBronevik">
        <template v-if="!hideOwnerPopularity && rating && rating.count >= 2">
          <div class="primary__item" v-for="item in primary" :key="item.name">
            <span class="primary__bolder">{{ item.value }}</span>
            <span class="primary__defis">-</span>
            <span>{{ item.text }}</span>
          </div>
        </template>
      </transition-group>
      <div class="guests-recommend" v-if="data.is_recommended && !hideOwnerPopularity">
        <div class="guests-recommend__icon">
          <img src="~/assets/img/icon/finger-like.svg" alt="finger-like" class="guests-recommend__img">
        </div>
        <span class="guests-recommend__text">
          {{ $t('recommended') }}
        </span>
      </div>
    </div>
    <div class="card-prices__bottom" v-if="!isBronevik">
      <transition name="fade" tag="div">
        <div class="card-prices__info" v-if="data.booking_now">
          <span class="icon-app-lightning" />
          <span class="info__text">{{ $t('fastBooking') }}</span>
        </div>
        <template v-else-if="!hideOwnerPopularity && user">
          <div class="card-prices__info" v-if="user.answer_quickly">
            <span class="card-prices__icon-answer-quickly"/>
            <span class="info__text">{{ $t('answerQuickly') }}</span>
          </div>
          <div class="card-prices__info" v-else-if="user.answer_always">
            <span class="card-prices__icon-answer-always"/>
            <span class="info__text">{{ $t('answerAlways') }}</span>
          </div>
          <div class="card-prices__info" v-else-if="user.online">
            <span class="card-prices__icon-online"/>
            <span class="info__text">{{ $t('online') }}</span>
          </div>
        </template>
      </transition>
      <transition name="fade" tag="div">
        <div
          v-if="freeCancellation"
          class="card-prices__free-cancellation"
        >
          <span>{{ $t('freeCancellation') }}</span>
        </div>
      </transition>
      <BasePriceBronevik v-if="priceBronevik" :priceBronevik="priceBronevik" textAlign="right" />
      <PriceOrder v-else class="card-prices__price" :data="data" />
    </div>
    <div v-if="isBronevik" class="loading-price-bronevik">
      <img src="~/assets/img/loader-bronevik-gray.gif" alt="" />
    </div>
  </a>
</template>

<script>
import PriceOrder from "./PriceOrder";
import BaseButtonOrder from "@/components/base/BaseButtonOrder.vue";
import Rating from "@/components/base/BaseRatingSearch";
import AvgRating from "./AvgRating";
import { mapState, mapGetters, mapMutations } from "vuex";
import _ from 'lodash';
import BasePriceBronevik from "@/components/base/BasePriceBronevik";

export default {
  name: "ObjectBlockPrices",
  components: {
    PriceOrder,
    BaseButtonOrder,
    Rating,
    AvgRating,
    BasePriceBronevik,
  },
  props: {
    data: {
      type: Object,
    },
    showDetail: {
      required: true,
    },
    user: {
      type: Object
    },
    rating: {
      type: Object
    },
    hideOwnerPopularity: {
      type: Boolean,
      default: false
    },
    flag: {
      default: null
    },
    linkBronevik: {
      type: String,
      default: ''
    },
  },
  emits: ["mousedown"],
  data() {
    return {
      viewAvgRating: false,
    };
  },
  watch: {
    rating(val) {
      if(val) {
        // this.sendGa()
        // this.sendGaPrimary()
      }
    }
  },
  computed: {
    ...mapState("search", ["guests", "checkIn", "loadBronevikPrice", "objectsBronevik", "pricesBronevik"]),
    ...mapGetters("search", ["occupied", "getQuery"]),
    freeCancellation() {
      if (this.data.hot_sale && Number(this.data.hot_sale.price_per_night) > 0) {
        return false
      }
      if (this.data.conveniences.fc) {
        return true
      } else {
        return false
      }
    },
    countReviews() {
      const countReviews = this.data.hotel
        ? this.rating?.count
        : this.data.count_reviews;
      return countReviews + this.countReviewsExternal
    },
    profitable() {
      return this.rating?.ratings?.price_quality > 4
    },
    hotSalePrice() {
      return this.data.hot_sale &&
        Number(this.data.hot_sale.price_per_night) > 0
        ? Number(this.data.hot_sale.price_per_night)
        : null;
    },
    whatView() {
      if(this.hotSalePrice)
        return 'hot_sale'
      else if(this.profitable)
        return 'profitable'
      else
        return null
    },
    discount() {
      if (this.data.price != this.data.price_without_hot) {
        const discount = 1 - this.data.price / this.data.price_without_hot;
        if (discount) return discount;
      }
      return 0;
    },
    primary() {

      if(this.rating?.ratings){
        const arrPrim = [
          "clear",
          "infrastructure",
          "service",
          "accuracy"
        ]
        const objPrim = Object.entries(_.omit(this.rating.ratings, ["avg_rating"]))
          .reduce((acc, [key, value], i, arr) => {
            if(
              (key === 'clear')
              || (key === 'infrastructure')
              || (key === 'accuracy')
              || (key === 'service')
            ) {
              acc[key] = value * 2;
            }
            return acc
          }, {});

        return Object.entries(objPrim)
          .reduce((acc, [key, value], i, arr) => {
            if(
              (key === 'clear' && value >= 9)
              || (key === 'infrastructure' && value >= 8)
              || (key === 'accuracy' && ((value + objPrim.service) / 2) > 8)
            ) {
              acc.push({
                name: key,
                value: key === 'accuracy'
                  ? ((value + objPrim.service) / 2).toFixed(1).toLocaleString().replace('.',',')
                  : value.toFixed(1).toLocaleString().replace('.',','),
                text: `${this.$t(key)}`
              })
            }
            return acc
          }, [])
          .sort((a, b) => (a.value > b.value) ? -1 : 1)
          .filter((item, i) => i < 2)
      }
      else
        return []
    },
    priceBronevik() {
      let idBronevik = this.objectsBronevik.find(item => item == this.data.id)
      if (idBronevik) {
        return (this.pricesBronevik && this.pricesBronevik[idBronevik] && this.pricesBronevik[idBronevik]['price']) ? this.pricesBronevik[idBronevik]['price'] : 0
      } else {
        return 0
      }
    },
    isBronevik() {
      if (this.objectsBronevik.findIndex(item => item == this.data.id) == -1 || this.linkBronevik) {
        return false
      } else {
        if (this.loadBronevikPrice || !this.occupied) {
          return true
        } else {
          return false
        }
      }
    },
    maxRatingExternal() {
      const externalReviews = this.data.external_reviews
      if (externalReviews) {
        const ratingAirbnb = externalReviews[0]["rating"] ? externalReviews[0]["rating"] : 0
        const ratingBooking = externalReviews[1]["rating"] ? externalReviews[1]["rating"] : 0

        return Math.max(ratingAirbnb, ratingBooking)
      } else {
        return 0
      }
    },
    countReviewsExternal() {
      const externalReviews = this.data.external_reviews
      if (externalReviews) {
        const countAirbnb = externalReviews[0]["count"] ? externalReviews[0]["count"] : 0
        const countBooking = externalReviews[1]["count"] ? externalReviews[1]["count"] : 0

        return countAirbnb + countBooking
      } else {
        return 0
      }
    },
    isRating() {
      return this.rating || !this.data.hotel || this.maxRatingExternal
    },
    countRatingReviews() {
      return this.rating?.count || 0;
    },
    isExternalRating() {
      if (this.countRatingReviews <= 1 && this.maxRatingExternal > 0) {
        return true;
      }
      return false;
    },
    dataRating() {
      if (this.isExternalRating) {
        return this.maxRatingExternal;
      }
      if (this.countRatingReviews) {
        return this.rating.ratings?.avg_rating || 0;
      }
      return 0;
    },
  },
  methods: {
    ...mapMutations("search", ["objectFlagMutate"]),
    sendGa() {
      let flag = null
      if(this.whatView === 'hot_sale')
        flag = 'e'
      else if(this.whatView === 'profitable')
        flag = 'v'

      if(flag) {
        this.objectFlagMutate({
          id: Number(this.data.id),
          index: 4,
          symbol: flag
        })
      }
    },
    sendGaPrimary() {
      if(this.primary) {
        this.primary.map((item, i) => {
          let flag = null
          if(item.name === 'clear')
            flag = 'c'
          else if(item.name === 'infrastructure')
            flag = 'p'
          else if(item.name === 'accuracy')
            flag = 'g'

          if(i === 0)
            this.objectFlagMutate({
              id: Number(this.data.id),
              index: 3,
              symbol: flag,
              obj: 0
            })
          else if(i === 1)
            this.objectFlagMutate({
              id: Number(this.data.id),
              index: 3,
              symbol: flag,
              obj: 1
            })
        })
      }
    },
    linkTo() {
      this.$parent.showDetail = !this.showDetail;
      document.body.style.overflow = "hidden";
    },
    clickObjectBronevik(event, link) {
      if (link || (link && event.which == 1) || (link && event.which == 2)) {
        event.preventDefault();
        window.open(link)
      }
    },
    hoverAvgRating() {
      return this.data.count_reviews > 1 || this.isExternalRating ? this.viewAvgRating = true : false
    },
    mousedownComponent($event) {
      this.$emit("mousedown", $event);
      this.clickObjectBronevik($event, this.linkBronevik);
    },
  },
};
</script>

<style lang="scss" scoped>
.card-prices {
  position: relative;
  padding: 16px 20px 20px 0;
  display: flex;
  background: #fff;
  border-top-left-radius: 0;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
  border-bottom-left-radius: 0;
  text-decoration: none;
  align-items: flex-end;
    flex-direction: column;
    justify-content: space-between;

  ::v-deep .price-order {
    grid-area: price;
  }
  ::v-deep .link {
    grid-area: link;
  }
  ::v-deep .rating-list__rating {
    font-weight: bold;
  }

  &__primary {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-top: 10px;

    .primary {
      &__item {
        display: flex;
        align-items: center;
        padding: 0 6px;
        height: 18px;
        margin-bottom: 4px;
        border-radius: 9px;
        color: #000;
        font-size: 9px;
        background-color: #f6f6f6;
      }
      &__defis {
        margin-left: 2px;
        margin-right: 2px;
      }
    }
  }
  &__profitable {
    position: relative;
    display: flex;
    align-items: center;
    width: 127px;
    height: 18px;
    border-bottom-left-radius: 9px;
    border-top-left-radius: 9px;
    border: solid 1px #c6d5b4;
    background-color: #ffffff;
    font-size: 10px;
    color: #417505;
    text-transform: uppercase;
    padding: 0 24px 0 4px;
    right: -45px;
    margin-bottom: 7px;
    white-space: nowrap;
    line-height: 12px;
  }
  &__free-cancellation {
    position: relative;
    display: flex;
    align-items: center;
    width: 127px;
    height: 18px;
    border-radius: 2px;
    border: solid 1px #407705;
    background-color: #407705;
    font-size: 10px;
    color: #fff;
    text-transform: uppercase;
    padding: 0 24px 0 4px;
    right: -45px;
    margin-bottom: 7px;
    white-space: nowrap;
    line-height: 12px;

    &::after {
      position: absolute;
      right: 0;
      bottom: -4px;
      content: '';
      border: 2px solid transparent;
      border-left: 2px solid #407705;
      border-top: 2px solid #407705;
    }
  }

  &__info {
    justify-content: flex-end;
    display: flex;
    align-items: center;
    width: 147px;
    height: 18px;
    font-size: 9px;
    color: #000;
    margin-bottom: 6px;
    position: relative;

    .icon-app-lightning {
      width: 11px;
      height: 16px;
      margin-right: 3px;
      &::before {
        content: '';
        display: block;
        width: 100%;
        height: 100%;
        background-image: url(~assets/img/icon/flash.svg);
        fill: #F51449;
      }
    }

    .info {
      &__text {
        display: block;
        line-height: 10px;
        white-space: nowrap;
      }
      &__circle {
        display: block;
        width: 4px;
        min-width: 4px;
        height: 4px;
        border-radius: 50%;
        margin-right: 8px;
        background-color: #417505;
      }
    }
  }
  &__icon-answer-always,
  &__icon-answer-quickly,
  &__icon-online {
    width: 16px;
    height: 16px;
    margin-right: 5px;

  }
  &__icon-answer-always,
  &__icon-answer-quickly {
    color: #0659ff;
    background-image: url(~assets/img/icon/chat.svg);
  }
  &__icon-online {
    color: #14c405;
    background-image: url(~assets/img/icon/chat-online.svg);
  }

  &__bottom {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    //width: 147px;
  }

  &__rating {
    // display: none;
    justify-content: flex-end;
    // @media only screen and (min-width: 1490px) {
      display: flex;
    // }
  }

  .discount {
    display: flex;
    align-items: center;
    text-align: center;
    grid-area: discount;
    color: #e10600;
    font-size: 13px;
    margin-bottom: 20px;
    .icon-app-fire {
      margin-right: 4px;
      font-size: 17px;
    }
  }

  .loading-price-bronevik {
    height: 12px;
    position: relative;
    width: 100%;
    margin-bottom: 8px;
    img {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 48px;
      margin-left: 12px;
    }
  }

  .guests-recommend {
    margin-top: 10px;
    margin-left: auto;
    margin-bottom: 5px;
  }
}

.guests-recommend {
  width: 28px;
  height: 28px;
  position: relative;
  &:hover {
    width: 88px;
    .guests-recommend__icon {
      transform: translateX(-100px);
    }
    .guests-recommend__text {
      opacity: 1;
    }
  }
  &__text {
    color: #000;
    font-size: 9px;
    position: absolute;
    width: max-content;
    right: 0;
    top: 10px;
    opacity: 0;
    transition: opacity .5s;
    pointer-events: none;
  }
  &__icon {
    width: 28px;
    height: 28px;
    background-color: #f51449;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    transition: transform .4s;
    position: absolute;
    pointer-events: none;
    right: 0;
  }
  &__img {
    transform: translate(1px, -1px);
    width: 13px;
  }
}
a {
  text-decoration: none;
  &:hover {
    text-decoration: none;
  }
}
</style>
