<i18n>
{
	"ru": {
    "reviews": "отзывов | {n} отзыв | {n} отзыва | {n} отзывов"
	},
	"en": {
    "reviews": "reviews | {n} review | {n} reviews | {n} reviews"
	}
}
</i18n>

<template>
  <div
    class="rating-list pad-block"
    :class="{ scale }"
    :data-rating="Math.round(dataRating)"
    itemprop="ratingValue"
    :content="dataRating"
    @mouseover="$emit('mouseover')"
    @mouseleave="$emit('mouseleave')"
    :style="`color: ${color}`"
  >
    <template v-if="countReviews === 1">
      <span
        :class="{
          'count-white': countWhite,
          'rating-list__count_prev': dataRating,
        }"
        class="rating-list__count"
        itemprop="reviewCount"
        >{{ $tc("reviews", countReviews) }}</span
      >
      <template v-if="dataRating">
        <div class="icon-app-star"></div>
        <span class="rating-list__rating" v-if="isExternalRating">{{
          $filters.newRatingExternal(dataRating)
        }}</span>
        <span class="rating-list__rating" v-else>{{
          $filters.newRating(dataRating)
        }}</span>
      </template>
    </template>
    <template v-else-if="countReviews > 1">
      <span
        v-if="viewPort === 'desktop'"
        :class="{ 'count-white': countWhite }"
        class="rating-list__count rating-list__count_prev"
        itemprop="reviewCount"
        >{{
          reviewsText
            ? $tc("reviews", countReviews)
            : $tc("reviews", countReviews)
        }}</span
      >
      <template v-if="dataRating">
        <div class="icon-app-star"></div>
        <span class="rating-list__rating" v-if="isExternalRating">{{
          $filters.newRatingExternal(dataRating)
        }}</span>
        <span class="rating-list__rating" v-else>{{
          $filters.newRating(dataRating)
        }}</span>
      </template>
      <span
        v-if="viewPort === 'mobile'"
        :class="{ 'count-white': countWhite }"
        class="rating-list__count"
        itemprop="reviewCount"
        >({{ countReviews }})</span
      >
    </template>
    <template v-if="countReviews === 0">
      <template v-if="dataRating">
        <div class="icon-app-star"></div>
        <span class="rating-list__rating" v-if="isExternalRating">{{
          $filters.newRatingExternal(dataRating)
        }}</span>
        <span class="rating-list__rating" v-else>{{
          $filters.newRating(dataRating)
        }}</span>
      </template>
    </template>
    <slot />
  </div>
</template>

<script>
export default {
  name: "BaseRatingSearch",
  props: {
    dataRating: {
      type: Number,
      required: true,
      default: 0,
    },
    countReviews: {
      type: Number,
      required: false,
    },
    isHotel: {
      type: Object,
      required: false,
    },
    countWhite: {
      type: Boolean,
      required: false,
      default: false,
    },
    isSmall: {
      type: Boolean,
      default: false,
    },
    scale: {
      type: Boolean,
      default: false,
    },
    reviewsText: {
      type: Boolean,
      default: false,
    },
    viewPort: {
      type: String,
      default: "desktop",
    },
    color: {
      type: String,
      default: "#ffa900",
    },
    isExternalRating: {
      type: Boolean,
      default: false,
    },
  },
};
</script>

<style lang="scss" scoped>
.rating-list {
  // z-index: 2;
  padding: 0;
  margin: 0;
  list-style: none;
  // position: absolute;
  // top: 10px;
  // left: 8px;
  display: flex;

  &.scale {
    align-items: flex-end;
    div {
      font-size: 22px;
      width: 14px;
      margin-right: 6px;
    }
    .rating-list__rating {
      font-size: 18px;
      line-height: 22px;
    }
  }

  .count {
    &-white {
      color: #fff;
    }
  }

  .object-hotel {
    margin-left: auto;
    display: inline-block;
    font-size: 10px;
    text-transform: uppercase;
    color: #444444;
    border: 1px solid #d8d8d8;
    border-radius: 2px;
    padding: 1px 3px;
  }

  .icon-app-star {
    width: 13px;
    height: 12px;
    margin-top: 1px;
    &::before {
      content: "";
      display: block;
      width: 100%;
      height: 100%;
      background-image: url(~assets/img/icon/star.svg);
      background-position: center;
      background-size: contain;
      background-repeat: no-repeat;
    }
  }

  div {
    // Pixel Perfect
    margin-left: -2px;
    margin-right: 2px;

    // text-align: center;
    font-size: 16px;
    width: 12px;
    color: #d8d8d8;
  }
  &[data-rating="1"] {
    div:nth-child(1) {
      color: #ffa900;
    }
  }
  &[data-rating="2"] {
    div:nth-child(1),
    div:nth-child(2) {
      color: #ffa900;
    }
  }
  &[data-rating="3"] {
    div:nth-child(1),
    div:nth-child(2),
    div:nth-child(3) {
      color: #ffa900;
    }
  }
  &[data-rating="4"] {
    div:nth-child(1),
    div:nth-child(2),
    div:nth-child(3),
    div:nth-child(4) {
      color: #ffa900;
    }
  }
  &[data-rating="5"] {
    div:nth-child(1),
    div:nth-child(2),
    div:nth-child(3),
    div:nth-child(4),
    div:nth-child(5) {
      color: #ffa900;
    }
  }

  &__count,
  &__rating {
    font-size: 14px;
    line-height: 16px;
  }

  &__rating {
    padding-left: 6px;
    color: #000;
    font-weight: bold;
  }
  &__count {
    padding-left: 3px;
    color: #7f7f7f;

    &_prev {
      padding-left: 0;
      padding-right: 10px;
    }
  }
}
</style>
