<template>
  <div class="block-price">
    <label>
      <input
        :value="valueMin"
        class="range-number-input"
        type="number"
        :min="min"
        :max="max"
        :step="step"
        :placeholder="placeholderMin"
        @input="inputMin($event.target.value)"
        @keydown="onKeyDown"
      />
      <div class="name">от</div>
      <div class="unit" v-if="inputAfter">{{ inputAfter }}</div>
    </label>
    <label>
      <input
          :value="valueMax"
          class="range-number-input"
          type="number"
          :min="min"
          :max="max"
          :step="step"
          :placeholder="placeholderMax"
          @input="inputMax($event.target.value)"
          @keydown="onKeyDown"
      />
      <div class="name">до</div>
      <div class="unit" v-if="inputAfter">{{inputAfter}}</div>
    </label>
  </div>
</template>
<script>
export default {
  name: "RangeNumber",
  props: {
    //тип лучше не указывать
    valueMin: {
      default: null,
    },
    valueMax: {
      default: null,
    },
    placeholderMin: {
      default: null,
    },
    placeholderMax: {
      default: null,
    },
    min: {
      default: 0,
    },
    max: {
      default: null,
    },
    step: {
      default: 1,
    },
    inputAfter: {
      default: null,
    },
    inputAfterSup: {
      default: null,
    },
  },
  data() {
    return {
      excludeKeys: ["Delete", "Backspace"],
    };
  },
  methods: {
    onKeyDown(event) {
      const VALUE = event.target.value || event.key;
      if (
        this.excludeKeys.includes(event.key) ||
        (!["-", "+"].includes(event.key) && /^[0-9]*$/.test(VALUE))
      ) {
        return true;
      }

      event.preventDefault();
      return false;
    },
    inputMin(item) {
      this.$emit('update:valueMin', Number(item));
      this.$emit('changeValue', 'min');
    },
    inputMax(item) {
      this.$emit('update:valueMax', Number(item));
      this.$emit('changeValue', 'max');
    }
  },
};
</script>
<style lang="scss" scoped>
.block-price {
  grid-area: price;
  display: flex;
  justify-content: space-between;
  width: 100%;
  label {
    display: block;
    background-color: #f1f0ed;
    width: 132px;
    border-radius: 4px;
    position: relative;
    font-size: 14px;
    input {
      display: block;
      width: 100%;
      height: 36px;
      padding: 0 24px 0 34px;
      margin: 0;
      font-size: 14px;
      color: #000;
      border: none;
      background: transparent;
      outline-style: none;
      &[type="number"]::-webkit-outer-spin-button,
      &[type="number"]::-webkit-inner-spin-button {
        -webkit-appearance: none;
        margin: 0;
      }
      &[type="number"] {
        -moz-appearance: textfield;
      }
      &[type="number"]:hover,
      &[type="number"]:focus {
        -moz-appearance: textfield;
      }
      &::placeholder {
        color: #000;
      }
    }
    .name {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 12px;
      line-height: 36px;
      color: #717171;
    }
    .unit {
      position: absolute;
      top: 0;
      bottom: 0;
      right: 12px;
      line-height: 36px;
    }
  }
}
</style>
