<template>
  <div
    class="card"
    :class="{ 'disabled-link': isDisabledLink }"
    @click="clickObjectWrap"
  >
    <div class="card__img" @click.capture="clickPhotoOthers">
      <Img
        v-model:selected-slide="selectedSlide"
        :linkBronevik="linkBronevik"
        :isBronevik="isBronevik"
        :data="objectData"
        :targetOpt="targetOpt"
      />
    </div>
    <Content
      :linkBronevik="linkBronevik"
      :isBronevik="isBronevik"
      :isDisabledLink="isDisabledLink"
      :data="objectData"
      :show-detail="showDetail"
      :selected-slide="selectedSlide"
      :hideOwnerPopularity="hideOwnerPopularity"
      :rating="rating"
      @click="clickObject"
      @mousedown="mousedownObject"
    />
    <Prices
      :linkBronevik="linkBronevik"
      :isBronevik="isBronevik"
      :data="objectData"
      :show-detail="showDetail"
      :selected-slide="selectedSlide"
      @click="clickObject"
      @mousedown="mousedownObject"
    />
    <ObjectDetail
      v-if="showDetail"
      :id="String(objectData.id)"
      :show-detail="showPage"
    />
    <LoadingPrice v-if="bronevikLoadingPrice" />
  </div>
</template>
<script>
import { mapState, mapGetters } from "vuex";
import Img from "./Img";
import Content from "./Content";
import Prices from "./Prices";
import ObjectDetail from "./ObjectDetail";
import LoadingPrice from "@/components/base/LoadingPrice.vue";

export default {
  name: "ObjectBlock",
  components: {
    Img,
    Content,
    Prices,
    ObjectDetail,
    LoadingPrice,
  },
  props: {
    objectData: {
      type: Object,
    },
    isBronevik: {
      type: Boolean,
      default: false,
    },
    analyticsEvent: {
      type: Boolean,
      default: false,
    },
    rating: {
      default: null,
    },
  },
  data() {
    return {
      showDetail: false,
      hoveredTop: false,
      selectedSlide: 0,
      bronevikLoadingPrice: false,
    };
  },
  computed: {
    ...mapState("search", [
      "guests",
      "objectsBronevik",
      "pricesBronevik",
      "loadBronevikPrice",
    ]),
    ...mapGetters("search", ["occupied", "getQuery"]),
    ...mapState("user", ["isVkApp"]),
    hideOwnerPopularity() {
      return this.objectData?.owner?.permissions?.includes(
        "hideObjectsPopularityStatus"
      )
        ? true
        : false;
    },
    targetOpt() {
      return this.isVkApp ? "_self" : "_blank";
    },
    conveniencesList() {
      if (!this.objectData.conveniences) return [];
      return Object.entries(this.objectData.conveniences)
        .filter(([key, value]) => value && key)
        .map(([key]) => key);
    },
    dataBronevik() {
      let idBronevik = this.objectsBronevik.find(
        (item) => item == this.objectData.id
      );
      if (idBronevik) {
        return this.pricesBronevik &&
          this.pricesBronevik[idBronevik] &&
          this.pricesBronevik[idBronevik]["prices"]
          ? this.pricesBronevik[idBronevik]["prices"]
          : [];
      } else {
        return [];
      }
    },
    linkBronevik() {
      if (this.dataBronevik.length) {
        return this.dataBronevik[0].link;
      } else if (this.objectData.url && this.objectData.owner.id === 4784337) {
        return this.objectData.url;
      } else {
        return "";
      }
    },
    isDisabledLink() {
      let idBronevik = this.objectsBronevik.find(
        (item) => item == this.objectData.id
      );
      if (idBronevik) {
        if (this.dataBronevik.length) {
          return false;
        } else {
          return true;
        }
      } else {
        return false;
      }
    },
  },
  watch: {
    loadBronevikPrice(val) {
      if (!val) {
        if (this.bronevikLoadingPrice) {
          this.bronevikLoadingPrice = false;
        }
      }
    },
  },
  methods: {
    showPage() {
      this.showDetail = !this.showDetail;
      document.body.style.overflow = "hidden";
      return this.showDetail;
    },
    mouseOver() {
      this.hoveredTop = true;
    },
    mouseOut() {
      this.hoveredTop = false;
    },
    clickPhotoOthers() {
      if (this.analyticsEvent) {
        this.$gtag.event("load", {
          event_category: "search_sa",
          event_label: "click_photo_others",
        });
      }
    },
    clickObject() {
      if (this.analyticsEvent) {
        this.$gtag.event("load", {
          event_category: "search_sa",
          event_label: "click_object",
        });
      }
    },
    mousedownObject(event) {
      if (event.button === 1) {
        if (this.analyticsEvent) {
          this.$gtag.event("load", {
            event_category: "search_sa",
            event_label: "click_object",
          });
        }
      }
    },
    clickObjectWrap(event) {
      if (this.isDisabledLink) {
        if (!this.occupied) {
          setTimeout(() => {
            document.querySelector(".open-calendar").click();
          }, 150);
        }
        event.preventDefault();
        if (this.occupied) {
          this.$gtag.event("click", {
            event_category: "search_sa",
            event_label: "object_b_nolink",
            value: this.objectData.location.city.title,
          });
        }
      }
      if (this.loadBronevikPrice) {
        event.preventDefault();
        this.bronevikLoadingPrice = true;
      }
    },
    clickObjectBronevik(event, link) {
      if (link || (link && event.which == 1) || (link && event.which == 2)) {
        event.preventDefault();
        window.open(link);
        this.$gtag.event("click", {
          event_category: "search_sa",
          event_label: "object_b_link",
          value: this.data.location.city.title,
        });
      }
    },
  },
};
</script>
<style lang="scss" scoped>
.card-list__wrapper {
  span {
    &:first-child {
      .card.card_recommended {
        margin-top: 0px;
      }
    }
  }
}
.card {
  display: grid;
  grid-template-areas:
    "carousel"
    "content"
    "prices";
  grid-template-columns: 100%;
  grid-template-rows: 210px auto auto;

  position: relative;

  width: 100%;
  border: none;
  margin: 8px 0 20px 0;
  height: 96%;

  &:hover {
    border-radius: 4px;
  }

  &--selected,
  &:hover {
    box-shadow: 0px 0px 7px rgba(0, 0, 0, 0.270805);
  }

  &__sticker {
    grid-area: top;
    position: absolute;
    top: -22px;
    left: 0;
  }

  &__link {
    color: inherit;
  }
  &__sticker-text {
    background: #d8d8d8;
    border-radius: 2px 2px 0px 0px;
    font-size: 13px;
    line-height: 17px;
    padding: 3px 10px;
    color: inherit;
  }
  &__img {
    ::v-deep {
      .carousel .sc-sliderlight,
      .carousel .sc-sliderlight img {
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
        border-top-left-radius: 4px;
        border-top-right-radius: 4px;
      }
    }
  }
  .card__img {
    grid-area: carousel;
  }
  .object-content {
    grid-area: content;
  }
  ::v-deep .object-prices {
    grid-area: prices;
    // display: flex;
    // flex-direction: column;
    // align-items: center;
  }
  &.disabled-link {
    a {
      cursor: default;
    }
    ::v-deep {
      .price-bronevik,
      .price-order {
        opacity: 0.7;
      }
    }
  }
}
</style>
