export default function (Component) {
  Component.__i18n = Component.__i18n || []
  Component.__i18n.push({
    "locale": "",
    "resource": {
      "en": {
        "clearFilters": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Clear filter"])},
        "noVariants": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["No variants"])},
        "noVariantsDescription": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["No listing meeting your conditions was found."])},
        "open": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Open the map"])},
        "close": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Show a list of objects"])},
        "searchCurrentArea": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Search here…"])},
        "from": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["from "])}
      },
      "ru": {
        "clearFilters": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Показать доступные варианты"])},
        "noVariants": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["В этой области нет вариантов"])},
        "noVariantsDescription": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Сдвиньте карту или сбросьте фильтры, чтобы увидеть предложения."])},
        "open": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Раскрыть карту"])},
        "close": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Показать список объектов"])},
        "searchCurrentArea": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Искать здесь…"])},
        "from": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["от "])}
      }
    }
  })
}
