<template>
  <div class="sc-yandex-map">
    <slot v-if="isLoad"></slot>
    <div class="sc-yandex-map__map" :id="id"></div>
    <!-- Добавь это к блоку если хочешь лоадер пока карта не загрузилась :class="{ 'loading loading_primary': !isLoad }" -->
  </div>
</template>

<script setup>
import {
  ref,
  provide,
  onMounted,
  onUpdated,
  onUnmounted,
  watch,
  markRaw,
} from "vue";

const props = defineProps({
  id: {
    type: String,
    default: "yandexMap",
  },
  settings: {
    type: Object,
    default: () => ({}),
  },
  coords: {
    type: Array,
    required: true,
  },
  zoom: {
    validator(val) {
      return !Number.isNaN(val);
    },
    default: 18,
  },
  bounds: Array,
  behaviors: {
    type: Array,
    default: () => ["default"],
  },
  customEvents: {
    type: Array,
    default: () => [],
  },
  controls: {
    type: Array,
    default: () => ["zoomControl"],
  },
  scrollZoom: {
    type: Boolean,
    default: true,
  },
  mapType: {
    type: String,
    default: "map",
    validator(val) {
      return ["map", "satellite", "hybrid"].includes(val);
    },
  },
  options: {
    type: Object,
    default: () => ({}),
  },
});

const emit = defineEmits([
  "ready",
  "abort",
  "boundschange",
  "update:zoom",
  "update:coords",
  "update:bounds",
  "update:customEvents",
]);

const isLoad = ref(false); // реактивная потому что есть в шаблоне
const unwatchArr = []; // для вотчеров которые сами не размонтируются

// реактивность для provide
const myMap = ref(null);
provide("myMap", myMap);

//сам размонтируется
watch(props.bounds, (val) => {
  if (myMap.value && myMap.value.setBounds) myMap.value.setBounds(val);
});

function init() {
  let userAction = false; // Пользователь взаимодействует с картой
  myMap.value = markRaw(
    new ymaps.Map(
      props.id,
      {
        center: props.coords,
        zoom: props.zoom,
        bounds: props.bounds,
        behaviors: props.behaviors,
        controls: props.controls,
        type: `yandex#${props.mapType}`,
      },
      {
        ...props.options,
        suppressMapOpenBlock: true,
        suppressObsoleteBrowserNotifier: true,
        yandexMapDisablePoiInteractivity: true,
      }
    )
  );

  if (props.scrollZoom === false) {
    myMap.value.behaviors.disable("scrollZoom");
  }

  emit("ready", myMap.value);
  isLoad.value = true;
  emit("update:zoom", myMap.value.getZoom());
  emit("update:coords", myMap.value.getCenter());
  emit("update:bounds", myMap.value.getBounds());
  props.customEvents.forEach((event) =>
    myMap.value.events.add(event, (e) =>
      emit("update:customEvents", {
        name: event,
        event: e,
      })
    )
  );
  myMap.value.events.add("boundschange", (e) => {
    const {
      originalEvent: { newZoom, newCenter, newBounds },
    } = e;
    emit("boundschange", e);
    emit("update:zoom", newZoom);
    emit("update:coords", newCenter);
    emit("update:bounds", newBounds);
  });
  // actionbegin actionend
  myMap.value.events.add("actionbegin", (e) => {
    userAction = true;
  });
  myMap.value.events.add("actionend", (e) => {
    userAction = false;
  });
  unwatchArr.push(
    watch(
      () => [props.coords, props.zoom],
      () => {
        if (!userAction) myMap.value.setCenter(props.coords, props.zoom);
      },
      { immediate: false }
    )
  ); // run immediately
}

// hooks
onMounted(() => {
  const {
    apiKey = "",
    lang = "ru_RU",
    version = "2.1",
    coordorder = "latlong",
    debug = false,
  } = props.settings;
  const mode = debug ? "debug" : "release";
  const settingsPart = `lang=${lang}${
    apiKey && `&apikey=${apiKey}`
  }&mode=${mode}&coordorder=${coordorder}`;
  const link = `https://api-maps.yandex.ru/${version}/?${settingsPart}`;
  const isLoadedScript = document.querySelector(`script[src*="${link}"]`);
  if (isLoadedScript) {
    // console.log("Map: Reuse old map script");
    isLoadedScript.onload = () => ymaps.ready(init);
    ymaps.ready(init);
  } else {
    // console.log("Map: Load script");
    const yandexMapScript = document.createElement("script");
    yandexMapScript.setAttribute("src", link);
    yandexMapScript.setAttribute("async", "");
    yandexMapScript.setAttribute("defer", "");
    document.body.appendChild(yandexMapScript);
    yandexMapScript.onload = () => ymaps.ready(init);
    yandexMapScript.onerror = () => {
      emit("abort");
    };
  }
});
onUnmounted(() => {
  unwatchArr.forEach((unwatch) => unwatch());
  myMap.value && myMap.value.destroy();
});
onUpdated(() => {
  // получаю версию яндекс карты, для добавления классов элементам карты, чтобы далее их стилизовать
  if (document.querySelector(".sc-yandex-map__map").firstElementChild) {
    const selectorVersion = document
      .querySelector(".sc-yandex-map__map")
      .firstElementChild.className.replace("-map", "");

    setTimeout(() => {
      if (document.querySelector(`.${selectorVersion}-zoom`)) {
        document
          .querySelector(`.${selectorVersion}-zoom__plus`)
          .classList.add("ymaps-zoom__plus");
        document
          .querySelector(`.${selectorVersion}-zoom__minus`)
          .classList.add("ymaps-zoom__minus");
        document
          .querySelectorAll(`.${selectorVersion}-zoom__icon`)
          .forEach((element) => {
            element.classList.add("ymaps-zoom__icon");
          });
      }
    }, 300);
  }
});
</script>

