export default function (Component) {
  Component.__i18n = Component.__i18n || []
  Component.__i18n.push({
    "locale": "",
    "resource": {
      "ru": {
        "titleHistory": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["История поиска"])},
        "titlePopular": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Популярные направления"])},
        "titleHotels": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Гостиницы"])}
      },
      "en": {
        "titleHistory": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Search history"])},
        "titlePopular": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Popular destinations"])},
        "titleHotels": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Hotels"])}
      }
    }
  })
}
