// import HTTP from "../http-common";
import axios from "axios";

/**
 * Получение данных о странах и городах из файла JSON,
 * который расположен ./public/countries.json
 *
 * @module getLocations
 * @return {object} Promise
 */
export default (locale) => {
  if (locale == "en") {
    return axios.get(
      `${process.env.VUE_APP_BASEPATH}/main_all_locations_en.json`
    );
  } else {
    return axios.get(
      `${process.env.VUE_APP_BASEPATH}/main_all_locations_ru.json`
    );
  }
};
