<i18n>
{
  "ru": {
    "variantAllocation": "Варианты размещения",
    "variantAllocation__apartments": "квартиры, апартаменты",
    "variantAllocation__apartmentsHotel": "апарт-отели",
    "variantAllocation__guestHouse": "гостевые дома",
    "variantAllocation__hostel": "хостелы",
    "variantAllocation__hotel": "гостиницы",
    "variantAllocation__house": "дома, коттеджи",
    "variantAllocation__miniHotel": "мини-гостиницы",
    "variantAllocation__room": "комнаты"
  },
  "en": {
    "variantAllocation": "Property type",
    "variantAllocation__apartments": "apartments",
    "variantAllocation__apartmentsHotel": "aparthotels",
    "variantAllocation__guestHouse": "guest houses",
    "variantAllocation__hostel": "hostels",
    "variantAllocation__hotel": "hotels",
    "variantAllocation__house": "houses",
    "variantAllocation__miniHotel": "Inns",
    "variantAllocation__room": "rooms"
  }
}
</i18n>

<template>
  <div class="filters">

    <!-- House type filter -->
    <ul>
      <li v-for="item in preference.variantAllocation" :key="item.key">
        <Checkbox
          v-model="filters[item.key]"
          :checked="filters[item.key]"
          :checked-value="1"
          :unchecked-value="0"
        >
          <span
            class="facets"
            >{{ item.text }}</span
          >
        </Checkbox>
      </li>
    </ul>

  </div>
</template>

<script>
import json from "./preferenceFilters.json";

import { mapState, mapGetters, mapMutations } from "vuex";

import { HTTP } from "@/api/index.js";
import { CancelToken } from "axios";
import _ from "lodash";

const defaultFilters = () => {
  let keys = Object.values(json).reduce((acc, value) => {
    if (Array.isArray(value)) {
      value.forEach((o) => o.key && (acc = { ...acc, [o.key]: 0 }));
    }
    return acc;
  }, {});
  return {
    price: [null, null],
    ...keys,
  };
};

export default {
  name: "Filters",
  data() {
    return {
      preference: json,
      lock: false, // Блокировка отправка нового состояния в случае обновления из стора

      cancelSource: null,

      clearFilters: {},
      filters: {
        ...defaultFilters(),
      },
      inDragging: false,
      priceRub: [
        0, 500, 1000, 1500, 2000, 2500, 3000, 4000, 5000, 6000, 7000, 8000,
        10000, 15000, 20000,
      ],
      priceUsd: [
        0, 10, 20, 30, 40, 50, 60, 70, 85, 100, 150, 200, 300, 400, 500,
      ],
      priceRange: [
        0, 500, 1000, 1500, 2000, 2500, 3000, 4000, 5000, 6000, 7000, 8000,
        10000, 15000, 20000,
      ],
      isBottom: false,
      date: {
        in: this.checkIn,
        out: this.checkOut,
      },
      showHeaderDatePicker: false,
      separateBedroomsAndStudio: null,
      visibleInsideRoom: false,
      visibleAdditionals: false,
      pricePer: false,
      villageId: null,
      placePreference: null,
    };
  },
  computed: {
    ...mapState("search", [
      "maxGuests",
      "bound",
      "isSupportFilters",
      "checkIn",
      "checkOut",
      "filtersTotalFound",
      "selectedFilters",
      "guests",
      "term",
      "locationForFilter"
    ]),
    ...mapState("search", { filtersStore: "filters" }),
    ...mapState("search", ["isPosition", "filtersView"]),
    ...mapState("user", [
      "viewPort",
    ]),
    ...mapGetters("search", ["occupied", "maxGuests", "days"]),
    location(){
      return this.locationForFilter
    },
    sortedPreferenceInside() {
      return this.preference.inside;
    },
    filterSortedPreferenceInside() {
      return this.visibleInsideRoom
        ? this.sortedPreferenceInside
        : this.sortedPreferenceInside.slice(0, 5);
    },
    filterPreferenceAdditional() {
      return this.visibleAdditionals
        ? this.preference.additional
        : this.preference.additional.slice(0, 5);
    },
    moreThenOneDay() {
      return this.sumOfDays > 1;
    },
    sumOfDays() {
      return Math.round(Math.abs(this.checkIn - this.checkOut) / 86400000);
    },
    dateSelectionCheck() {
      return this.checkIn && this.checkOut;
    },
  },
  props: {
    viewFilters: {
      type: Boolean,
      default: false,
    },
    modalFilters: {
      type: Boolean,
      default: false,
    },
    showAll: {
      type: Boolean,
      default: true,
    },
    loadClick: {
      type: Boolean,
      default: false,
    },
  },
  watch: {
    pricePer(val) {
      val ? (this.filters.price_per = 2) : (this.filters.price_per = 1);
    },
    filtersStore(value) {
      this.lock = true;
      this.filters = {
        ...defaultFilters(),
        ...JSON.parse(JSON.stringify(value)),
      };
      this.$nextTick(() => {
        this.lock = false;
      });
    },
    separateBedroomsAndStudio(val) {
      if (val === "is_studio") {
        this.filters.is_studio = 1;
        this.filters.separate_bedrooms = null;
        return;
      }
      if (Number.isInteger(val)) {
        this.filters.separate_bedrooms = val;
        this.filters.is_studio = null;
        return;
      }
      this.filters.separate_bedrooms = null;
      this.filters.is_studio = null;
    },
    filters: {
      handler(val, old) {
        if (!this.loadClick) {
          this.updateSelectedFilters();
        }
        if (this.inDragging) return;
        if (val.price_per == 2) {
          this.pricePer = true;
        } else {
          this.pricePer = false;
        }
      },
      deep: true,
    },
    checkIn(val, oVal) {
      if (val != oVal && val == null)
        this.date = {
          in: null,
          out: null,
        };
    },
    checkOut(val, oVal) {
      if (val != oVal && val == null)
        this.date = {
          in: null,
          out: null,
        };
    },
    location() {
      this.deleteFilters([
        "metro",
        "place_id",
        "region_id",
        "country_id",
        "distance_to_sea",
        "ropeway_distance",
      ]);
    },
  },
  beforeCreate: function () {
    // Применение перевода
    if (!json.translate) {
      Object.keys(json).map((key) => {
        if (Array.isArray(json[key]))
          return json[key].map((o) => {
            if (o.text) o.text = this.$t(o.text);
            if (o.textAbout) o.textAbout = this.$t(o.textAbout);
          });
      });
      json.translate = true;
    }
  },
  async mounted() {
    await this.$nextTick();
    await this.getPlacePreference();
    this.init();

    this.separateBedroomsAndStudio =
      this.filters.separate_bedrooms || this.filters.is_studio
        ? "is_studio"
        : null;

    if (!this.showAll) {
      this.visibleInsideRoom = true;
      this.visibleAdditionals = true;
    }
  },
  methods: {
    ...mapMutations("search", [
      "setFiltersTotalFound",
      "setSelectedFilters",
      "setIsLoadFilter",
      "deleteFilter",
      "deleteFilters",
    ]),
    init() {
      this.filters = {
        ...defaultFilters(),
        ...JSON.parse(JSON.stringify(this.filtersStore)),
      };
    },
    setPriceFilter(evt) {
      if (evt.price_per !== null) {
        let pricePer = evt.price_per === true ? 2 : 1;
        this.filters["price_per"] = pricePer;
      }
      if (evt.price[0] === null && evt.price[1] === null) {
        delete this.filters["price"];
      } else {
        this.filters["price"] = evt.price;
      }
    },
    flowChartRefresh: _.debounce(async function () {
      this.setIsLoadFilter(true);
      if (this.cancelSource) {
        this.cancelSource.cancel("Operation canceled by the user.");
      }
      this.cancelSource = CancelToken.source();
      let formData = new FormData();
      if (this.filters.price_per == 2) {
        for (let index = 0; index < 15; index++) {
          formData.append(
            `groups[${index}][from]`,
            (this.priceRange[index] / this.days).toFixed()
          );
          formData.append(
            `groups[${index}][to]`,
            (
              this.priceRange[index == 14 ? index : index + 1] / this.days
            ).toFixed()
          );
        }
      } else {
        for (let index = 0; index < 15; index++) {
          formData.append(`groups[${index}][from]`, this.priceRange[index]);
          formData.append(
            `groups[${index}][to]`,
            this.priceRange[index == 14 ? index : index + 1]
          );
        }
      }

      Object.entries(filtersSend).forEach(([key, value]) => {
        formData.append(key, value);
      });
      if (this.occupied) formData.append("occupied", this.occupied);
      if (this.maxGuests) formData.append("max_guests", this.maxGuests);

      if (this.bound) {
        formData.append("NE[lat]", this.bound.NE.lat);
        formData.append("NE[lng]", this.bound.NE.lng);
        formData.append("SW[lat]", this.bound.SW.lat);
        formData.append("SW[lng]", this.bound.SW.lng);

        await HTTP.post(
          "json/search/getPriceChartsWithFacetsByBounds",
          formData,
          {
            cancelToken: this.cancelSource.token,
          }
        )
          .then((response) => {
            const {
              data: {
                data: { facets, bars, count },
              },
            } = response;
            this.facets = facets;
            this.flowPrice = bars;
            this.$store.commit("search/setFlowPrice", bars);
            this.$store.commit("search/setFacets", facets);

          })
          .catch((error) => {
            console.log("Ajax error: ", error);
          });
      } else {
        formData.append("location_type", this.location.type);
        formData.append("location_id", this.location.id);

        await HTTP.post("json/search/getPriceChartsWithFacets", formData, {
          cancelToken: this.cancelSource.token,
        })
          .then((response) => {
            const {
              data: {
                data: { facets, bars, count },
              },
            } = response;
            this.facets = facets;
            this.flowPrice = bars;
          })
          .catch((error) => {
            console.log("Ajax error: ", error);
          });
      }
      this.setIsLoadFilter(false);
    }, 800),
    queryObject(isEmit = true) {
      let dfilters = defaultFilters();
      let params = {};
      for (const [key, value] of Object.entries(this.filters)) {
        if (JSON.stringify(dfilters[key]) == JSON.stringify(value) || !value)
          continue;
        params[key] = value;
      }
      if (isEmit) {
        this.$emit("update:modelValue", params);
      }

      if (this.loadClick) {
        this.$emit("choiseFilterMain", params);
      }
      this.clearFilters = params;
    },
    reset() {
      this.$emit("update:modelValue", {});
      this.clearFilters = {};
      this.flowChartRefresh();
    },
    triol(val) {
      // добавляем числам неразрывные пробелы
      return val ? val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ") : 0;
    },
    // для вывода всех выбранных фильтров
    // сделано здесь а не в сторе потому что нужен текст фильтра
    updateSelectedFilters() {
      let selectedFilters = Object.entries(this.filters).reduce(
        (acc, [key, value]) => {
          if (Array.isArray(value) && value.length) {
            // для диапазонов
            if (value[0] || value[1] || value.includes(0)) {
              let textRange = "";
              let uncheckedValue = [];
              if (value[0] && value[1]) textRange = `${value[0]} — ${value[1]}`;
              else if (value[0] || (value[0] == 0 && key !== "floor"))
                textRange = `от ${value[0]}`;
              else if (value[1] || value[1] == 0) textRange = `до ${value[1]}`;

              acc.push({
                key: key,
                value: value,
                text: textRange,
                uncheckedValue: uncheckedValue,
              });
            }
          }
          return acc;
        },
        []
      );
      this.setSelectedFilters(_.uniqBy(selectedFilters, "key"));
      return _.uniqBy(selectedFilters, "key");
    },
    onCheckboxButton(event, key) {
      /* if (Array.isArray(event)) {
        event = event.filter(v => !v);
      } */
      if (
        (Array.isArray(event) && !!event.length) ||
        (!Array.isArray(event) && event)
      ) {
        this.filters[key] = event;
      } else {
        this.deleteFilter(key);
      }
    },
    resetToDefault() {
      this.filters = { ...defaultFilters() };
    },

    checkTerm(str, item) {
      return str.indexOf(item) >= 0 ? true : false;
    },
    async getPlacePreference() {
      this.placePreference = JSON.parse(
        localStorage.getItem("place_" + this.location.id)
      );
      if (
        this.placePreference &&
        Date.now() / 1000 - this.placePreference.timestamp < 84600
      ) {
        this.placePreference = Object.values(this.placePreference.data.places);
        return this.placePreference;
      }

      await HTTP.get("json/locations/getCityPlaces", {
        params: { cityId: this.location.id },
      })
        .then((response) => {
          this.placePreference = response.data;
          localStorage.setItem(
            "place_" + this.location.id,
            JSON.stringify(this.placePreference)
          );
          this.placePreference = Object.values(
            this.placePreference.data.places
          );
          return this.placePreference;
        })
        .catch((error) => {
          console.log("Ajax error: ", error);
        });
    },
  },
};
</script>

<style lang="scss" scoped>
.filters {
  ul {
    list-style: none;
    padding: 0;
    margin: 0 0 18px;
    li {
      .sc-checkbox {
        padding: 8px 20px;
      }
      &:hover {
        background: #f8f7f5;
      }
    }
  }
}
</style>
