<i18n>
{
	"ru": {
		"add": "Добавить в избранное",
    "remove": "Убрать из избранного",
    "added": "Добавлено"
	},
	"en": {
		"add": "Add to favourites",
    "remove": "Remove from favorites",
    "added": "Added"
	}
}
</i18n>

<template>
  <div
    class="favorite"
    :class="{ circle: !isOpenFavoriteText, hover: isOpenFavoriteText }"
    @click.stop.prevent="toggle()"
    @mouseover="mouseoverHandler()"
    @mouseleave="mouseleaveHandler()"
  >
    <div class="favorite-wrapp">
      <transition name="favorite-animation">
        <span v-if="isOpenFavoriteText" class="favorite-text">
          {{ favoriteText }}
        </span>
      </transition>
      <span
        class="red"
        :class="{
          'icon-app-heart-tile': !favorite,
          'icon-app-heart-tile-fill': favorite,
        }"
      >
      </span>
    </div>
  </div>
  <!-- :data-favorite="favorite" -->
</template>
<script>
import { HTTP } from "@/api/index.js";
import { mapState, mapActions, mapMutations } from "vuex";

export default {
  name: "BaseFavorite",
  props: {
    id: {
      type: Number,
      required: true,
    },
    isFavorite: {
      type: Boolean,
      required: true,
    },
  },
  data() {
    return {
      is: false,
      favorite: this.isFavorite,
      isAdded: false,
      addedTimeout: null,
      disableShowFavoriteText: false,
    };
  },
  computed: {
    ...mapState("user", ["viewPort"]),
    favoriteText(){
      if (this.favorite && this.isAdded) {
        return this.$t("added");
      } else if (this.favorite) {
        return this.$t("remove");
      } else {
        return this.$t("add");
      }
    },
    isOpenFavoriteText(){
      if (this.is && !this.isAdded) {
        return true;
      } else if (!this.is && this.isAdded){
        return true;
      } else if (this.is){
        return true;
      }
      return false;
    }
  },
  watch: {
    favorite(val){
      if (val) {
        this.isAdded = true;
        this.addedTimeout = setTimeout(() => {
          this.isAdded = false;
        }, 3000)
      }
    }
  },
  methods: {
    ...mapActions("user", ["loadUserData"]),
    mouseoverHandler(){
      if (this.disableShowFavoriteText) {
        return false;
      } else {
        this.is = true;
      }
    },
    mouseleaveHandler(){
      this.disableShowFavoriteText = false;
      this.is = false;
    },
    toggle() {
      this.disableShowFavoriteText = true;
      this.is = false;

      let newState = !this.favorite;
      this.favorite = newState;
      this.$emit("favoriteState", this.favorite);
      HTTP.get("json/objects/toggle_favorite/id/" + this.id, {
        headers: {
          "api-version": String(null), // Если указать версию, то будет минимум данных
        },
        cache: false,
      })
        .then((response) => {
          newState = Boolean(response?.data?.data?.is_favorite);
          this.favorite = newState;
          setTimeout(() => {
            this.loadUserData();
          }, 0);
          this.$emit("favoriteState", this.favorite);
        })
        .catch((error) => {
          this.favorite = !newState;
          this.$emit("favoriteState", this.favorite);
          console.warn("Ajax error: not toggle favorite, ", error);
        });
    },
  },
};
</script>
<style lang="scss" scoped>
.favorite-animation-enter-active,
.favorite-animation-leave-active {
  transition: all 0.35s ease;
  overflow: hidden;
}

.favorite-animation-enter-from,
.favorite-animation-leave-to {
  width: 0px;
  max-width: 0px;
}
.favorite-animation-enter-to,
.favorite-animation-leave-from {
  max-width: 140px;
  width: 100%;
}
.favorite {
  z-index: 2;
  display: block;
  position: absolute;
  top: 11.5%;
  right: 4.5%;
  margin-top: -9px;
  margin-left: -9px;
  padding: 7px 5px 5px;
  width: auto;
  height: 29px;
  cursor: pointer;
  color: #fff;
  border-radius: 50px;
  .icon-app-heart-tile-fill,
  .icon-app-heart-tile {
    display: block;
    opacity: 0.7;
    width: 18px;
    height: 16px;
    position: relative;
    margin-left: 0;
    &::before {
      content: '';
      display: block;
      width: 18px;
      height: 16px;
      position: absolute;
      top: 0;
      right: 0;
      background-repeat: no-repeat;
      background-size: contain;
    }
    &.red {
      color: #f51449
    }
    @media screen and (min-width: 768px){
      &:hover {
        opacity: 1;
      }
    }
  }
  .icon-app-heart-tile::before {
    background-image: url(~assets/img/icon/like.svg);
  }
  .icon-app-heart-tile-fill::before {
    background-image: url(~assets/img/icon/like_red_active.svg);
  }

  .favorite-wrapp {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 100%;
    overflow: hidden;
    .favorite-text {
      margin: 0 2px 0 6px;
      display: inline-block;
      font-family: Inter;
      font-size: 12px;
      font-weight: normal;
      color: #444444;
      white-space: nowrap;
    }
  }
  &.circle {
    background-color: rgba(255, 255, 255, 0.5);
  }
  &.hover {
    background-color: rgba(255, 255, 255, 0.8);
    .icon-app-heart-tile::before {
      background-image: url(~assets/img/icon/like_red.svg);
    }
  }
}
</style>
