<i18n>
{
  "ru": {
    "guestChoice":"Выбор гостей",
    "guests": "Гости",
    "adult": "0|{n} взрослый | {n} взрослых | {n} взрослых",
    "guest": "0|{n} гость | {n} гостя | {n} гостей",
    "child": " без детей |{n} ребёнок | {n} ребёнка | {n} ребёнка",
    "children": "{n} детей",
    "ok": "Сохранить",
    "addChild":"Добавить ребёнка"
  },
  "en": {
    "guestChoice":"Guest Choice",
    "guests": "Guests",
    "adult": "0|{n} adult | {n} adult | {n} adult",
    "guest": "0|{n} guest | {n} guests | {n} guests",
    "child": " without children |{n} child | {n} child's | {n} child's",
    "children": "{n} children's",
    "ok": "Save",
    "addChild":"Add child"
  }
}
</i18n>
<template>
  <div class="select-guests" :class="view">
    <button
      class="select-guests--btn"
      :class="{
        merge: isMerge,
        active: isMerge && maxGuests > 1,
        radius: isNewStyle,
      }"
      @click="showModal"
    >
      <template v-if="isMerge">
        <div>{{ $tc("guest", maxGuests) }}</div>
      </template>

      <template v-else>
        <div class="title">
          {{ $t("guests") }}
        </div>
        <div class="body">
          {{ $tc("adult", guestsAdult) }}
          <span :class="{ empty: guestsChildrens === 0 }">
            {{ childrenText }}
          </span>
        </div>
        <span class="ico-arrow"></span>
      </template>
    </button>
    <BottomSheet v-model="isGuestsModal" class="modal-warning" :title="$t('guestChoice')">
      <div v-if="countLocalGuests" class="select-guests__body">
        <GuestsCounter
          v-if="countLocalGuests.adults"
          v-model="countLocalGuests.adults"
          :text="$tc('adult', countLocalGuests.adults)"
          class="adults"
        />
        <template v-for="(child, index) in countLocalGuests.childrens" :key="index">
          <ChildrenSelect
            :ref="`guest_${index}`"
            v-model="countLocalGuests.childrens[index]"
            class="guests__item"
            :add-child-text="$t('addChild')"
            :is-static="true"
            @delete="countLocalGuests.childrens.splice(index, 1)"
          />
        </template>
      </div>
      <div>
        <button
          class="button button_size_md button_dark-gray button_w-100"
          @click="apply"
        >
          {{ btnTitle ? btnTitle : $t("ok") }}
        </button>
      </div>
    </BottomSheet>
  </div>
</template>

<script>
import SelectMain from "./SelectMain";
import ChildrenSelect from "./ChildrenSelect";
import GuestsCounter from "./GuestsCounter";
import Modal from "@sutochno/modal";
import { mapState, mapGetters, mapActions, mapMutations } from "vuex";
import getHost from "@/utils/getHost";
import BottomSheet from "@/components/base/BottomSheet";

export default {
  name: "SelectGuestsMobile",
  components: {
    Modal,
    SelectMain,
    ChildrenSelect,
    GuestsCounter,
    BottomSheet,
  },
  props: {
    view: {
      type: String,
    },
    maxGuestsObject: {
      type: Number,
    },
    localGuests: {
      type: Object,
    },
    isMerge: {
      type: Boolean,
      default: false,
    },
    btnTitle: {
      type: String,
      default: null,
    },
    isBlockingClick: {
      type: Boolean,
      default: false,
    },
    isAnimateModal: {
      default: true,
      type: Boolean,
    },
    isNewStyle: {
      // новый стиль, задача VUECLIENT-1130
      default: false,
      type: Boolean,
    },
  },
  computed: {
    ...mapState("search", ["guests"]),
    ...mapGetters("search", ["maxGuests"]),
    guestsAdult() {
      if (this.localGuests) {
        if (this.maxGuests > this.maxGuestsObject) {
          return 1;
        } else {
          if (this.view != "page-hotel") {
            return this.countLocalGuests.adults;
          }
        }
      } else {
        return this.guests.adults;
      }
    },
    guestsChildrens() {
      if (this.localGuests) {
        if (this.maxGuests > this.maxGuestsObject) {
          return 0;
        } else {
          if (this.view != "page-hotel") {
            return this.countLocalGuests.childrens.length;
          }
        }
      } else {
        return this.guests.childrens.length;
      }
    },
    childrenText() {
      return this.guests.childrens.length >= 5
        ? this.$tc("children", { n: this.guests.childrens.length })
        : this.$tc("child", this.guests.childrens.length, { n: this.guests.childrens.length });
    },
  },
  watch: {
    isGuestsModal(val) {
      if (!val) {
        this.closeModal();
      }
    },
    "countLocalGuests.childrens": {
      deep: true,
      handler(val) {
        if (val[val.length - 1] !== null) {
          val.push(null);
        }
      },
    },
    guests(val){
      if(val.adults != this.countLocalGuests?.adults || val.childrens?.length != this.countLocalGuests?.childrens?.length){
        this.countLocalGuests = JSON.parse(JSON.stringify(this.guests))
      }
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.countLocalGuests = JSON.parse(JSON.stringify(this.guests));
    });
  },
  methods: {
    ...mapMutations("application", ["setModalsToggle"]),
    ...mapActions("search", ["updateAdults", "updateChildrens"]),
    closeModal() {
      this.$emit("guestsClose");
      this.countLocalGuests = JSON.parse(JSON.stringify(this.guests));
    },
    showModal() {
      this.isGuestsModal = true;
      this.$emit("clickGuests");
      this.$emit("click");
    },
    hideModal() {
      this.$refs.modalRef.close();
    },
    apply() {
      if (this.countLocalGuests.adults != this.guests.adults) {
        this.$store.commit("search/setAdults", this.countLocalGuests.adults);
      }
      let childs = this.countLocalGuests.childrens.filter((x) => x !== null);
      if (childs.length != this.guests.childrens.length) {
        if (childs.length > this.guests.childrens.length) {
          // childs.filter((x) => x !== null)
          for (let i = 0; i < childs.length; i++) {
            if (!this.guests.childrens[i]) {
              this.$store.commit("search/addChildrens", 10);
            }
          }
        } else if (childs.length < this.guests.childrens.length) {
          for (let i = this.guests.childrens.length; i > childs.length; i--) {
            this.$store.commit("search/deleteChildrens", i - 1);
          }
        }
      }
      if (this.countLocalGuests.age) {
        this.$store.commit("search/updateChildrens", {
          index: this.countLocalGuests.age.index,
          val: this.countLocalGuests.age.this.countLocalGuests,
        });
      }
      this.updateChildrens(childs);
      this.isGuestsModal = false;
    },
  },
  data() {
    return {
      selectGuests: null,
      countLocalGuests: null,
      isGuestsModal: false,
    };
  },
};
</script>

<style lang="scss" scoped>
.btn-ok {
  height: 42px;
}
.guests {
  &__item {
    margin-top: 15px;
  }
}
.select-guests {
  width: 100%;

  &__body {
    padding: 20px;
    z-index: 1;
    position: relative;
  }

  &--fade-enter-active,
  &--fade-leave-active {
    opacity: 1;
    transition: opacity 0.15s ease;
  }
  &--fade-enter,
  &--fade-leave-to {
    opacity: 0;
  }
  &--btn {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    cursor: pointer;
    margin: 0;
    outline-style: none;
    background: #fff;
    font-size: 16px;
    line-height: 1;
    color: #444444;
    border: none;
    width: 100%;
    border-radius: 2px;
    padding: 0 30px 0 20px;
    position: relative;
    box-shadow: inset 0 0 0 1px #fff;

    &.radius{
      border-radius: 50px;
    }

    .title {
      font-size: 14px;
      opacity: 0.5;
      margin-bottom: 4px;
    }
    .body {
      span {
        &.empty {
          color: #717171;
        }
      }
    }
    .ico-arrow {
      display: block;
      width: 12px;
      height: 6px;
      position: absolute;
      top: 50%;
      right: 24px;
      margin-top: -3px;
      transform: rotate(0deg);
      &:after,
      &:before {
        content: "";
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        background: #717171;
        height: 1px;
        width: 8px;
        margin: 2px 0 0 0;
      }
      &:after {
        transform: rotate(45deg);
      }
      &:before {
        transform: rotate(-45deg);
        margin: 2px 0 0 5px;
      }
    }
  }
  &.page-search {
    .select-guests--btn {
      &.merge {
        height: 32px;
        @media screen and (max-width: 560px){
          height: 42px;
        }
        padding: 0 15px;
        color: #000;
        border: 1px solid #d1d1d1;
        &.active {
          border: 1px solid #000;
        }
      }
    }
  }
  &.page-search,
  &.page-hotel {
    max-width: max-content;
    margin-bottom: 0;
    .select-guests--btn {
      height: 36px;
      font-size: 14px;
      border: 1px solid #d1d1d1;
      border-radius: 3px;
      white-space: nowrap;
      &.radius{
        border-radius: 50px;
      }
      .ico-arrow {
        right: 10px;
      }
    }
  }
  &.page-hotel {
    // margin-left: 2px;
    max-width: max-content;
    .select-guests--btn {
      height: 36px;
      padding: 0 26px 0 12px;
      font-size: 14px;
      min-width: 80px;
      .ico-arrow {
        right: 8px;
      }
    }
  }
  &.page-detail {
    .select-guests--btn {
      height: 36px;
      border: 1px solid #d1d1d1;
      border-radius: 3px;
      font-size: 14px;
      padding: 0 24px 0 14px;
      .ico-arrow {
        right: 12px;
      }
    }
  }
}
.custom-modal {
  ::v-deep .sc-modal-footer {
    padding: 10px 10px calc(10px + env(safe-area-inset-bottom));
  }
}
</style>
