import HTTP from "../http-common";

/**
 * Получение номеров телефона авторизованного пользователя
 *
 * @module getMyMobiles
 * @return {mobiles} Promise
 */
export default () =>
  HTTP.get("json/users/getMyMobiles", {
    headers: {
      // "api-version": 1.8 // Если указать версию, то будет минимум данных
    },
    cache: false,
  });
