<template>
  <transition name="sc-modal--fade">
    <div
      v-if="vShow || visible" 
      v-show="visible"
      class="sc-modal"
      :class="{ 'sc-modal_mobile': isMobile, 'sc-modal_desktop': !isMobile }"
    >
      <div class="sc-modal-wrap" :style="wrapStyles" :class="{loading: isLoading}">
        <div class="sc-modal-header" :class="{ light: theme === 'light' }">
          <template v-if="isMobile && isClose">
            <span
              v-if="theme === 'light'"
              class="icon-app-cross"
              @click="close()"
            ></span>
            <span v-else class="icon-app-arrow-left" @click="close()"></span>
          </template>
          <template v-else-if="isClose">
            <span
              class="icon-app-cross"
              @click="close()"
            ></span>
          </template>
          <span class="sc-modal-title">{{ title }}</span>
          <div
            v-if="!!this.$slots['side-action']"
            class="sc-modal-side-action"
          > 
            <slot name="side-action"></slot>
          </div>
        </div>
        <div v-if="isSlotHead" class="sc-modal-head">
          <slot name="head"></slot>
        </div>
        <div ref="body" class="sc-modal-body" :style="{'margin-bottom': `${footerHeight || 0}px`}">
          <slot></slot>
        </div>
        <div
          v-if="!!this.$slots['footer']"
          class="sc-modal-footer"
          ref="modalFooter"
          :class="{
            'sc-modal-footer-merge': footerMerge,
            'sc-modal-footer--box-shadow': isBoxShadowFooter,
          }"
          :style="{position: isMobile ? 'fixed' : 'relative'}"
        >
          <slot name="footer"></slot>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  name: "Modal",
  props: {
    modelValue: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: "",
    },
    theme: {
      type: String,
      default: "dark",
    },
    footerMerge: {
      type: Boolean,
      default: false,
    },
    height: {
      type: String,
      default: "auto",
    },
    minHeight: {
      type: String,
      default: "50px",
    },
    maxHeight: {
      type: String,
      default: "calc(100% - 50px)",
    },
    width: {
      type: String,
      default: "auto",
    },
    minWidth: {
      type: String,
      default: "630px",
    },
    maxWidth: {
      type: String,
      default: "calc(100% - 50px)",
    },
    imitationClose: {
      type: Boolean,
      default: false
    },
    vShow: {
      type: Boolean,
      default: false
    },
    isClose: {
      type: Boolean,
      default: true
    },
    isLoading: {
      type: Boolean,
      default: false,
    }
  },
  data() {
    return {
      visible: false,
      isMobile: false,
      isBoxShadowFooter: false,
      footerHeight: null
    };
  },
  computed: {
    wrapStyles() {
      if (this.isMobile)
        return {
          height: "100%",
          minHeight: "100%",
          maxHeight: "100%",
          width: "100%",
          minWidth: "100%",
          maxWidth: "100%"
        };
      else
        return {
          height: this.height,
          minHeight: this.minHeight,
          maxHeight: this.maxHeight,
          width: this.width,
          minWidth: this.minWidth,
          maxWidth: this.maxWidth
        };
    },
    isSlotHead() {
      return !!this.$slots["head"];
    },
    isSlotFooter() {
      return !!this.$slots["footer"];
    }
  },
  watch: {
    modelValue: function (value) {
      this.visible = Boolean(value);
      if (this.visible) {
        document.body.style.overflow = "hidden";
          this.$nextTick(() => {
            if((!!this.$slots["footer"]) && this.$refs && this.$refs.modalFooter && this.visible && this.isMobile) {
              this.footerHeight = this.$refs.modalFooter.offsetHeight
            }
          })
      } else {
        document.body.style.overflow = "";
      }
    },
  },
  mounted() {
    this.handleWindowResize();
    this.$nextTick(function () {
      window.addEventListener("resize", this.handleWindowResize);
    });
  },
  serverPrefetch() {
    const ua = this.$ssrContext.req.headers["user-agent"].toLowerCase();
    //check viewport on server side
    if (
      !/(android|bb\d+|meego).+mobile|avantgo|bada\/|blackberry|blazer|compal|elaine|fennec|hiptop|iemobile|ip(hone|od)|iris|kindle|lge |maemo|midp|mmp|mobile.+firefox|netfront|opera m(ob|in)i|palm( os)?|phone|p(ixi|re)\/|plucker|pocket|psp|series(4|6)0|symbian|treo|up\.(browser|link)|vodafone|wap|windows ce|xda|xiino/i.test(
        ua
      ) ||
      /1207|6310|6590|3gso|4thp|50[1-6]i|770s|802s|a wa|abac|ac(er|oo|s\-)|ai(ko|rn)|al(av|ca|co)|amoi|an(ex|ny|yw)|aptu|ar(ch|go)|as(te|us)|attw|au(di|\-m|r |s )|avan|be(ck|ll|nq)|bi(lb|rd)|bl(ac|az)|br(e|v)w|bumb|bw\-(n|u)|c55\/|capi|ccwa|cdm\-|cell|chtm|cldc|cmd\-|co(mp|nd)|craw|da(it|ll|ng)|dbte|dc\-s|devi|dica|dmob|do(c|p)o|ds(12|\-d)|el(49|ai)|em(l2|ul)|er(ic|k0)|esl8|ez([4-7]0|os|wa|ze)|fetc|fly(\-|_)|g1 u|g560|gene|gf\-5|g\-mo|go(\.w|od)|gr(ad|un)|haie|hcit|hd\-(m|p|t)|hei\-|hi(pt|ta)|hp( i|ip)|hs\-c|ht(c(\-| |_|a|g|p|s|t)|tp)|hu(aw|tc)|i\-(20|go|ma)|i230|iac( |\-|\/)|ibro|idea|ig01|ikom|im1k|inno|ipaq|iris|ja(t|v)a|jbro|jemu|jigs|kddi|keji|kgt( |\/)|klon|kpt |kwc\-|kyo(c|k)|le(no|xi)|lg( g|\/(k|l|u)|50|54|\-[a-w])|libw|lynx|m1\-w|m3ga|m50\/|ma(te|ui|xo)|mc(01|21|ca)|m\-cr|me(rc|ri)|mi(o8|oa|ts)|mmef|mo(01|02|bi|de|do|t(\-| |o|v)|zz)|mt(50|p1|v )|mwbp|mywa|n10[0-2]|n20[2-3]|n30(0|2)|n50(0|2|5)|n7(0(0|1)|10)|ne((c|m)\-|on|tf|wf|wg|wt)|nok(6|i)|nzph|o2im|op(ti|wv)|oran|owg1|p800|pan(a|d|t)|pdxg|pg(13|\-([1-8]|c))|phil|pire|pl(ay|uc)|pn\-2|po(ck|rt|se)|prox|psio|pt\-g|qa\-a|qc(07|12|21|32|60|\-[2-7]|i\-)|qtek|r380|r600|raks|rim9|ro(ve|zo)|s55\/|sa(ge|ma|mm|ms|ny|va)|sc(01|h\-|oo|p\-)|sdk\/|se(c(\-|0|1)|47|mc|nd|ri)|sgh\-|shar|sie(\-|m)|sk\-0|sl(45|id)|sm(al|ar|b3|it|t5)|so(ft|ny)|sp(01|h\-|v\-|v )|sy(01|mb)|t2(18|50)|t6(00|10|18)|ta(gt|lk)|tcl\-|tdg\-|tel(i|m)|tim\-|t\-mo|to(pl|sh)|ts(70|m\-|m3|m5)|tx\-9|up(\.b|g1|si)|utst|v400|v750|veri|vi(rg|te)|vk(40|5[0-3]|\-v)|vm40|voda|vulc|vx(52|53|60|61|70|80|81|83|85|98)|w3c(\-| )|webc|whit|wi(g |nc|nw)|wmlb|wonu|x700|yas\-|your|zeto|zte\-/i.test(
        ua.substr(0, 4)
      )
    ) {
      this.isMobile = false;
    } else {
      this.isMobile = true;
    }
  },
  updated() {
    if (!!this.$slots["footer"]) {
      const MODAL_BODY = this.$refs.body;
      this.isBoxShadowFooter =
        MODAL_BODY && MODAL_BODY.scrollHeight > MODAL_BODY.offsetHeight;
    } else {
      this.isBoxShadowFooter = false;
    }
  },
  beforeUnmount() {
    // Возвращаем скрол в случае скрытия модалки путем выгрузки компонента
    document.body.style.overflow = "";
  },
  methods: {
    handleWindowResize() {
      if (window.innerWidth <= 768) {
        this.isMobile = true;
      } else if (window.innerWidth > 768) {
        this.isMobile = false;
      }
    },
    show() {
      this.visible = true;
      document.body.style.overflow = "hidden";
      this.$emit("show");
      this.$emit("update:modelValue", Boolean(this.visible));
    },
    close() {
      if(this.imitationClose) {
        this.$emit("imitationClose");
      } else {
        this.visible = false;
        document.body.style.overflow = "";
        this.$emit("close");
        this.$emit("update:modelValue", Boolean(this.visible));
      }
    },
    toggle() {
      this.visible ? this.close() : this.show();
    },
  },
};
</script>
<style lang="scss">
.sc-modal--fade-enter-active,
.sc-modal--fade-leave-active {
  transform: translateX(0%);
  transition: transform 0.35s ease;
}
.sc-modal--fade-enter-from,
.sc-modal--fade-leave-to {
  transform: translateX(100%);
}
.sc-modal {
  position: fixed;
  top: 0;
  height: 100vh;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-overflow-scrolling: touch;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  overflow: hidden;
  background: rgba(0, 0, 0, 0.25);
  z-index: 200;
  &-wrap {
    position: relative;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    background-color: #ffffff;

    &.loading{
      .sc-modal-header {
        z-index: 0;
      }
    }
  }
  &-header {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
    height: 50px;
    text-align: center;
    font-size: 16px;
    line-height: 50px;
    background-color: #444444;
    color: white;
    top: 0;
    z-index: 201;
    &.light {
      background-color: #f6f6f6;
      color: #000;
      .icon-app-cross {
        color: #464646;
      }
    }
    .icon-app-arrow-left,
    .icon-app-cross {
      position: absolute;
      left: 0px;
      display: flex;
      align-items: center;
      height: 100%;
      padding-left: 20px;
    }
    .icon-app-cross {
      font-size: 10px;
    }
  }
  &-footer {
    // position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    position: relative;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    padding: 10px;
    background: #fff;
    z-index: 1;
    &-merge {
      box-shadow: none;
    }
    &--box-shadow {
      box-shadow: 0 2px 12px 0px rgba(0, 0, 0, 0.4);
    }
  }
  &-body {
    position: relative;
    flex: 1 1 auto;
    overflow-x: hidden;
    overflow-y: auto;
    color: #444444;
    // padding-bottom: 82px;
  }
  &-close {
    position: absolute;
    font-size: 64px;
    line-height: 50px;
    width: 50px;
    height: 50px;
    transform: scale(0.7);
    cursor: pointer;
    &::before,
    &::after {
      content: "";
      background-color: white;
      width: 4px;
      height: 20px;
      display: inline-block;
    }
    &::before {
      transform: rotate(45deg) translate(-6px, -10px);
      transform-origin: 100% 50%;
    }
    &::after {
      transform: rotate(-45deg);
      transform-origin: 0% 50%;
    }
  }

  .desktop & {
    .icon-app-cross {
      position: absolute;
      right: 0px;
      left: initial;
      display: flex;
      align-items: center;
      padding-left: 0;
      font-size: 14px;
      width: 40px;
      height: 50px;
      color: #a6a6a6;
      cursor: pointer;
    }
    &-wrap {
      overflow: auto;
      border: 1px solid rgba(0, 0, 0, 0.2);
      box-shadow: 0 3px 9px rgba(0, 0, 0, 0.5);
    }
    &-header {
      justify-content: space-between;
      color: #444444;
      background: #fff;
      text-indent: 10px;
    }
    &-title {
      font-size: 18px;
      color: #000;
    }
    &-close {
      right: 0;
      &::before,
      &::after {
        content: "";
        background-color: black;
        width: 1px;
        height: 30px;
        display: inline-block;
      }
      &::before {
        transform: rotate(45deg) translate(-2px, -2px);
        transform-origin: 100% 50%;
      }
      &::after {
        transform: rotate(-45deg) translate(0px, -3px);
        transform-origin: 0% 50%;
      }
    }
    &-body {
      &::-webkit-scrollbar {
        width: 6px;
        background: transparent;
      }
      &::-webkit-scrollbar-thumb {
        background: #b6b6b6;
        border-radius: 1px;
      }
      &::-webkit-scrollbar-thumb:hover {
        background: #787878;
      }
    }
    &-footer {
      justify-content: flex-end;
    }
    &--fade-enter-active,
    &--fade-leave-active {
      opacity: 1;
      transition: opacity 0.25s ease;
    }
    &--fade-enter,
    &--fade-leave-to {
      opacity: 0;
    }
  }

  .sc-modal_mobile & {
    &-title {
      margin: 0 52px 0 60px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }
}
</style>