<i18n>
{
	"ru": {
    "night": "за сутки|за {n} сутки|за {n} суток|за {n} суток",
    "rankSuper": "Суперхозяин",
    "reviews": "отзывов | {n} отзыв | {n} отзыва | {n} отзывов",
    "meter": "м²"
	},
  "en": {
    "night": "per day | for {n} days | for {n} days | for {n} days",
    "rankSuper": "Superhost",
    "reviews": "reviews | {n} reviews | {n} reviews | {n} reviews",
    "meter": "m²"
  }
}
</i18n>

<template>
  <transition name="fade">
    <Carousel
      ref="slider"
      class="object-list"
      :settings="{
        itemsToShow: 1,
        transition: 300,
      }"
      @init="initCarousel"
      @slide-end="slide"
    >
      <Slide v-for="item in results" :key="item.id">
        <div
          ref="link"
          class="block-wrapper"
          @touchend="clickObjectBronevik($event, item.id, item)"
        >
          <span
            v-if="
              item.owner.permissions.includes('showSuperhostLabelAbility')
            "
            class="superhost"
          >
            {{ $t("rankSuper") }}
          </span>
          <a
            href="#"
            target="_blank"
            @click="linkHandler(route)"
          >
            <div class="block">
              <img
                class="block--image"
                :src="item.media && item.media[0].replace('medium', 'small')"
                alt=""
              />

              <div class="block-line--wrapper">
                <div class="block-title tmp-font--medium">
                  {{ item.name ? item.name : item.title }}
                </div>
                <ObjectInformation class="block__desc" :item="item" />
                <div class="block-line block-price">
                  <div
                    v-if="isBronevik(Number(item.id))"
                    class="loading-price-bronevik"
                  >
                    <img src="~/assets/img/loader-bronevik-gray.gif" alt="" />
                  </div>
                  <template v-else>
                    <BasePriceBronevik
                      v-if="priceBronevik(Number(item.id))"
                      :priceBronevik="priceBronevik(Number(item.id))"
                      :isMap="true"
                    />
                    <div v-else class="price">
                      <span class="price-value" v-if="days === 1">
                        {{ formatedPrice(item.price.per_night) }}
                      </span>
                      <span class="price-value" v-else>
                        {{ formatedPrice(item.price.full) }}
                      </span>
                      <span class="price-day">
                        {{ $tc("night", days === 1 ? 0 : days) }}
                      </span>
                    </div>
                  </template>
                </div>
              </div>
              <LoadingPrice v-if="false" />
            </div>
          </a>
        </div>
      </Slide>
    </Carousel>
  </transition>
</template>
<script>
import { mapGetters, mapState, mapActions, mapMutations } from "vuex";
import { Carousel, Slide } from "vue3-carousel";
import LoadingPrice from "@/components/base/LoadingPrice.vue";
import BasePriceBronevik from "@/components/base/BasePriceBronevik";
import ObjectInformation from "./ObjectInformation.vue";
import "vue3-carousel/dist/carousel.css";

export default {
  name: "ObjectListMap",
  components: {
    Carousel,
    Slide,
    LoadingPrice,
    BasePriceBronevik,
    ObjectInformation,
  },
  props: {
    results: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      isPriceBronevik: true,
    };
  },
  computed: {
    ...mapState("search", [
      "selectedMapPointId",
      "guests",
      "loadBronevikPrice",
      "objectsBronevik",
      "pricesBronevik",
    ]),
    ...mapGetters("search", ["days", "occupied", "getQuery"]),
  },
  methods: {
    ...mapMutations("search", ["setSelectedMapPointId"]),
    // eslint-disable-next-line no-unused-vars
    slide({ currentSlideIndex, prevSlideIndex, slidesCount }) {
      const id = this.results[currentSlideIndex]?.id;
      this.setSelectedMapPointId(id);
    },
    formatedPrice(price) {
      return new Intl.NumberFormat(this.$i18n.locale, {
        style: "currency",
        currency: "RUB",
        minimumFractionDigits: 0,
      }).format(price);
    },
    linkHandler(route, href) {
      window.open(href, "_blank");
    },
    priceBronevik(id) {
      let idBronevik = this.objectsBronevik.find((item) => item == id);
      if (idBronevik) {
        return this.pricesBronevik &&
          this.pricesBronevik[idBronevik] &&
          this.pricesBronevik[idBronevik]["price"]
          ? this.pricesBronevik[idBronevik]["price"]
          : 0;
      } else {
        return 0;
      }
    },
    isBronevik(id) {
      if (this.objectsBronevik.findIndex((item) => item == id) == -1) {
        return false;
      } else {
        if (this.loadBronevikPrice) {
          return true;
        } else {
          return false;
        }
      }
    },
    isDisabledLink(id) {
      let idBronevik = this.objectsBronevik.find((item) => item == Number(id));
      if (idBronevik) {
        let prices =
          this.pricesBronevik &&
          this.pricesBronevik[idBronevik] &&
          this.pricesBronevik[idBronevik]["prices"]
            ? this.pricesBronevik[idBronevik]["prices"]
            : [];
        if (prices && prices.length) {
          return false;
        } else {
          return true;
        }
      } else {
        return false;
      }
    },
    clickObjectBronevik(event, id, item) {
      if (this.isDisabledLink(id)) {
        event.preventDefault();
      }
      let idBronevik = this.objectsBronevik.find((item) => item == Number(id));
      if (idBronevik) {
        let prices =
          this.pricesBronevik &&
          this.pricesBronevik[idBronevik] &&
          this.pricesBronevik[idBronevik]["prices"]
            ? this.pricesBronevik[idBronevik]["prices"]
            : [];
        if (prices && prices.length) {
          event.preventDefault();
          window.open(prices[0].link);
        }
      }
    },
    countReviewsExternal(external_reviews) {
      const externalReviews = external_reviews;
      if (externalReviews) {
        const countAirbnb = externalReviews[0]["count"]
          ? externalReviews[0]["count"]
          : 0;
        const countBooking = externalReviews[1]["count"]
          ? externalReviews[1]["count"]
          : 0;

        return countAirbnb + countBooking;
      } else {
        return 0;
      }
    },
    initCarousel() {
      const slide = this.results.findIndex(
        (v) => v.id == this.selectedMapPointId
      );
      if (slide !== -1) {
        this.$refs.slider.slideTo(slide);
      }
    },
  },
  watch: {
    selectedMapPointId() {
      this.$refs.slider.slideTo(
        this.results.findIndex((v) => v.id == this.selectedMapPointId)
      );
    },
  },
};
</script>
<style lang="scss" scoped>
.object-list.carousel {
  position: absolute;
  z-index: 1;
  bottom: 15px;
  left: 20px;
  height: 125px;
  width: 100%;
  width: calc(100% - 40px);
  text-align: inherit;
  :deep(.carousel__viewport) {
    overflow: unset;
  }
  a {
    text-decoration: none;
    &:hover {
      text-decoration: none;
    }
  }
  .carousel__slide {
    padding: 0 5px;
    .block {
      height: 106px;
      min-width: 95px;
      background: white;
      color: black;
      border-radius: 8px;
      box-shadow: 0 0 12px 0 rgba(0, 0, 0, 0.2);
      overflow: hidden;
      display: flex;
      position: relative;
      &-wrapper {
        position: relative;
        width: 100%;
        height: 100%;
      }
      &__desc {
        padding-left: 10px;
      }
      &-line {
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        padding-left: 10px;

        &--wrapper {
          width: calc(100% - 95px);
          position: relative;
          padding: 10px 0;
          &::before {
            content: "";
            display: block;
            position: absolute;
            right: 0;
            width: 10px;
            background: linear-gradient(270deg, white, #ffffff36);
            top: 0;
            bottom: 0;
          }
        }
      }
      &-labels {
        margin-bottom: 3px;
        color: #7f7f7f;
        padding-right: 36px;
      }
      &-title {
        margin-bottom: 10px;
        padding-right: 40px;
        padding-left: 10px;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        overflow: hidden;
      }
      &-price {
        display: flex;
        align-items: flex-end;
        position: absolute;
        bottom: 8px;
        width: 100%;
        .star {
          margin-left: auto;
          padding-right: 10px;
          // position: absolute;
          // right: 0;
          font-size: 12px;
          &-grey {
            color: #717171;
          }
          .rating-val {
            font-weight: bold;
            font-size: 14px;
            margin-left: 2px;
          }
        }
        .price {
          text-overflow: ellipsis;
          white-space: nowrap;
          overflow: hidden;
          color: #000000;
          &-value {
            font-size: 14px;
            font-weight: bold;
          }
          &-day {
            font-size: 12px;
          }
        }
      }
      &--image {
        height: 100%;
        width: 96px;
        min-width: 96px;
        object-fit: cover;
        display: inline-block;
      }
      .favorite-btn {
        color: #000000;
        top: 18px;
        right: 6px;
        ::v-deep {
          .icon-app-heart-tile {
            margin: 0;
            font-weight: 800;
            font-size: 14px;
          }
          .icon-app-heart-tile-fill {
            font-size: 14px;
          }
        }
      }
    }
  }
}
.icon-app-star {
  color: #fdb12e;
  font-size: 18px;
  position: relative;
  top: 3px;
  margin: 0 -1px;
  &.grey {
    color: grey;
  }
}
.hotel {
  align-self: flex-start;
  border: 1px solid #d8d8d8;
  border-radius: 2px;
  color: #444444;
  justify-content: center;
  font-size: 10px;
  line-height: 16px;
  padding: 0px 6px;
  text-transform: uppercase;
}
.superhost {
  // display: inline-block;
  // text-overflow: ellipsis;
  // white-space: nowrap;
  // line-height: 18px;
  // font-size: 9px;
  // border-radius: 2px;
  // height: 18px;
  // padding: 0 5px;
  // border-radius: 2px;
  // background-color: #ffe6b6;
  // color: #000;
  // text-transform: uppercase;
  position: absolute;
  top: -10px;
  left: 9px;

  border-radius: 9px;
  color: #fff;
  font-size: 9px;
  font-weight: 600;
  height: 20px;
  line-height: 18px;
  padding: 0 9px 0 10px;
  text-align: center;
  width: 80px;
  background-color: #ffa900;
  z-index: 2;
}
.loading-price-bronevik {
  height: 12px;
  position: relative;
  img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 48px;
    margin: 0 0 0 28px;
  }
}
:deep(.warning-broneevik) {
  .icon-app-clock,
  .icon-app-clock {
    display: none;
  }
  padding: 6px 6px;
  margin: -5px 8px 0;
  position: relative;
  z-index: 100;
}
:deep(.warning-broneevik--text) {
  span {
    display: none;
  }
}
</style>
