<template>
  <div class="switch-view" :class="viewWidget">
    <button @click="setViewResult('list')" :class="{'selected': viewResult === 'list'}">
      <img src="~assets/img/icon-list-filter.svg" alt="">
      Списком
    </button>
    <button @click="setViewResult('map')" :class="{'selected': viewResult === 'map'}">
      <img src="~assets/img/icon-map-filter.svg" alt="">
      На карте
    </button>
  </div>
</template>

<script>
import {mapMutations, mapState} from "vuex";

export default {
  name: "SearchViewSwitch",
  computed: {
    ...mapState("search", ["viewResult"]),
  },
  methods: {
    ...mapMutations("search", ["setViewResult"]),
  }
}
</script>

<style lang="scss" scoped>
.switch-view {
  padding: 4px;
  border-radius: 4px;
  background-color: #f1f0ed;
  display: flex;
  button {
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    padding: 0 10px;
    margin: 0;
    height: 28px;
    border: none;
    background: transparent;
    border-radius: 4px;
    font-size: 14px;
    width: 104px;
    justify-content: center;
    position: relative;
    z-index: 2;
    transition: background .3s ease;
    img {
      margin-right: 7px;
      margin-top: 1px;
    }
    &.selected {
      background: #fff;
    }
    .adaptive & {
      width: 100%;
    }
  }
}
</style>
