<template>
  <div class="sutochno-widget-container" :class="viewWidget">
    <div class="sutochno-widget" :class="viewWidget">
      <div class="sutochno-widget--description">
        <div class="sutochno-slogan">Найдём, где остановиться</div>
        <div class="sutochno-options">
          200 тысяч вариантов жилья для поездок по России и зарубежью
        </div>
      </div>
      <div class="sutochno-widget--logo">
        <img src="~/assets/img/sutochno-logo-horizontal.svg" alt="" />
      </div>
      <div class="sutochno-widget--search">
        <SearchMainAdaptive v-if="viewWidget === 'adaptive'" />
        <SearchMain v-else />
      </div>
      <div class="sutochno-widget--filter">
        <Filter />
      </div>
      <div v-if="viewWidget !== 'adaptive'" class="sutochno-widget--view">
        <SearchViewSwitch />
      </div>
    </div>
    <SearchViewSwitch v-if="viewWidget === 'adaptive'" />
    <Result v-if="visibleResult" />
  </div>
</template>

<script>
import SearchViewSwitch from "@/components/Search/SearchViewSwitch/";
import Filter from "@/components/Search/Filter";
import SearchMain from "@/components/Search/SearchMain";
import SearchMainAdaptive from "@/components/Search/SearchMainAdaptive";
import Result from "@/components/Search/Result";
import {mapMutations, mapState} from "vuex";

export default {
  name: "App",
  components: {
    SearchViewSwitch,
    Filter,
    SearchMain,
    SearchMainAdaptive,
    Result,
  },
  mounted() {
    this.$nextTick(function () {
      window.addEventListener("resize", this.handleWindowResize);
      this.handleWindowResize();
    });
  },
  computed: {
    ...mapState("search", ["visibleResult", "viewWidget"]),
  },
  unmounted() {
    window.removeEventListener("resize", this.handleWindowResize);
  },
  methods: {
    ...mapMutations("search", ["setViewWidget"]),
    handleWindowResize() {
      let viewWidget = null;
      if (window.innerWidth <= 980) {
        viewWidget = "adaptive";
      } else if (window.innerWidth > 980) {
        viewWidget = "desktop";
      }
      this.setViewWidget(viewWidget);
    },
  }
};
</script>

