<i18n>
{
	"ru": {
		"onNoMore": "К сожалению больше объектов нет",
    "onNoResults": "К сожалению ничего не найдено",
    "find": "Найдено",
    "button": "Выберите даты"
	},
	"en": {
		"onNoMore": "Unfortunately there are no more objects",
    "onNoResults": "Sorry, nothing was found",
    "find": "Found",
    "button": "Select dates"
	}
}
</i18n>
<template>
  <div class="card-list">
    <div v-if="results" class="card-list__wrapper">
      <template v-for="(list, val) in results" :key="val">
        <template v-for="(item, index) in list" :key="item.id">
          <ObjectBlock
            :class="{
              'card--selected': item.id === selectedMapPointId,
              'card--selected': item.id === hoverMapPointId,
              'card--selected': item.id === hoverPlacemark,
            }"
            :i="val + index"
            :object-data="item"
            :ref="`object_${item.id}`"
            :rating="
              avgReviewsRatings &&
              avgReviewsRatings.find((rate) => rate.object_id == item.id)
                ? avgReviewsRatings.find((rate) => rate.object_id == item.id)
                : null
            "
            :isBronevik="
              item.owner && item.owner.id && item.owner.id === 4784337
                ? true
                : false
            "
            @mouseenter="onEnter(item.id)"
            @mouseleave="onLeave"
          />
        </template>
      </template>
    </div>
  </div>
</template>
<script>
import ObjectBlock from "./ObjectBlock.vue";
import Banner from "@/components/base/Banner";
import { mapState, mapGetters, mapActions, mapMutations } from "vuex";
import { Messages, Users } from "@/api/index.js";
import _ from "lodash";

export default {
  name: "ObjectsList",
  components: {
    ObjectBlock,
    Banner,
  },
  props: {
    results: {
      type: Array,
    },
    adblock: {
      type: Number,
      default: 0,
    },
    scrollStop: {
      type: Boolean,
      default: false,
    },
    hoverPlacemark: {
      type: Number,
      default: 0,
    },
  },
  data() {
    return {
      userSearchParams: [],
      avgReviewsRatings: [],
    };
  },
  mounted() {
    this.drawByResults();
  },
  methods: {
    ...mapActions("search", ["setUsersSearchParams", "setAvgReviewsRatings"]),
    ...mapMutations("search", ["flagMutate"]),
    choiceDates() {
      const datepicker = document.getElementById("datepickerSearch");
      if (datepicker) {
        setTimeout(() => {
          datepicker.click();
        }, 0);
      }
    },

    // Данные об арендодателях
    getUsersSearchParams() {
      const usersId = Object.entries(this.results).forEach(list => {
        list[1].map((item) => {
          return Number(item.owner.id);
        });
      });

      Users.getUsersSearchParams(_.uniqBy(usersId)).then((res) => {
        this.userSearchParams = res.data.data.users;
      });
    },

    //Данные о рейтинге и отзывах объектов
    getAvgReviewsRatings() {
      Messages.getAvgReviewsRatings(this.objectIds).then((res) => {
        this.avgReviewsRatings = res.data.data.objects;
      });
    },

    onEnter(ID) {
      this.$emit("hoverObject", ID);
    },
    onLeave() {
      this.$emit("hoverObject", null);
    },
    drawByResults() {
      if (this.results) {
        // FIX
        if (true) {
          this.getUsersSearchParams();
          this.getAvgReviewsRatings();
        }
      }
    },
  },
  computed: {
    ...mapState("search", [
      "selectedMapPointId",
      "hoverMapPointId",
      "totalFound",
      "guests",
      "isPosition",
      "checkIn",
      "checkOut",
    ]),
    ...mapGetters("search", ["maxGuests"]),
    objectIds() {
      const objectIds = [];
      Object.entries(this.results).forEach(list => {
        list[1].map((item) => {
          objectIds.push(Number(item.id));
          return item;
        });
      });
      return objectIds;
    },
    totalFoundText() {
      return `${this.$t("find")} ${this.totalFound}`;
    },
  },
  watch: {
    results() {
      this.drawByResults();
    },
  },
};

const simple = [
  [
    {
      name: "s",
      value: false,
    },
    {
      name: "g",
      value: false,
    },
  ], // s,g - length 2
  [
    {
      name: "l",
      value: false,
    },
  ], // l,b,a,z,s - length 1
  [
    {
      name: "d",
      value: false,
    },
  ], // d,2,p,a - length 1
  [
    {
      name: "c",
      value: false,
    },
    {
      name: "p",
      value: false,
    },
  ], // c,p,g - length 2
  [
    {
      name: "e",
      value: false,
    },
    {
      name: "v",
      value: false,
    },
  ], // e,v - length 2
];
</script>

<style lang="scss" scoped>
.card-list {
  width: 100%;
  &__options {
    height: 30px;
    display: none;
    align-items: center;
    // margin-bottom: 20px;
    @media screen and (max-width: 1459px) {
      display: flex;
    }
  }
  &__total-found {
    color: #7f7f7f;
    font-size: 14px;
    line-height: 18px;
  }
  &__banner {
    margin-bottom: 20px;
  }
}
</style>
