export default class YandexAds {
  static isLoad = false;
  static QueueRender = [];

  constructor(blockId, renderTo) {
    const link = "//an.yandex.ru/system/context.js";
    const isLoadedScript = document.querySelector(`script[src*="${link}"]`)

    if(isLoadedScript && YandexAds.isLoad) {
      console.log("YandexAds: render (blockId", blockId, ", renderTo", renderTo, ")");
      Ya.Context.AdvManager.render({ blockId: blockId, renderTo: renderTo, async: true });
    } else if (isLoadedScript && YandexAds.isLoad) {
      console.log("YandexAds: Add queue (blockId", blockId, ", renderTo", renderTo, ")");
      YandexAds.QueueRender.push(() => {
        Ya.Context.AdvManager.render({ blockId: blockId, renderTo: renderTo, async: true });
      })
    } else {
      console.log("YandexAds: Add queue (blockId", blockId, ", renderTo", renderTo, ")");
      YandexAds.QueueRender.push(() => {
        Ya.Context.AdvManager.render({ blockId: blockId, renderTo: renderTo, async: true });
      })
      console.log("YandexAds: Loading");
      const yandexAdsScript = document.createElement('script');
      yandexAdsScript.setAttribute('src', link);
      yandexAdsScript.setAttribute('async', '');
      yandexAdsScript.setAttribute('defer', '');
      document.body.appendChild(yandexAdsScript);
      yandexAdsScript.onload = () => {
        console.log("YandexAds: Loaded");
        YandexAds.isLoad = true;
        // Выполняем редер очереди
        YandexAds.QueueRender.forEach(f => f());
      };
    }
  }
}