<i18n>
{
	"ru": {
		"cityOrCountry": "Город, страна",
		"historySearch": "История поисков"
	},
	"en": {
		"cityOrCountry": "City, country",
		"historySearch": "Search history"
	}
}
</i18n>
<template>
  <div class="choice-city">
    <div class="choice-city-header">
      <input
        ref="input"
        v-model="textSearch"
        v-focus
        type="text"
        :placeholder="$t('cityOrCountry')"
        @keyup.enter.prevent
        @submit.prevent
      />
      <div
        v-show="textSearch"
        class="choice-city-header--reset icon-app-cross-min"
        @click="onReset()"
      ></div>
    </div>

    <!-- Города из поиска -->
    <template v-if="citySuggestions.length > 0">
      <template v-for="value in citySuggestions" :key="value.id">
        <div class="choice-city-elem" @click="apply(value)">
          <div class="choice-city-elem--title">
            {{ value.title }}
            <div v-if="value.region">{{ value.region }}</div>
            <div v-else-if="value.sight_id">{{ value.city }}</div>
            <div v-else-if="value.region_id">{{ value.country }}</div>
          </div>
          <div v-if="value.count" class="choice-city-elem--count">
            {{ new Intl.NumberFormat("ru-RU").format(value.count) }}
          </div>
        </div>
      </template>
    </template>
    <!-- Города из истории -->
    <template v-else-if="cityHistory.length > 0">
      <div class="choice-city-title">{{ $t("historySearch") }}</div>
      <template v-for="value in cityHistory" :key="value.id">
        <div class="choice-city-elem" @click="apply(value)">
          <div class="choice-city-elem--history"></div>
          <div class="choice-city-elem--title">
            {{ value.title }}
            <div v-if="value.region">{{ value.region }}</div>
          </div>
          <div v-if="value.count" class="choice-city-elem--count">
            {{ new Intl.NumberFormat("ru-RU").format(value.count) }}
          </div>
        </div>
      </template>
    </template>
  </div>
</template>
<script>
import _ from "lodash";
import { HTTP } from "@/api/index.js";
import { CancelToken } from "axios";
import { mapState, mapGetters } from "vuex";

export default {
  name: "CityChoice",
  components: {},
  mounted() {
    if (this.$route.name != "Main") {
      this.textSearch = this.location.title;
    }
    this.textSearch = this.$store.state.search.term || this.textSearch;
  },
  methods: {
    onReset() {
      this.textSearch = "";
      this.$refs.input.focus();
    },
    apply(cityData) {
      if (cityData.type == "object") {
        window.open(cityData.url);
      } else {
        if (!this.cityHistory.find((city) => city.id == cityData.id))
          localStorage.setItem(
            "cityHistory",
            JSON.stringify([cityData, ...this.cityHistory])
          );
        localStorage.setItem("city", JSON.stringify(cityData));

        if (cityData.type == "sight" || cityData.sight_id) {
          this.$store.dispatch("search/setLocation", {
            id: cityData.city_id,
            type: "city",
            sight: cityData,
          });

          this.sight = {
            ...this.sight,
            map: {
              lat: cityData.point.lat,
              lng: cityData.point.lon,
              title: cityData.title,
              zoom: 16,
            },
          };

          this.$store.commit("search/setView", "map");
          this.$store.commit("search/setMap", this.sight.map);
          this.$store.commit("search/setPlace", this.sight.map);
        } else {
          this.sight = null;
          this.$store.commit("search/setPlace");
          this.$store.dispatch("search/setLocation", cityData);
        }

        //Close modal
        this.$parent.close();
        this.$emit("update:modelValue", cityData);
        this.textSearch =
          cityData.title ||
          (this.location.sight
            ? this.location.sight.title
            : this.location.title) ||
          cityData.city;
        this.$store.commit("search/setTerm", this.textSearch);
        this.isVisible = false;
      }
    },
    getTermSuggestions(request) {
      if (this.cancelSource) {
        this.cancelSource.cancel("Operation canceled by the user.");
      }
      this.cancelSource = CancelToken.source();
      HTTP.get("rest/search/getTermSuggestions?query=" + request, {
        headers: {
          "api-version": String(null), // Если указать версию, то будет минимум данных
        },
        cancelToken: this.cancelSource.token,
      })
        .then((response) => {
          this.citySuggestions = response.data.data.suggestions;
        })
        .catch((error) => {
          console.log("Ajax error:", error);
        });
    },
  },
  watch: {
    textSearch: {
      handler: _.debounce(function (value) {
        if (value.trim().length > 0) {
          this.getTermSuggestions(value);
        }
      }, 500),
      deep: true,
    },
  },
  computed: {
    ...mapState("search", ["location", "map", "view"]),
    ...mapGetters("search", ["getQuery"]),
  },
  data() {
    return {
      textSearch: this.currentCity,
      citySuggestions: [],
      cityHistory: JSON.parse(localStorage.getItem("cityHistory") || "[]"),
      cancelSource: null,
      sight: {
        map: {},
      },
    };
  },
};
</script>
<style lang="scss" scoped>
.choice-city {
  &-header {
    background-color: #444444;
    padding: 0px 10px 10px 10px;
    position: sticky;
    top: 0;
    input {
      border-radius: 2px;
      width: 100%;
      padding: 15px;
      border-color: transparent;
      &:focus {
        outline-style: none;
        -webkit-box-shadow: none;
        box-shadow: none;
      }
    }
    &--reset {
      position: absolute;
      top: 18px;
      right: 24px;
      color: #464646;
    }
  }
  &-title {
    padding: 0 10px;
  }
  &-elem {
    display: flex;
    font-size: 16px;
    align-items: center;
    padding: 5px 10px;
    cursor: pointer;

    &:active {
      background-color: #99999930;
    }
    &--count {
      margin-left: auto;
      color: #717171;
    }
    &--history {
      background-image: url("~assets/img/time-left.svg");
      background-repeat: no-repeat;
      width: 24px;
      height: 16px;
    }
    &--title {
      color: black;
      div {
        font-size: 13px;
        color: #717171;
        line-height: 1;
      }
    }
  }
}
</style>
