import HTTP from "../http-common";

/**
 * Подтверждение контакта (повторная отправка)
 *
 * @module users
 *
 * @return {object} Promise
 *
 */
export default (formData) => {
  return HTTP.post("/json/users/requestApproveContact/", formData, {
    headers: {
      "api-version": "1"
    },
    cache: false,
  });
};
