export default function (Component) {
  Component.__i18n = Component.__i18n || []
  Component.__i18n.push({
    "locale": "",
    "resource": {
      "ru": {
        "cityOrCountry": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Город, страна"])},
        "historySearch": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["История поисков"])}
      },
      "en": {
        "cityOrCountry": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["City, country"])},
        "historySearch": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Search history"])}
      }
    }
  })
}
