<i18n>
{
  "en": {
    "clearFilters": "Clear filter",
    "noVariants": "No variants",
    "noVariantsDescription": "No listing meeting your conditions was found.",
    "open": "Open the map",
    "close": "Show a list of objects",
    "searchCurrentArea": "Search here…",
    "from": "from "
  },
  "ru": {
    "clearFilters": "Показать доступные варианты",
    "noVariants": "В этой области нет вариантов",
    "noVariantsDescription": "Сдвиньте карту или сбросьте фильтры, чтобы увидеть предложения.",
    "open": "Раскрыть карту",
    "close": "Показать список объектов",
    "searchCurrentArea": "Искать здесь…",
    "from": "от "
  }
}
</i18n>

<template>
  <YandexMap
    class="map-mobile"
    :coords="mapCenter"
    :zoom="mapZoom"
    :custom-events="customEventsMap"
    :controls="[]"
    :settings="{
      apiKey,
    }"
    :options="{
      minZoom: 5,
    }"
    @ready="onInitMap"
    @update:customEvents="updateCustomEvents"
    @boundschange="boundschange"

  >
    <!-- <div v-if="!switchMap" class="map-mobile--viewer" @touchstart="viewMap()" @touchmove="viewMap()"></div> -->
    <Placemark :placemarks="placemarks" @click="onClick" />
    <Placemark
      v-if="place.lat && place.lng"
      key="imageFlag"
      :placemarks="{
        imageFlag: {
          coords: [place.lat, place.lng],
          typeMark: 'image',
          options: {
            iconImageHref: require('assets/img/flag.png'),
            iconImageSize: [28, 30],
            iconImageOffset: [0, -30],
            zIndex: 700,
          },
        },
      }"
    />
    <ObjectListMap v-if="selectedMapPointId" :results="results" />
    <Warn v-if="totalFound == 0 && switchMap && !isLoading" />
  </YandexMap>
</template>

<script>
import _ from "lodash";
// this.updateSwitchMap(true);
import { YandexMap, Placemark } from "@sutochno/YandexMap";

import { mapState, mapGetters, mapMutations } from "vuex";
// import BlockUpdate from "@/components/mobile/NewSearch/BlockUpdate.vue";
import ObjectListMap from "./ObjectListMap";
import Warn from "./Warn";

import Supercluster from "supercluster";

import { Objects } from "@/api/index.js";

export default {
  name: "Search",
  components: {
    // BlockUpdate,
    YandexMap,
    Placemark,
    ObjectListMap,
    Warn,
  },
  props: {
    results: {
      require: true,
      type: Array,
    },
    status: {
      type: String,
      require: true,
    },
    zoom: {
      type: Number,
      require: true
    },
    isSection: {
      type: Boolean,
      default: false
    },
    isLoading: {
      type: Boolean,
      default: false
    },
  },
  data() {
    return {
      $myMap: null,
      mapZoom: 13,
      mapCenter: [55.745161166568124, 37.62050512070092],
      apiKey:
        process.env.NODE_ENV === "development"
          ? "ed03bb3a-3f94-48b0-a552-7a2b6d165870"
          : this.$route.name === "Detail" ? "0614d94c-41a4-486b-8813-86ee84ce2b0c"
          : "b4456580-40d3-4951-a3fe-e14527fbfa0e",
      //           this.$localStorage &&
      // this.$localStorage.get("adminInfo", {}, Object).isSearchGeocoderMap
      //   ? "590046a8-de02-4e92-bef6-dcb4d64df9c5"
      //   : "",
      cancelSource: null,
      hoverPlacemark: null,
      recentlyResults: [], // Выборка выдачи для карты
      historyMapActivity: {},
      lockUpdate: false, // Блокирвока между анимациями
      isNoBlockUpdate: true, // Блокировка обновления пользователем
      unwatch: [],
      favoritesPoints: [],
      firstObject: null,
      isMoved: false, // Сдвинул карту?
      isLoadMap: false,
      customEventsMap: ["actionbegin", "actionend"],
    };
  },
  watch: {
    results (val) {
      if (val && val.length) {
        this.firstObject = val[0].id;
      }
    },
    isNoBlockUpdate (val) {
      if (!this.isSection && val === true) {
        this.$emit('onIsSection', true);
      }
    },
    zoom (val) {
      this.mapZoom = val;
    },
    switchMap(val) {
      if (!val) {
        this.closeMap();
        for (const key of Object.keys(this.historyMapActivity)) {
          this.historyMapActivity[key] = "visited";
        }
        this.setSelectedMapPointId(null);
      } else if (this.firstObject) {
        this.historyMapActivity[this.firstObject] = "active";
        this.setSelectedMapPointId(this.firstObject);
      }
    },
    historyMapActivity(value) {
      localStorage.setItem("historyMapActivity", JSON.stringify(value));
    },
    selectedMapPointId: function (value, oldValue) {
      // Помечаем метку как посещеную
      if (oldValue) {
        this.historyMapActivity[oldValue] = "visited";
      } else if (this.firstObject) {
        this.historyMapActivity[this.firstObject] = "visited";
      }
      if (value) {
        this.historyMapActivity[value] = "active";
      }
    },
    bound: {
      deep: true,
      handler: function (bound) {
        if (!this.myMap) {
          // console.warn("Попытка перемещения карты до загрузки карты");
          return;
        }
        const { center, zoom } = ymaps.util.bounds.getCenterAndZoom(
          [
            [bound.SW.lat, bound.SW.lng],
            [bound.NE.lat, bound.NE.lng],
          ],
          this.myMap.container.getSize(),
          ymaps.projection.wgs84Mercator,
          {
            inscribe: false,
          }
        );

        if (zoom !== this.mapZoom) {
          this.mapZoom = zoom;
        }
        this.mapCenter = center;
      },
    },
    mapCenter(val) {
      this.$emit("mapCenter", val);
    },
    mapZoom(val) {
      this.$nextTick(() => {
        const bound = this.myMap.getBounds();
        this.setBound({
          NE: {
            lat: bound[1][0],
            lng: bound[1][1],
          },
          SW: {
            lat: bound[0][0],
            lng: bound[0][1],
          },
        });
      });

      this.$emit("mapZoom", val);
    },
    isLoadMap(val) {
      this.$emit("onLoadMap", val);
    },
    isMoved(val) {
      this.$emit("update:modelValue", val)
    },
    term() {
      setTimeout(() => {
        this.isMoved = false;
      }, 1000);
    },
  },
  computed: {
    ...mapState("search", [
      "filters",
      "totalFound",
      "location",
      "selectedMapPointId",
      "place",
      "bound",
      "switchMap",
      "isDefaultLocationRender",
      "term",
        "checkIn",
        "checkOut",
        "objectsBronevik",
        "pricesBronevik",
    ]),
    ...mapGetters("search", ["occupied", "getQuery"]),
    ...mapGetters("search", { maxGuests: "maxGuests" }),

    placemarks() {
      const points = JSON.parse(JSON.stringify(this.results || []));

      // SUPECLUSTER
      const geoPoints = points.map((p) => ({
        type: "Feature",
        id: p.id,
        geometry: {
          type: "Point",
          coordinates: [p.lng, p.lat],
        },
      }));
      const index = new Supercluster({
        // Примерно подабрал чтобы вблизи агресивнее брались метки в кластер
        // Есть кейсы где можно сказать метки на разные поъезды и в каждом по много объектов
        radius: this.mapZoom > 15 ? (10 * 1) / (1.5 ^ this.mapZoom) + 10 : 0, // когда зум маленький точки слишком далеко разбрасываются
        minZoom: 8,
        maxZoom: 19,
      });
      index.load(geoPoints);
      // ЭТО ПРАВИЛЬНО, НО БУДЕТ БАГА ТОГДА ТК BOUNDS поменяться быстрее чем с апи придет
      // const cluster = index.getClusters([this.bound.SW.lng, this.bound.SW.lat, this.bound.NE.lng, this.bound.NE.lat], this.mapZoom);
      // БЕРЕМ ВСЮ КАРТУ ИБО НЕХРЕН
      const cluster = index.getClusters([-180, -85, 180, 85], this.mapZoom);

      //SMART CONST
      const offsetZoom = {
        19: 0.00009,
        18: 0.0002,
        17: 0.00025,
        16: 0.0003,
        15: 0.0008,
        14: 0.002,
        13: 0.0035,
        12: 0.005,
        11: 0.006,
        10: 0.008,
        9: 0.00975,
        8: 0.0125,
        7: 0.125,
        // Дальше смысла нет
      };

      const meanPoints = cluster.map((p) => {
        if (p.properties && p.properties.cluster) {
          const [cLng, cLat] = p.geometry.coordinates;
          const cPoints = index.getLeaves(p.properties.cluster_id, Infinity); // Вынимаем из кластера все точки
          const nums = cPoints.length;
          const r = offsetZoom[this.mapZoom] || 0.2; // Радиус измеряеться в градусах

          // Перестановка точек по окружности
          cPoints.forEach((cPoint, index) => {
            const path = ((2 * Math.PI) / nums) * index;
            const newLng = cLng + r * Math.cos(path);
            const newLat =
              cLat + Math.sin((cLat * Math.PI) / 180) * r * Math.sin(path);

            cPoint.geometry.coordinates = [newLng, newLat];
            // cPoint.angle = `${-((path/(2*Math.PI))*360  - 90)}deg`
          });
          return cPoints;
        }
        return p;
      });
      const fixPoints = _.flatten(meanPoints).reduce((acc, p) => {
        acc[p.id] = {
          lat: p.geometry.coordinates[1],
          lng: p.geometry.coordinates[0],
        }; //, angle: p.angle
        return acc;
      }, {});
      return points.reduce((acc, value) => {

        const { lat, lng } = fixPoints[value.id] || value; // FALLBACK так на всякий случай
        // const angle = fixPoints[value.id] && fixPoints[value.id].angle || "0deg"
        // style="transform: translate(-50%, -100%) rotate(${angle}); transform-origin: 50% 30px;"

        const classHover = this.hoverPlacemark == value.id ? "hover" : "";
        const classHistory = this.historyMapActivity[value.id] || "";
        const isActive = this.historyMapActivity[value.id] === "active";

          // цена объекта из бронивика
          let idBronevik = this.objectsBronevik.find(item => item == value.id);
          let priceBronevik = 0;
          let textFromPrice = "";
          if (idBronevik) {
              priceBronevik =  (this.pricesBronevik && this.pricesBronevik[idBronevik] && this.pricesBronevik[idBronevik]['price']) ? this.pricesBronevik[idBronevik]['price'] : 0;
              if (priceBronevik) {
                  textFromPrice = this.$t("from");
              }
          }
          let countDay = 1;
          if (this.checkIn && this.checkOut) {
              let checkIn = new Date(this.checkIn).getTime();
              let checkOut = new Date(this.checkOut).getTime();
              countDay = (checkOut - checkIn) / 86400000;
          }
          priceBronevik = priceBronevik / countDay;
          let priceObject = priceBronevik ? priceBronevik : value.price.per_night;

        acc[value.id] = {
          coords: [lat, lng],
          typeMark: "html",
          options: {
            // Если ховер то выше
            zIndex:
              this.hoverPlacemark == value.id ? 668 : isActive ? 667 : 666,
            iconLayout: `<div id="${
              value.id
            }" class="map-placemark ${classHover} ${classHistory}">
              ${
                value.booking_now
                  ? `<span class="icon-app-lightning"></span>`
                  : ""
              }
              ${textFromPrice}${new Intl.NumberFormat(this.$i18n.locale, {
                style: "currency",
                currency: "RUB",
                minimumFractionDigits: 0,
              }).format(priceObject)}
              </div>`,
            iconShape: {
              type: "Rectangle",
              // Прямоугольник описывается в виде двух точек - верхней левой и нижней правой.
              coordinates: [
                [-35, -25],
                [35, 0],
              ],
            },
          },
        };
        return acc;
      }, {});
    },
  },
  unmounted() {
    // window.removeEventListener("resize", this.handleWindowResize);
    this.unwatch.forEach((unwatch) => unwatch());
  },
  created() {
    this.mapCenter = [
      (this.bound.SW.lat + this.bound.NE.lat) / 2,
      (this.bound.SW.lng + this.bound.NE.lng) / 2,
    ];
  },
  mounted() {
    // window.addEventListener("resize", this.handleWindowResize);
    if (this?.getQuery?.hash === "map") {
      this.viewMap();
    }
    window.scrollTo(0, 0);
    this.historyMapActivity = Object.keys(
      JSON.parse(localStorage.getItem("historyMapActivity") || "{}")
    ).reduce((acc, key) => ({ ...acc, [key]: "visited" }), {});
    this.$emit("mapCenter", this.mapCenter);
    this.$emit("mapZoom", this.mapZoom);
  },
  methods: {
    ...mapMutations("search", ["updateSwitchMap", "setBound", "setSelectedMapPointId"]),
    // TODO: HACK LOCK UPDATE BEFORE CLICK CARD
    boundschange () {
      if (this.isLoadMap) {
        this.isMoved = true;
      } else {
        this.isLoadMap = true;
      }
    },
    addHashMap() {
      this.$router.push({
        hash: "#map",
        query: this.getQuery.query,
      });
    },
    removeHashMap() {
      if (this.$router.currentRoute.value.hash) {
        this.$router.push({
          hash: "",
          query: this.getQuery.query,
        });
      }
    },
    closeMap() {
      this.removeHashMap();
    },
    viewMap(noHash) {
      setTimeout(() => {
        if (!noHash) {
          this.addHashMap();
        }
        this.$nextTick(() => {
          this.updateSwitchMap(true);
        });
      }, 100);
    },
    onActionEnd() {
      const bound = this.myMap.getBounds();
      // Узнаем центры
      const from = ymaps.util.bounds.getCenter([
        [this.bound.SW.lat, this.bound.SW.lng],
        [this.bound.NE.lat, this.bound.NE.lng],
      ]);
      const to = ymaps.util.bounds.getCenter(bound);
      // deltaDistance
      const deltaDistance = ymaps.coordSystem.geo.getDistance(from, to);

      const distanceZoomFrom = ymaps.coordSystem.geo.getDistance(
        [this.bound.SW.lat, this.bound.SW.lng],
        [this.bound.NE.lat, this.bound.NE.lng]
      );
      const distanceZoomTo = ymaps.coordSystem.geo.getDistance(
        bound[0],
        bound[1]
      );

      // Удаление слоя карты
      // if (this.myMap.getZoom() === 19) {
      //   this.myMap &&
      //     this.markLayer &&
      //     this.myMap.layers.remove(this.markLayer);
      // } else {
      //   this.myMap && this.markLayer && this.myMap.layers.add(this.markLayer);
      // }

      // Если сдвинули на < 10% экрана и зум не менялся блокируем обновления
      if (
        deltaDistance < distanceZoomFrom * 0.1 &&
        Math.abs(distanceZoomFrom - distanceZoomTo) < 200
      ) {
        // console.log("LOCK - NO DIFF");
        return;
      } else if (this.lockUpdate) {
        // Блокирвока при открытие карточки на карте
        // console.log("LOCK - openCard");
        return;
      } else if (!this.isNoBlockUpdate) {
        // Пользователь решил не обновлять автоматически
        // console.log("LOCK - USER BLOCK");
        this.isMoved = true;
        //Если нет результатов обновляем поиск
        if (this.totalFound == 0 && this.switchMap) {
          this.onForceUpdateArea();
        }
        return;
      } else {
        // console.log("UPDATE");
        this.onActionEndDebounced();
      }
    },
    onActionEndDebounced: _.debounce(function () {
      const bound = this.myMap.getBounds();
      this.$store.commit("search/setBound", {
        NE: {
          lat: bound[1][0],
          lng: bound[1][1],
        },
        SW: {
          lat: bound[0][0],
          lng: bound[0][1],
        },
      });
    }, 100),

    onInitMap(map) {
      this.myMap = map;

      // Избранное
      // при загрузке страницы если стоит только флаг избранного,
      // то ищем регион где больше объектов и строим bbox при инициализации карты
      if (this.getQuery?.query?.is_favorites && this.isDefaultLocationRender) {
        Objects.getFavoritesList().then((response) => {
          const objects = response.data?.data?.objects;
          // собераем все id регионов и количество объектов в регионе
          let regionIdsCount = {};
          objects.map((o) => {
            regionIdsCount[o.region_id]
              ? regionIdsCount[o.region_id]++
              : (regionIdsCount[o.region_id] = 1);
          });

          // ищем регион где всех больше объектов
          const regionId = Object.keys(regionIdsCount).reduce((a, b) =>
            regionIdsCount[a] > regionIdsCount[b] ? a : b
          );
          // собираем точки избранных объектов в регионе
          const favoritesPoints = objects.reduce((acc, obj) => {
            if (obj.region_id == regionId) acc.push([obj.lat, obj.lng]);
            return acc;
          }, []);

          //  Получение карты по точкам
          const bound = ymaps.util.bounds.fromPoints(favoritesPoints);

          const { center, zoom } = ymaps.util.bounds.getCenterAndZoom(
            [
              [bound[0][0], bound[0][1]],
              [bound[1][0], bound[1][1]],
            ],
            this.myMap.container.getSize()
          );
          // правила для зума
          // min = 5, max = 15
          // если в диапазоне между 5 и 15 на 1 уменьшаем
          const newZoom = zoom > 15 ? 15 : zoom < 5 ? 5 : zoom - 1;
          this.myMap.setCenter(center, newZoom);
          // чтобы корректно отправлялись запросы
          const mapBound = this.myMap.getBounds();
          this.setBound({
            NE: {
              lat: mapBound[1][0],
              lng: mapBound[1][1],
            },
            SW: {
              lat: mapBound[0][0],
              lng: mapBound[0][1],
            },
          });
          this.$nextTick(() => {
            this.isMoved = false;
          });
        });
      } else {
        const { center, zoom } = ymaps.util.bounds.getCenterAndZoom(
          [
            [this.bound.SW.lat, this.bound.SW.lng],
            [this.bound.NE.lat, this.bound.NE.lng],
          ],
          this.myMap.container.getSize(),
          ymaps.projection.wgs84Mercator,
          {
            inscribe: false,
          }
        );
        this.mapZoom = zoom;
        this.mapCenter = center;
        this.$nextTick(() => {
          // После изменения BBOX пойдет пойск
          const bound = this.myMap.getBounds();
          this.setBound({
            NE: {
              lat: bound[1][0],
              lng: bound[1][1],
            },
            SW: {
              lat: bound[0][0],
              lng: bound[0][1],
            },
          });
          this.$nextTick(() => {
            this.isMoved = false;
          });
        });
      }
    },
    onActionBegin() {
      if (this.selectedMapPointId && !this.lockUpdate) {
        this.setSelectedMapPointId(null);
      }
    },
    onForceUpdateArea() {
      this.onActionEndDebounced();
    },
    onClick(mark) {
      this.setSelectedMapPointId(mark.id);
    },
    hideBlockUpdate() {
      setTimeout(() => {
        this.isMoved = false;
      }, 2000);
    },
    // обработчик для customEventsMap
    updateCustomEvents(val) {
      if (!val?.name) return;
      switch (val.name) {
        case "actionbegin":
          this.onActionBegin(val.event);
          break;
        case "actionend":
          this.onActionEnd(val.event);
          break;
        default:
          break;
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.3s ease;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.map-mobile {
  position: relative;
  height: 100%;

  &--viewer {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    // background: #000;
    z-index: 1;
  }
  &--force {
    cursor: pointer;
    user-select: none;
    position: absolute;
    top: 30px;
    border-radius: 21px;
    background: #444;
    color: #fff;
    left: 50%;
    transform: translateX(-50%);
    z-index: 2;
    padding: 0 15px;
    font-size: 14px;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 34px;
    line-height: 34px;
    &.loading {
      &:before {
        border-radius: 16px;
      }
    }
  }

  ::v-deep .map-placemark {
    position: relative;
    display: inline-block;
    white-space: nowrap;
    background-color: #ffffff;
    color: #000;
    height: 24px;
    box-sizing: border-box;
    padding: 0 8px;
    font-size: 12px;
    line-height: 24px;
    font-weight: bold;
    border-radius: 8px;
    transform: translate(-50%, -100%);
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.3);
    top: -5px;
    &:after {
      position: absolute;
      bottom: 5px;
      left: 50%;
      transform: translate(-50%, 100%) rotate(45deg);
      content: "";
      background-color: #ffffff;
      width: 8px;
      height: 8px;
      // box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
      box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.25);
    }
    .icon-app-lightning {
      display: inline-block;
      font-size: 11px;
      position: relative;
      top: 1px;
      width: 9px;
      display: none;
    }
    &.active {
      background-color: #444444;
      color: white;
      &:after {
        background-color: #444444;
      }
      z-index: 999;
    }
    &.visited {
      background-color: #ffffff;
      color: #717171;
      &:after {
        background-color: #ffffff;
      }
    }
    &.hover:not(.active) {
      transform-origin: 50% 100%;
      transform: translate(-50%, -100%) scale(1.2, 1.2);
      z-index: 998;
    }
  }
}
</style>
