export default function (Component) {
  Component.__i18n = Component.__i18n || []
  Component.__i18n.push({
    "locale": "",
    "resource": {
      "ru": {
        "price_any": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["любая цена"])},
        "price_from": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["oт"])},
        "price_to": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["до"])}
      },
      "en": {
        "price_any": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["any price"])},
        "price_from": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["from"])},
        "price_to": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["to"])}
      }
    }
  })
}
