<template>
    <a
      ref="link"
      class="card-content"
      :target="targetOpt"
      @mousedown="mousedownComponent"
    >
      <template v-if="data.hotel">
      <div class="card-content__object-subtext tmp-font--medium">
        <span v-if="data.hotel.type" class="object-hotel__type">{{
          data.hotel.type
        }}</span>
        <span v-if="data.hotel.title">«{{ data.hotel.title }}»</span>
      </div>

      <h2 class="card-content__object-type">{{ data.title }}</h2>
      </template>

      <template v-else>
        <div
          v-if="data.name"
          class="card-content__object-subtext tmp-font--medium"
        >
          <span>{{ data.title }}</span>
        </div>

        <h2 class="card-content__object-type">
          {{ data.name ? data.name : data.title }}
        </h2>
      </template>

      <template v-if="data.occupied === 'busy'">
        <div class="card-content__property">
          <div class="info-block">
            <div class="info-block-ico">
              <div class="icon-app-clock"></div>
            </div>
            <div>
              {{ $t("booked_for_your_dates") }}
              <br />
              {{ $t("housing_is_in_demand") }}
            </div>
          </div>
        </div>
      </template>
      <template v-else>
        <div class="card-content__facilities">
          <div class="facilities__main">
            <p v-if="data.guests" class="facilities__facility tmp-font--medium">
              {{ $tc("guests", data.guests) }}
            </p>
            <p
              v-if="data.count_beds"
              class="facilities__facility tmp-font--medium"
            >
              {{ $tc("beds", data.count_beds) }}
            </p>
            <p v-if="data.bedrooms" class="facilities__facility tmp-font--medium">
              {{ $tc("bedrooms", data.bedrooms) }}
            </p>
          </div>
          <div v-if="data.area" class="facilities__size">
            <span> {{ data.area }} {{ $t("meter") }} </span>
          </div>
        </div>
      </template>

      <div
        v-if="!hideOwnerPopularity && popularity && (popularity.value || meals)"
        class="card-content__popularity"
      >
        <div class="popularity__item">
          <div
            v-if="meals && (meals.included || meals.unincluded)"
            class="item-wrapp popularity__meals"
          >
            <div v-if="meals.included" class="meals__item meals__included">
              {{ meals.included }}
            </div>
            <div v-if="meals.unincluded" class="meals__item">
              <span class="popularity__circle blue breakfast"></span>
              <span class="breakfast">
                {{ meals.unincluded }}
              </span>
            </div>
          </div>
          <div v-else-if="profitable" class="item-wrapp">
            <span class="popularity__circle green profitable"></span>
            <span class="profitable">{{ $t("profitable") }}</span>
          </div>
          <div v-else-if="popularity.name === 'last_room'" class="item-wrapp">
            <span class="popularity__circle blue"></span>
            <span class="last-room blue">{{ $t("lastRoom") }}</span>
          </div>
          <div v-else-if="popularity.name === 'last_booking'" class="item-wrapp">
            <span class="popularity__circle blue"></span>
            <span class="last-booking">{{ popularity.lastBooking }}</span>
          </div>
          <div v-else-if="popularity.name === 'high_demand'" class="item-wrapp">
            <span class="popularity__circle green"></span>
            <span class="high-demand">{{ $t("highDemand") }}</span>
          </div>
          <div v-else-if="popularity.name === 'demand'" class="item-wrapp">
            <span class="popularity__circle green"></span>
            <span class="demand">{{ $t("demand") }}</span>
          </div>
          <div
            v-else-if="
              popularity.name === 'watching_now' && popularity.value !== 0
            "
            class="item-wrapp"
          >
            <span class="popularity__circle blue"></span>
            <span class="watching">{{
              $tc("watchingNow", Number(popularity.value))
            }}</span>
          </div>
        </div>
      </div>

      <div v-if="data.occupied !== 'busy'" class="card-content__object-info">
        <!-- временно убрал плашки занятости объектов пока не починят запрос цен
            внизу у template нужно будет вернуть v-else -->
        <!-- <BaseWarningBronevik v-if="isDisabledLink" @showCalendarBronevik="showCalendarBronevik" /> -->
        <div
          v-if="
            data.location &&
            ((data.location.ropeway && data.location.ropeway.distance != null) ||
              (data.location.sea && data.location.sea.distance != null) ||
              (data.metro && data.metro[0]) ||
              data.location.place)
          "
          class="text-fog"
          style="overflow: hidden; position: relative"
        >
          <!-- подъемники -->
          <div
            v-if="data.location.ropeway && data.location.ropeway.distance != null"
          >
            <a
              class="card-content__property-list-elevator"
            >
              <span class="img-elevator"></span>
              <span
                v-tooltip="{
                  content: $t('promptElevator'),
                  popperClass: 'v-popper__popper--max-width-300',
                }"
              >
                {{
                  $t("distance_elevator", {
                    distance: disatanceDozen(data.location.ropeway.distance),
                    objectName: data.location.ropeway.title,
                  })
                }}
              </span>
            </a>
          </div>
          <!-- подъемники -->

          <!-- море -->
          <div
            v-else-if="data.location.sea && data.location.sea.distance != null"
            v-tooltip="{
              content: $t('hint_sea'),
              popperClass: 'v-popper__popper--max-width-300',
            }"
            class="object-location"
          >
            <a
              :target="targetOpt"
              @mousedown="clickObjectBronevik($event, linkBronevik)"
            >
              <span class="icon-app-sea"></span>
              {{
                $t("distance_to_sea", {
                  n: disatanceDozen(data.location.sea.distance),
                })
              }}
            </a>
          </div>
          <!-- море -->

          <!-- метро -->
          <div
            v-else-if="
              data.location && data.location.metro && data.location.metro.title
            "
            v-tooltip="{
              content: hintMetro,
              popperClass: 'v-popper__popper--max-width-300',
            }"
            class="object-location"
          >
            <a
              :target="targetOpt"
              @mousedown="clickObjectBronevik($event, linkBronevik)"
            >
              <span
                class="icon-app-metro-circle"
                :style="{
                  color: data.location.metro.line_color || '#ADACAC',
                }"
              />
              <span
                v-if="data.location.metro.title && data.location.metro.distance"
                >{{
                  $t("metro_description", {
                    metroName: data.location.metro.title,
                    distance: meterToMinWalking(data.location.metro.distance),
                  })
                }}</span
              >
            </a>
          </div>
          <!-- метро -->

          <!-- район -->
          <span
            v-else-if="data.location.place"
            v-tooltip="{
              content: hintDistrict,
              popperClass: 'v-popper__popper--max-width-300',
            }"
            class="object-location"
          >
            <a
              :target="targetOpt"
              @mousedown="clickObjectBronevik($event, linkBronevik)"
            >
              {{ data.location.place.title }} {{ $t("place") }}
            </a>
          </span>
          <!-- район -->
        </div>

        <!-- адресс -->
        <div v-if="data.address" class="card-content__address">
          <a
            class="address__link"
            :target="targetOpt"
            @mousedown="clickObjectBronevik($event, linkBronevik)"
          >
            <p
              v-tooltip="{
                content: $t('showMapAddress', { address: data.address }),
                popperClass: 'v-popper__popper--max-width-300',
                html: true,
              }"
              class="address__text"
            >
              {{ data.address }}
            </p>
          </a>
        </div>
        <!-- адресс -->
      </div>
    </a>
</template>

<script>
import { mapState, mapGetters, mapMutations } from "vuex";
import BaseWarningBronevik from "@/components/base/BaseWarningBronevik";
import _ from "lodash";

export default {
  name: "ObjectBlockContent",
  components: {
    BaseWarningBronevik,
  },
  props: {
    data: {
      type: Object,
    },
    showDetail: {
      required: true,
    },
    user: {
      type: Object,
    },
    hideOwnerPopularity: {
      type: Boolean,
      default: false,
    },
    flag: {
      default: null,
    },
    rating: {
      type: Object,
    },
    linkBronevik: {
      type: String,
      default: "",
    },
    isDisabledLink: {
      type: Boolean,
      default: false,
    },
    isBronevik: {
      type: Boolean,
      default: false,
    },
  },
  emits: ["mousedown"],
  watch: {
    // popularity(val) {
    //   if(val)
    //     this.sendGa()
    // }
  },
  computed: {
    ...mapState("search", ["guests"]),
    ...mapGetters("search", ["occupied", "getQuery", "maxGuests"]),
    targetOpt() {
      return this.isVkApp === false ? "_blank" : "_self";
    },
    profitable() {
      return this.rating?.ratings?.price_quality > 4;
    },
    meals() {
      // питание
      const ucFirst = (str) => {
        return str[0].toUpperCase() + str.slice(1);
      };

      if (!this.data?.meal?.meal_types) return null;
      if (this.data.meal?.is_all_inclusive) {
        return {
          included: ucFirst(this.$t("allInclusive")),
          unincluded: null,
        };
      }

      let included = [];
      let unincluded = [];
      this.data.meal.meal_types.forEach((v) => {
        if (v.kind === "nofood" || v.kind === "") return false;
        if (v.price === 0) {
          included.push(this.$t(`meals.${v.name}`));
        } else if (v.price === 1) {
          unincluded.push(this.$t(`meals.${v.name}`));
        }
      });

      const obj = {
        included: included.length
          ? ucFirst(this.$tc("meals.included", included.length - 1, included))
          : null,
        unincluded: unincluded.length
          ? ucFirst(
              this.$tc("meals.unincluded", unincluded.length - 1, unincluded)
            )
          : null,
      };
      return obj;
    },
    popularity() {
      // switch(this.data.popularity)
      const popular = this.data?.popularity;
      if (popular?.name === "last_booking") {
        const dateUser = new Date();
        const date = new Date(popular.value);
        const dateParse = {
          year: date.getFullYear(),
          month: date.getMonth(),
          day: date.getDate(),
          hours: date.getHours(),
          minutes: date.getMinutes(),
          seconds: date.getSeconds(),
        };
        const dateUtc = new Date(
          new Date(
            Date.UTC(
              dateParse.year,
              dateParse.month,
              dateParse.day,
              dateParse.hours,
              dateParse.minutes,
              dateParse.seconds
            )
          ).getTime() -
            3 * 3600000
        );
        const viewDate = Math.floor(
          (dateUser.getTime() - dateUtc.getTime()) / 3600000
        );
        return {
          ...popular,
          lastBooking: `${this.$t("lastBooking")} ${this.$tc(
            "hour",
            viewDate
          )}`,
        };
      } else {
        return popular;
      }
    },
    conveniencesList() {
      if (!this.data.conveniences) return [];
      return Object.entries(this.data.conveniences)
        .filter(([key, value]) => value && key)
        .map(([key]) => key);
    },
    conveniencesPrimary() {
      let primary = null;
      if (this.data.is_children && this.guests?.childrens?.length > 0)
        primary = "is_children";
      else if (
        this.data.for_couple &&
        this.maxGuests === 2 &&
        this.guests?.childrens?.length === 0
      ) {
        primary = "for_couple";
      } else if (this.data.party) primary = "party";
      else if (this.conveniencesList.find((item) => item === "pets"))
        primary = "pets";

      return primary ? primary : null;
    },
    place() {
      if (this.data.sea_distance && this.data.sea_distance.distance != null) {
        const dist =
          this.data.sea_distance.distance < 1000
            ? `${this.data.sea_distance.distance} м`
            : `${(this.data.sea_distance.distance / 1000).toFixed(1)} км`;
        return `${dist} ${this.data.sea_distance.sea}`;
      }
      return this.data.location;
    },
    textPlace() {
      if (this.data.sea_distance) {
        return this.$t("hint_sea");
      }
      return "none";
    },
    hintMetro() {
      return this.$t("hint_metro");
    },
    hintDistrict() {
      return this.$t("hint_district");
    },
    hintRoomInfo() {
      const bedrooms = this.data.bedrooms
        ? `${this.$tc("bedroomsBeutifull", this.data.bedrooms)}, `
        : "";
      const beds = this.data.count_beds
        ? `${this.$tc("beds", this.data.count_beds)}, `
        : "";
      const person = this.data.guests
        ? `${this.$t("posible_living")} ${this.$tc("person", this.data.guests)}`
        : "";
      return (
        `${this.$t("in_room")} ` +
        bedrooms +
        beds +
        person +
        `, ${this.$t("includeChild")}`
      );
    },
    tooltipClasses() {
      return "max";
    },
  },
  mounted() {
    // this.sendGa()
    // this.sendGaProperty()
  },
  methods: {
    ...mapMutations("search", ["objectFlagMutate"]),
    // sendAddProduct() {
    //   Search.gaAddProduct(
    //     this.data.hotel
    //       ? this.data.title
    //       : this.data.name
    //         ? this.data.name
    //         : this.data.title,
    //     this.data.id,
    //     this.data.price.full,
    //     this.data.location.city.title,
    //     this.data.pr,
    //   )
    // },
    showCalendarBronevik() {
      if (!this.occupied) {
        setTimeout(() => {
          document.querySelector("#datepickerSearch").click();
        }, 150);
      }
    },
    onClickPopoverGA(e) {
      window.open(e.target.href, "_blank");
    },
    sendGa() {
      let flag = null;
      if (this.popularity?.name === "last_room") flag = "l";
      else if (this.popularity?.name === "last_booking") flag = "b";
      else if (this.popularity?.name === "high_demand") flag = "a";
      else if (this.popularity?.name === "demand") flag = "z";
      else if (
        this.popularity?.name === "watching_now" &&
        this.popularity?.value !== 0
      )
        flag = "s";

      if (flag) {
        this.objectFlagMutate({
          id: Number(this.data.id),
          index: 1,
          symbol: flag,
        });
      }
    },
    sendGaProperty() {
      let flag = null;

      if (this.conveniencesPrimary === "is_children") flag = "d";
      else if (this.conveniencesPrimary === "for_couple") flag = "2";
      else if (this.conveniencesPrimary === "party") flag = "p";
      else if (this.conveniencesPrimary === "pets") flag = "a";

      if (flag) {
        this.objectFlagMutate({
          id: Number(this.data.id),
          index: 2,
          symbol: flag,
        });
      }
    },
    linkTo() {
      this.$parent.showDetail = !this.showDetail;
      document.body.style.overflow = "hidden";
    },
    getFormatOccupied(date) {
      let format = (date) => {
        let month = (date.getMonth() + 1).toString().padStart(2, 0),
          day = date.getDate().toString().padStart(2, 0),
          year = date.getFullYear();
        return year + "-" + month + "-" + day;
      };
      return date.in && date.out
        ? format(date.in) + ";" + format(date.out)
        : null;
    },
    meterToMinWalking(distance) {
      const SPEED_WALKING = 0.012; // величина времени пройденый за 1 метр
      const time = Math.round(distance * SPEED_WALKING);
      return time === 0 ? 1 : time;
    },
    disatanceDozen(distance) {
      if (distance < 50) {
        return this.$t("distance_meters", {
          distance: "< 50",
        });
      } else if (distance < 991) {
        return this.$t("distance_meters", {
          distance: (Math.ceil(distance / 10) * 10).toLocaleString(),
        });
      } else {
        return this.$t("distance_kilometers", {
          distance: (
            (Math.round(distance / 100) * 100) /
            1000
          ).toLocaleString(),
        });
      }
    },
    clickObjectBronevik(event, link) {
      if (
        (this.isDisabledLink && event.which == 1) ||
        (this.isDisabledLink && event.which == 2)
      ) {
        event.preventDefault();
      }
      if (link || (link && event.which == 1) || (link && event.which == 2)) {
        event.preventDefault();
        window.open(link);
      }
    },
    mousedownComponent($event) {
      this.$emit("mousedown", $event);
      this.clickObjectBronevik($event, this.linkBronevik);
    },
  },
};
</script>

